export const shouldDisplayElement = (hasPermission, isAddonProduct, imageType, workObject) => {
 
    const isManager = hasPermission("image_cx:write");
    const isArtFileDesigner = hasPermission("image_artfile:write");
    const isPrintFileDesigner = hasPermission("image_printfile:write");
  
    if (isAddonProduct) {
        if(imageType === 'printfile' && (isPrintFileDesigner || isManager) && workObject.workObject.status === 4) {
          return true;
        } else {
          return false;
        }
    }
    
    const workObjectStatus = workObject.workObject.status;

    if((workObjectStatus > 1 && workObjectStatus < 6 ) || workObjectStatus === 9003) {
      if(imageType === 'cx' && isManager) {
        return true;
      } else if (workObjectStatus === 3 && imageType === 'artfile' && (isArtFileDesigner || isManager)) {
        return true;
      } else if (workObjectStatus === 4 && imageType === 'printfile' && (isPrintFileDesigner || isManager)) {
        return true;
      } else {
        return false;
      }
    }
}