import { useContext } from "react";
import { Link } from "react-router-dom";
import AuthContext from "../../services/auth.context";
import ConfigContext from "../../services/config.context";
import { Badge, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import { faShopify } from "@fortawesome/free-brands-svg-icons";
import StatusBadgeComponent from "../shared/status.component";
export default function DashboardTableRowComponent({ item }) {
    const { hasPermission } = useContext(AuthContext);
    const { config } = useContext(ConfigContext);
    return (
        <tr key={`${item.orderId}-${item.shopifyOrderId}-${item.lastModified}`}>
            <td>
                <Badge
                    pill
                    bg="secondary"
                    style={{ cursor: 'pointer' }}
                >
                    {item.orderName}
                </Badge>
            </td>
            <td><StatusBadgeComponent status={item.status} /></td>
            <td>{item.shopifyOrderId}</td>
            <td>{item.metadata?.customer?.FirstName}</td>
            <td>{item.metadata?.customer?.LastName}</td>
            <td>{item.metadata?.customer?.Email}</td>
            <td>{item.metadata?.lineItems?.map((lineItem) => (
                    <Badge key={lineItem.id} pill bg="light border text-dark" className="me-1" size="sm">
                        {lineItem?.LineItem?.Name}
                    </Badge>
                ))
                }</td>
            <td className="d-flex align-items-center" style={{
                height: '4.1em'
            }}>
                <Link to={`/details/${item.shopifyOrderId}`}>
                    <Button variant="" className="me-1 btn-brand" size="sm">
                        <FontAwesomeIcon icon={faEdit} />
                    </Button>
                </Link>
                {hasPermission('user:write') ? (
                    <Button href={`${config.shopifyUrl}/admin/orders/${item.shopifyOrderId}`} rel="noopener noreferrer" target="_blank" variant="success" className="me-1 bg-shopify" size="sm">
                        <FontAwesomeIcon icon={faShopify} />
                    </Button>
                ) : null}
            </td>
        </tr>
    )
}