import React, { useState, useContext } from 'react';
import { Modal, Button, Badge, Alert } from 'react-bootstrap';
import Select from 'react-select';
import LoadingComponent from '../shared/loading.component';
import CacheContext from '../../services/cache.context';

export default function OrderBulkDownloadComponent({ selectedWorkObjectIds, showBulkDownloadModal, setShowBulkDownloadModal, handleDeselectAll }) {
    const { downloadService } = useContext(CacheContext);
    const [ selectedImageTypes, setSelectedImageTypes ] = useState([]);
    const [ isCreatingDownloadJob, setIsCreatingDownloadJob ] = useState(false);
    const [ alertMessage, setAlertMessage ] = useState(null);
    const [ alertVariant, setAlertVariant ] = useState('success');

    const imageTypeOptions = [
        { value: 'cx', label: 'CX' },
        { value: 'artfile', label: 'Artfile' },
        { value: 'printfile', label: 'Printfile' },
    ];

    const handleImageTypeChange = selectedOptions => {
        setSelectedImageTypes(selectedOptions);
    };

    const handleCreateDownloadJob = async () => {
        if (selectedWorkObjectIds.length > 0 && selectedImageTypes.length > 0) {
            setIsCreatingDownloadJob(true);
            try {
                await downloadService.bulkDownload(
                    selectedImageTypes.map(type => type.value),
                    selectedWorkObjectIds
                );
                setAlertMessage('Download job created successfully. You will receive an email with the download link in a few minutes.');
                setAlertVariant('success');
                handleDeselectAll();
            } catch (error) {
                setAlertMessage(`Error creating download job: ${error.message}`);
                setAlertVariant('danger');
            } finally {
                setIsCreatingDownloadJob(false);
            }
        }
    };

    const handleCloseModal = () => {
        setShowBulkDownloadModal(false);
        setIsCreatingDownloadJob(false);
        setAlertMessage(null);
    };

    return (
        <Modal show={showBulkDownloadModal} onHide={handleCloseModal} size="lg">
            <Modal.Header closeButton>
                <Modal.Title className='h4'>Bulk Download</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {isCreatingDownloadJob && <LoadingComponent />}
                {alertMessage && (
                    <Alert variant={alertVariant}>
                        {alertMessage}
                    </Alert>
                )}
                <div className="mb-3">
                    <h5>
                        Selected Orders:
                        <Badge className="bg-dark ms-2 pt-1 pb-1">
                            {selectedWorkObjectIds.length} Orders Selected
                        </Badge>
                    </h5>
                </div>
                <div className="mb-3">
                    <Select
                        isMulti
                        options={imageTypeOptions}
                        value={selectedImageTypes}
                        onChange={handleImageTypeChange}
                        placeholder="Select Image Types"
                    />
                </div>
                <div className='text-end'>
                    <Button
                    variant="dark"
                        className="btn btn-dark"
                        onClick={handleCreateDownloadJob}
                        disabled={isCreatingDownloadJob || selectedWorkObjectIds.length === 0 || selectedImageTypes.length === 0}
                    >
                        {isCreatingDownloadJob ? 'Processing Download...' : 'Download'}
                    </Button>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleCloseModal}>
                    Close
                </Button>
            </Modal.Footer>
        </Modal>
    );
}
