import { useContext, useEffect, useState } from 'react';
import { Popover, Overlay, ListGroup, Badge } from 'react-bootstrap';
import ConfigContext from '../../services/config.context';
import CacheContext from '../../services/cache.context';
import LoadingComponent from '../shared/loading.component';
import StatusBadgeComponent from '../shared/status.component';

const renderLineItems = (order) => {
    const lineItems = order?.workObject?.metadata?.lineItems ?? [];
    if (lineItems.length === 0) {
        return (
            <ListGroup.Item>
                <div className="text-center text-muted">Line items not found</div>
            </ListGroup.Item>
        );
    }
    return lineItems.map((item, idx) => (
        <ListGroup.Item key={idx}>
            <strong>{item.LineItem.Name.split('-')[0]}</strong> <br />
            <p className='badge bg-brand mb-1 rounded-pill'>{item.LineItem.Name.split('-')[1]}</p> <br />
        </ListGroup.Item>
    ));
};

export default function OrderPopoverComponent({ show, target, onClose, orderId }) {
    const { config } = useContext(ConfigContext);
    const { orderService } = useContext(CacheContext);
    const [ order, setOrder ] = useState(null);
    const [ isLoading, setIsLoading ] = useState(true);

    useEffect(() => {
        setIsLoading(true);
        orderService.getDetails(orderId)
            .then((res) => {
                setOrder(res.data);
            }, (err) => {
                console.log(err);
            }).finally(() => {
                setIsLoading(false);
            });

    }, [orderId, orderService, config]);

    return (
        <Overlay
            show={show}
            target={target}
            placement="left"
            rootClose={true}
            onHide={onClose}
        >
            <Popover id="popover-contained" className='shadow'>
                <Popover.Header as="h3">
                    {order && !isLoading && (
                        <>
                            {order.workObject && <StatusBadgeComponent status={order.workObject.status} />}
                            <Badge className='bg-dark float-end'>{order.workObject.metadata.lineItems?.length ?? '0'}</Badge>
                        </>
                    )}
                </Popover.Header>

                <Popover.Body>
                    {isLoading ? (
                        <LoadingComponent message="Loading order details..." size="sm" variant='dark'/>
                    ) : order ? (
                        <ListGroup className='shadow-sm'>
                            {renderLineItems(order)}
                        </ListGroup>
                    ) : (
                        <div className="alert alert-danger" role="alert">
                            Order not found
                        </div>
                    )}
                </Popover.Body>
            </Popover>
        </Overlay>
    );
}
