import { useState, useContext, useEffect } from 'react';
import { Row, Col, Form, Card, Button, Alert } from "react-bootstrap";
import AuthContext from '../../services/auth.context';

export default function OrderCustomerInformation({ workObject, customer, shippingAddress, orderService, fetchWorkObject }) {
  const [ editedShippingAddress, setEditedShippingAddress ] = useState({ ...shippingAddress });
  const [ success, setSuccess ] = useState(false);
  const { hasPermission } = useContext(AuthContext);

  const allowEditing = hasPermission("order:write") && hasPermission("line_item:write");

  useEffect(() => {
    if (success) {
      const timer = setTimeout(() => {
        fetchWorkObject();
        setSuccess(false);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [success, fetchWorkObject]);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setEditedShippingAddress(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSave = async (e) => {
    e.preventDefault();
    orderService.updateShippingAddress(workObject.workObject.shopifyOrderId, editedShippingAddress).then((res) => {
      setSuccess(true);
    }).catch((err) => {
      console.log(err);
    }).finally(() => {

    });

  };

  return (
    <>
      <Col md={6} sm={12}>
        <div className="text-start mb-2">
          <h4 className="mb-0 ms-1">Customer</h4>
        </div>
        <Card>
          <Card.Body>
            {success && <Alert variant="success">Shipping address updated successfully!</Alert>}
            <Form onSubmit={handleSave}>
              <Row>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>First Name</Form.Label>
                  <Form.Control
                    name="FirstName"
                    value={editedShippingAddress?.FirstName || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Last Name</Form.Label>
                  <Form.Control
                    name="LastName"
                    value={editedShippingAddress?.LastName || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-12 mb-2">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    name="Email"
                    defaultValue={customer?.Email || ""}
                    disabled={!allowEditing}
                  />
                </Form.Group>
              </Row>
              <Row>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Address Line 1</Form.Label>
                  <Form.Control
                    name="Address1"
                    value={editedShippingAddress?.Address1 || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Address Line 2</Form.Label>
                  <Form.Control
                    name="address2"
                    value={editedShippingAddress?.Address2 || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
              </Row>
              <Row>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>City</Form.Label>
                  <Form.Control
                    name="City"
                    value={editedShippingAddress?.City || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Province</Form.Label>
                  <Form.Control
                    name="Province"
                    value={editedShippingAddress?.Province || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Province Code</Form.Label>
                  <Form.Control
                    name="ProvinceCode"
                    value={editedShippingAddress?.ProvinceCode || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Country</Form.Label>
                  <Form.Control
                    name="Country"
                    value={editedShippingAddress?.Country || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Country Code</Form.Label>
                  <Form.Control
                    name="CountryCode"
                    value={editedShippingAddress?.CountryCode || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
                <Form.Group className="col-md-6 mb-2">
                  <Form.Label>Zip</Form.Label>
                  <Form.Control
                    name="Zip"
                    value={editedShippingAddress?.Zip || ""}
                    onChange={handleInputChange}
                    disabled={!allowEditing}
                  />
                </Form.Group>
              </Row>
              <div className="text-end">
                <Button className="btn btn-brand" type="submit" disabled={!allowEditing}>Save</Button>
              </div>
            </Form>
          </Card.Body>
        </Card>
      </Col>
    </>
  );
}
