import { createContext, useContext, useState, useEffect, useRef } from "react";
import ConfigContext from "./config.context";
import LookupService from "./lookup.service";
import OrderService from "./order.service";
import AttachmentService from "./attatchment.service";
import CommentsService from "./order.comments.service";
import DistributionService from "./distribution.service";
import DownloadService from "./download.service";
import ToastContainer from "../components/shared/toast.container.component";
import DashboardService from "./dashboard.service";
const CacheContext = createContext();

export const CacheContextProvider = ({ children }) => {
    const { config, isConfigReady } = useContext(ConfigContext);
    const [ statuscache, setStatusCache ] = useState(null);
    const [ userscache, setUsersCache ] = useState(null);
    const [ productscache, setProductsCache ] = useState(null);
    const [ productshippingprofiles, setProductShippingProfiles ] = useState(null);
    const [ lookupService, setLookupService ] = useState(null);
    const [ distributionService, setDistributionService ] = useState(null);
    const [ orderService, setOrderService ] = useState(null);
    const [ downloadService, setDownloadService ] = useState(null);
    const [ dashboardService, setDashboardService ] = useState(null);
    const [ attachmentService, setAttachmentService ] = useState(null);
    const [ commentsService, setCommentsService ] = useState(null);
    const [ toasts, setToasts ] = useState([]);

    const toastCounter = useRef(0); //using ref to keep track of toast id counter state didn't work

    const showToastNotification = (message, variant = 'success') => {
        const newId = `toast-${toastCounter.current}`;
        setToasts((prevToasts) => [...prevToasts, { id: newId, message, variant }]);
        toastCounter.current += 1;
    };

    const hideToastNotification = (id) => {
        setToasts((prevToasts) => prevToasts.filter((toast) => toast.id !== id));
    };

    useEffect(() => {
        if (isConfigReady && config !== null) {
            setLookupService(new LookupService(config.baseUrl));
            setOrderService(new OrderService(config.baseUrl));
            setDashboardService(new DashboardService(config.baseUrl));
            setAttachmentService(new AttachmentService(config.baseUrl));
            setCommentsService(new CommentsService(config.baseUrl));
            setDistributionService(new DistributionService(config.baseUrl));
            setDownloadService(new DownloadService(config.baseUrl));
        }
    }, [isConfigReady, config])

    const getUserById = (userId) => {
        return getUserList()
            .then((res) => {
                const targetUser = res.find(user => user.id === userId);

                if (targetUser !== undefined) {
                    if (targetUser.metadata.firstName === undefined || targetUser.metadata.lastName === undefined) {
                        return targetUser.metadata.email;
                    } else {
                        return `${targetUser.metadata.firstName} ${targetUser.metadata.lastName}`;
                    }
                } else {
                    return userId;
                }
            }, (err) => {
                return Promise.resolve('Failed to locate user.');
            })
    }

    const getWorkObjectStatusLookup = () => {
        if (statuscache !== null) {
            return Promise.resolve(statuscache);
        } else {
            return lookupService.getWorkObjectStatusLookup()
                .then((res) => {
                    setStatusCache(res.data);
                    return res.data;
                })
        }
    }

    const getUserList = () => {
        if (userscache !== null) {
            return Promise.resolve(userscache);
        } else {
            return lookupService.getUsers()
                .then((res) => {
                    setUsersCache(res.data.results);
                    return res.data.results;
                })
        }
    }

    const getProductShippingProfiles = () => {
        if (productshippingprofiles !== null) {
            return Promise.resolve(productshippingprofiles);
        } else {
            return lookupService.getProductShippingProfiles()
                .then((res) => {
                    setProductShippingProfiles(res.data);
                    return res.data;
                });
        }
    }

    const getProductsCache = () => {
        if (productscache !== null) {
            return Promise.resolve(productscache);
        } else {
            return lookupService.getProducts()
                .then((res) => {
                    setProductsCache(res.data);
                    return res.data;
                });
        }
    }


    return (
        <CacheContext.Provider value={{
            getWorkObjectStatusLookup,
            getUserList,
            getUserById,
            getProductShippingProfiles,
            setProductShippingProfiles,
            productshippingprofiles,
            getProductsCache,
            setProductsCache,
            productscache,
            orderService,
            dashboardService,
            distributionService,
            downloadService,
            lookupService,
            attachmentService,
            commentsService,
            showToastNotification,
            hideToastNotification,
            toasts
        }}>
            {lookupService !== null && children}
            <ToastContainer />
        </CacheContext.Provider>
    );
};

export default CacheContext;
