import authHeader from "./auth.header";
import axios from "axios";
export default class AttachmentService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    uploadAttachment(file, workObjectId, imageType, lineItemId, cxPhotoIndex) {
        let data = new FormData();
        data.append('file', file);
    
        let url = `${this.baseUrl}/portal/api/v1/Attachment/upload/${workObjectId}/${imageType}/${lineItemId}`;
    
        // Add the cxPhotoIndex parameter if it's not null
        if (cxPhotoIndex !== null) {
            url += `?cxPhotoIndex=${cxPhotoIndex}`;
        }
    
        let config = {
            method: 'post',
            url: url,
            headers: {
                'Content-Type': 'multipart/form-data',
                ...authHeader().headers
            },
            data: data
        };
    
        return axios.request(config);
    }
    
    bulkUploadAttachment(file, imageType,) {
        let data = new FormData();
        data.append('file', file);
        // let url = `https://ww-upload-proxy.ecomgraduates.workers.dev/upload/${imageType}`;
        let url = `${this.baseUrl}/portal/api/v1/Attachment/upload/${imageType}`;
    
        let config = {
            method: 'post',
            url: url,
            headers: {
                'Content-Type': 'multipart/form-data',
                ...authHeader().headers
            },
            data: data
        };
    
        return axios.request(config);
    }
    
    deleteAttatchment(id, attachmentId) {
        return axios.delete(`${this.baseUrl}/portal/api/v1/attachment/${id}/attachment/${attachmentId}`, authHeader());
    }
}

