import './App.scss';
import { Route, Routes } from 'react-router-dom';
import { ConfigContextProvider } from './services/config.context';
import { AuthContextProvider } from './services/auth.context';
import { CacheContextProvider } from './services/cache.context';
import ProtectedRoute from './services/protected.route';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWarning } from '@fortawesome/free-solid-svg-icons';
import Unauthorized from "./components/unauthorized.component";
import SearchComponent from './components/search/search.component';
import WorkObject from './components/workobject/workobject.component';
import DistributionComponent from './components/distribution/distribution.component';
import BulkUploadComponent from './components/bulkupload/bulk.upload.component';
import SkuLookUpComponent from './components/skulookup/skulookup.component';
import DashboardHomeComponent from './components/dashboard/dashboard.home.component';
import ScrollTopComponent from './components/shared/scrolltop.component';
import ScrollDownComponent from './components/shared/scrolldown.component';
function App() {
  return (
    <>
      <ConfigContextProvider>
        <AuthContextProvider>
          <CacheContextProvider>
            <Routes>
              <Route path="/" element={
                <ProtectedRoute>
                    <SearchComponent />
                </ProtectedRoute>
              } />
              <Route path="/details/:orderId" element={
                <ProtectedRoute>
                  <WorkObject />
                </ProtectedRoute>
              } />
               <Route path="/distribution" element={
                <ProtectedRoute>
                  <DistributionComponent />
                </ProtectedRoute>
              } />
               <Route path="/skus" element={
                <ProtectedRoute>
                  <SkuLookUpComponent />
                </ProtectedRoute>
              } />
               <Route path="/dashboard" element={
                <ProtectedRoute permission={'user:write'}>
                  <DashboardHomeComponent />
                </ProtectedRoute>
              } />
              <Route path="/unauthorized" element={
                <Unauthorized />
              } />
              <Route path="*" element={
                <>
                  <h1>404 - Page Not Found</h1>
                  <FontAwesomeIcon icon={faWarning} className="text-warning" size="6x" />
                  <h5>The page you are trying to access does not exist. If this problem persists, please contact the site administrator for additional support.</h5>
                  <h5>Click <a href="/">here</a> to return.</h5>
                </>
              } />
            </Routes>
            <ScrollTopComponent />
            <ScrollDownComponent />
            <BulkUploadComponent />
          </CacheContextProvider>
        </AuthContextProvider>
      </ConfigContextProvider>
    </>
  );
}
export default App;
