import { useContext, useEffect, useState } from "react";
import moment from 'moment-timezone';
import AuthContext from "../../services/auth.context";
import { Button, Col, Form, Row } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDownWideShort, faArrowUpWideShort, faMagnifyingGlass, faBan, faListCheck, faDownload, faCancel } from "@fortawesome/free-solid-svg-icons";
import StatusBadgeComponent from "../shared/status.component";
import CacheContext from "../../services/cache.context";
import Select from 'react-select';
// import { convertToEasternTime } from "../../utils/convertToEasternTime";
export default function OrderFilterComponent({ onSearch, selectedOrders, selectedWorkObjectIds, setShowBulkAssignModal, setShowBulkDownloadModal, handleDeselectAll }) {
    const { user, hasPermission } = useContext(AuthContext);
    const { getUserList, getWorkObjectStatusLookup } = useContext(CacheContext)
    const [ statusOptions, setStatusOptions ] = useState([]);
    const [ userOptions, setUserOptions ] = useState([]);

    // Search filter variables
    const [ status, setStatus ] = useState([]);
    const [ assignedTo, setAssignedTo ] = useState([]);
    const [ createdOnStart, setCreatedOnStart ] = useState(null);
    const [ createdOnEnd, setCreatedOnEnd ] = useState(null);
    const [ orderName, setOrderName ] = useState([]);
    const [ customerEmail, setCustomerEmail ] = useState(null);
    const [ descendingOrder, setDescendingOrder ] = useState(true);
    const [ sku, setSku ] = useState([]);
    const [ tags, setTags ] = useState([]);

    useEffect(() => {
        Promise.all([
            getUserList(),
            getWorkObjectStatusLookup()
        ]).then(([userList, statusList]) => {
            const formattedUserOptions = userList.map(user => ({
                value: user.id,
                label: `${user.metadata.firstName} ${user.metadata.lastName}`
            }));

            setUserOptions(formattedUserOptions);

            // Set to signed in user
            setAssignedTo([user.id]);

            const formattedStatusOptions = Object.entries(statusList).map((arr) => ({
                value: arr[1],
                label: arr[0]
            }));

            setStatusOptions(formattedStatusOptions);
        });
    }, [getUserList, getWorkObjectStatusLookup, user]);

    useEffect(() => {
  
    }, [descendingOrder]);

    //TODO find a better way to handle sorting....
    const handleSortToggle = () => {
        setDescendingOrder(prevState => !prevState);

        //not the most ideal but it works doesnt work when you search
        setTimeout(() => {
            handleSearch();
        }, 100);
    };


    const handleSearch = () => {
        // Build search filter and call parent component
        const startDate = createdOnStart ? createdOnStart + 'T04:00:00.000Z' : null
        const endDate = createdOnEnd ? createdOnEnd + 'T03:59:59.999Z' : null
        const searchFilter = {
            status: status,
            assignedTo: assignedTo,
            createdOnStart: startDate ? startDate : null,
            createdOnEnd: endDate ? moment(endDate).add(1, 'days') : null,
            tags: tags,
            order: {
                orderName: orderName,
                customerEmail: customerEmail,
                lineItemSkus: sku
            },
            descendingOrder: descendingOrder
        };

        // if (searchFilter.createdOnStart) {
        //     searchFilter.createdOnStart = new Date(searchFilter.createdOnStart).toISOString();
        // }

        // if (searchFilter.createdOnEnd) {
        //     const endOfDay = new Date(searchFilter.createdOnEnd);
        //     endOfDay.setHours(23, 59, 59, 999);
        //     searchFilter.createdOnEnd = endOfDay.toISOString();
        // }

        searchFilter.order.orderName = searchFilter.order.orderName.filter(o => o !== '');
        searchFilter.customerEmail = searchFilter.customerEmail === '' ? null : searchFilter.customerEmail;

        onSearch(searchFilter);
    };

    const StatusOption = ({ innerProps, data }) => {
        return (
            <div {...innerProps} className="ms-2">
                <StatusBadgeComponent status={data.value} />
            </div>
        );
    };

    // Inline function for rendering selected values with the StatusBadgeComponent
    const CustomMultiValueLabel = props => {
        return <StatusBadgeComponent status={props.data.value} />;
    };

    // Custom components for react-select
    const customComponents = {
        Option: StatusOption,
        MultiValueLabel: CustomMultiValueLabel
    };

    const handleClear = () => {
        // Reset all fields back to defaults
        handleDeselectAll();
        setStatus([]);
        setSku([]);
        setTags([]);
        setAssignedTo([user.id]);
        setCreatedOnStart(null);
        setCreatedOnEnd(null);
        setOrderName([]);
        setCustomerEmail(null);
        handleSearch();
    };

    return (
        <Form>
            <Row>
                <Form.Group className="col-md-3">
                    <Form.Label>Order #</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="#123456"
                        value={orderName}
                        onChange={(e) => {
                            const value = e.target.value;
                            const updatedValue = value.includes('#') ? value : `#${value}`;
                            setOrderName([updatedValue]);
                        }}
                    />
                </Form.Group>
                <Form.Group className="col-md-3">
                    <Form.Label>Sku</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="QUO-3351231783"
                        value={sku}
                        onChange={(e) => setSku([e.target.value])}
                    />
                </Form.Group>
                <Form.Group className="col-md-3">
                    <Form.Label>Tag</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="tag1"
                        value={tags}
                        onChange={(e) => setTags([e.target.value])}
                    />
                </Form.Group>
                <Form.Group className="col-md-3">
                    <Form.Label>Customer Email</Form.Label>
                    <Form.Control
                        type="email"
                        placeholder="example@email.com"
                        value={customerEmail || ''}
                        onChange={(e) => setCustomerEmail(e.target.value)}
                    />
                </Form.Group>

                {hasPermission('order:read') && (
                    <>
                        <Form.Group className="col-md-3 mt-md-3 mt-2">
                            <Form.Label>Status</Form.Label>
                            <Select
                            isMulti
                            options={statusOptions}
                            components={customComponents}
                            onChange={(selectedOptions) => setStatus(selectedOptions.map(option => option.value))}
                            value={statusOptions.filter(option => status.includes(option.value))}
                        />
                        </Form.Group>
                        <Form.Group className="col-md-3  mt-md-3">
                            <Form.Label>Assigned to</Form.Label>
                            <Select
                                isMulti
                                options={userOptions}
                                onChange={(e) => setAssignedTo(e.map(option => option.value))}
                                value={userOptions.filter(option => assignedTo.includes(option.value))}
                            />
                        </Form.Group>
                    </>
                )}
                
                    <>
                        <Form.Group className="col-md-3  mt-md-3">
                            <Form.Label>Created On Start</Form.Label>
                            <Form.Control
                                type="date"
                                value={createdOnStart || ''}
                                onChange={(e) => setCreatedOnStart(e.target.value)}
                            />
                        </Form.Group>
                        <Form.Group className="col-md-3  mt-md-3">
                            <Form.Label>Created On End</Form.Label>
                            <Form.Control
                                type="date"
                                value={createdOnEnd || ''}
                                onChange={(e) => setCreatedOnEnd(e.target.value)}
                            />
                        </Form.Group>

                    </>
             
            </Row>
            <Row className="mt-3">
                <Col className="text-start">
                    {selectedOrders && selectedOrders.length > 0 && (
                        <>
                            <div className="d-flex mb-n1">
                                <Button variant="light" className="ms-2" onClick={() => setShowBulkAssignModal(true)}>
                                    <FontAwesomeIcon icon={faListCheck} /> Bulk Assign
                                    {selectedOrders && selectedOrders.length > 0 && (
                                        <span className="badge bg-dark ms-2">{selectedOrders.length}</span>
                                    )}
                                </Button>
                                <Button variant="light" className="ms-2" onClick={() => setShowBulkDownloadModal(true)}>
                                    <FontAwesomeIcon icon={faDownload} /> Bulk Download
                                    {selectedOrders && selectedOrders.length > 0 && (
                                        <span className="badge bg-dark ms-2">{selectedOrders.length}</span>
                                    )}
                                </Button>
                                <Button variant="light" className="ms-2" onClick={handleDeselectAll}>
                                <FontAwesomeIcon icon={faCancel} /> Deselect All
                                </Button>
                            </div>
                            <br />
                            <small className="text-muted rounded bg-light p-3 ms-2 mb-3 d-none d-md-inline">*use <span className="bg-dark text-white rounded-1 p-1 text-center ps-2 me-1">SHIFT </span> + <span className="bg-dark text-white rounded-1 p-1 text-center ps-2 me-1"> C </span> to copy order numbers to clipboard</small>
                        </>
                    )}
                </Col>
                <Col className="text-end">
                    <Button variant="light" onClick={handleSortToggle}>
                        <FontAwesomeIcon icon={descendingOrder ? faArrowDownWideShort : faArrowUpWideShort} /> Sort
                    </Button>
                    <Button variant="light" className="ms-2" onClick={handleClear}>
                        <FontAwesomeIcon icon={faBan} /> Clear Search
                    </Button>
                    <Button variant="" className="ms-2 btn-brand text-white" onClick={handleSearch}>
                        <FontAwesomeIcon icon={faMagnifyingGlass} /> Search
                    </Button>
                </Col>
            </Row>
        </Form>
    );
}