import React, { useState } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';

export default function ShowReturnModalComponent({ show, onHide, workObject, fetchWorkObject, setAlert, setShowReturnModal, orderService }) {
  const [ status, setStatus ] = useState('');
  const [ comment, setComment ] = useState('');
  const [ removeImage, setRemoveImage ] = useState(false);

  const submitReturnOrder = () => {
    orderService.returnOrder(workObject.workObject.id, parseInt(status), comment, removeImage)
      .then(res => {
        setAlert({ show: true, message: 'Order returned successfully', variant: 'success' });
        setShowReturnModal(false);
        fetchWorkObject();
      })
      .catch(err => {
        setAlert({ show: true, message: 'Failed to return order', variant: 'danger' });
      });
  };

  return (
    <Modal show={show} onHide={onHide}>
      <Modal.Header closeButton>
        <Modal.Title>Return Order</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group className="mb-3">
            <Form.Label>Return To</Form.Label>
            <Form.Select aria-label="Status select" onChange={(e) => setStatus(e.target.value)}>
              <option>Select</option>
              <option value="3">ArtFile Designer</option>
              <option value="4">PrintFile Designer</option>
            </Form.Select>
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>Comment</Form.Label>
            <Form.Control as="textarea" rows={3} value={comment} onChange={(e) => setComment(e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
              <Form.Check
                variant="dark"
                type="switch"
                id="remove-image-switch"
                label="Remove Image"
                onChange={(e) => setRemoveImage(e.target.checked)}
              />
            </Form.Group>
          <div className='text-end'>
            <Button variant="dark" onClick={submitReturnOrder} disabled={!status || !comment}>
              Submit Return
            </Button>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
};
