import { Table, OverlayTrigger, Tooltip, } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleQuestion } from "@fortawesome/free-solid-svg-icons";
import OrderTableRowComponent from "./order.table.row.component";

export default function OrderTableComponent({ results, onPopOverOpen, onCheckboxChange, selectedOrders, selectedOrderNames }) {
    const renderTooltip = (props, text) => (
        <Tooltip id="button-tooltip" {...props}>
            {text}
        </Tooltip>
    );
    return <>
        {results?.totalItems === 0 ? (
            <p className="text-muted text-center">No results were found...</p>
        ) : (
            <Table striped bordered hover className="rounded-table">
                <thead>
                    <tr>
                        <th>Order #</th>
                        <th>
                            Received On
                            <OverlayTrigger
                                placement="top"
                                overlay={props => renderTooltip(props, 'This is the date and time the order was received by the system.')}
                            >
                                <FontAwesomeIcon className="text-muted ms-1" size="sm" icon={faCircleQuestion} />
                            </OverlayTrigger>
                        </th>
                        <th>
                            Status
                            <OverlayTrigger
                                placement="top"
                                overlay={props => renderTooltip(props, 'This is the current status of the order.')}
                            >
                                <FontAwesomeIcon className="text-muted ms-1" size="sm" icon={faCircleQuestion} />
                            </OverlayTrigger>
                        </th>
                        <th>
                            Sensaria Status
                            <OverlayTrigger
                                placement="top"
                                overlay={props => renderTooltip(props, 'This is the current status provided by Sensaria.')}
                            >
                                <FontAwesomeIcon className="text-muted ms-1" size="sm" icon={faCircleQuestion} />
                            </OverlayTrigger>
                        </th>
                        <th>
                            Tags
                            <OverlayTrigger
                                placement="top"
                                overlay={props => renderTooltip(props, 'These are the tags associated with the order.')}
                            >
                                <FontAwesomeIcon className="text-muted ms-1" size="sm" icon={faCircleQuestion} />
                            </OverlayTrigger>
                        </th>
                        <th>
                            Assigned To
                            <OverlayTrigger
                                placement="top"
                                overlay={props => renderTooltip(props, 'This is the person the order is assigned to.')}
                            >
                                <FontAwesomeIcon className="text-muted ms-1" size="sm" icon={faCircleQuestion} />
                            </OverlayTrigger>
                        </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {results?.results.map((order, index) => (
                        <OrderTableRowComponent
                            index={index}
                            order={order}
                            key={order.id}
                            onPopOverOpen={onPopOverOpen}
                            selectedOrders={selectedOrders}
                            selectedOrderNames={selectedOrderNames}
                            onCheckboxChange={onCheckboxChange}
                        />
                    ))}
                </tbody>
            </Table>
        )}
    </>
}

