import React, { useState, useContext, useEffect } from 'react';
import CacheContext from '../../services/cache.context';
import { Modal, Button, Badge, ProgressBar } from 'react-bootstrap';
import Select from 'react-select';
import LoadingComponent from '../shared/loading.component';

export default function OrderBulkAssignComponent({ selectedOrders, showBulkAssignModal, setShowBulkAssignModal, handleDeselectAll }) {
    const { orderService, distributionService, getUserById } = useContext(CacheContext);

    const [selectedUser, setSelectedUser] = useState(null);
    const [selectedArtist, setSelectedArtist] = useState(null);
    const [selectedPrintFileDesigner, setSelectedPrintFileDesigner] = useState(null);
    const [userCapacities, setUserCapacities] = useState([]);
    const [users, setUsers] = useState([]);
    const [artists, setArtists] = useState([]);
    const [printFileDesigners, setPrintFileDesigners] = useState([]);
    const [totalFailed, setTotalFailed] = useState(0);
    const [totalSucceeded, setTotalSucceeded] = useState(0);
    const [orderType, setOrderType] = useState('artfile');
    const [isAssigning, setIsAssigning] = useState(false);
    const [orderStatus, setOrderStatus] = useState({});
    const [startTime, setStartTime] = useState(null);
    const [elapsedTime, setElapsedTime] = useState('');
    const [assigningOverStatus2, setAssigningOverStatus2] = useState(true);
    const [showNextStepMessage, setShowNextStepMessage] = useState(false);

    useEffect(() => {
        distributionService.getUsersCapacity(orderType).then(capacityData => {
            setUserCapacities(capacityData.data);
        });
    }, [distributionService, orderType]);

    useEffect(() => {
        if (userCapacities.length > 0) {
            const userPromises = userCapacities.map(uc => getUserById(uc.userId));
            Promise.all(userPromises).then(allUsers => {
                const options = allUsers.map((user, index) => ({
                    value: userCapacities[index].userId,
                    label: user,
                }));
                setUsers(options);
            });
        }
    }, [userCapacities, getUserById]);

    useEffect(() => {
        distributionService.getUsersCapacity('artfile').then(capacityData => {
            const artistPromises = capacityData.data.map(uc => getUserById(uc.userId));
            Promise.all(artistPromises).then(allArtists => {
                const options = allArtists.map((artist, index) => ({
                    value: capacityData.data[index].userId,
                    label: artist,
                }));
                setArtists(options);
            });
        });

        distributionService.getUsersCapacity('printfile').then(capacityData => {
            const designerPromises = capacityData.data.map(uc => getUserById(uc.userId));
            Promise.all(designerPromises).then(allDesigners => {
                const options = allDesigners.map((designer, index) => ({
                    value: capacityData.data[index].userId,
                    label: designer,
                }));
                setPrintFileDesigners(options);
            });
        });
    }, [distributionService, getUserById]);

    const handleCloseModal = () => setShowBulkAssignModal(false);

    const handleTypeChange = value => {
        setOrderType(value);
        setSelectedUser(null);
    };

    useEffect(() => {
        let timer;
        if (isAssigning && startTime) {
            timer = setInterval(() => {
                const now = new Date();
                const diffInSeconds = Math.floor((now - startTime) / 1000);
                const minutes = Math.floor(diffInSeconds / 60);
                const seconds = diffInSeconds % 60;
                setElapsedTime(`${minutes ? `${minutes} minutes` : ''} ${seconds} seconds`);
            }, 1000);
        } else if (!isAssigning && startTime) {
            clearInterval(timer);
        }
        return () => {
            if (timer) {
                clearInterval(timer);
            }
        };
    }, [isAssigning, startTime]);

    const handleBulkAssign = async () => {
        if (selectedUser && selectedOrders.length > 0) {
            setStartTime(new Date());
            setIsAssigning(true);
            setTotalFailed(0);
            setTotalSucceeded(0);

            const orderIdToNameMap = {};
            selectedOrders.forEach(order => {
                orderIdToNameMap[order.id] = order.orderName;
            });

            // Filter orders with status over 2
            const ordersOverStatus2 = selectedOrders.filter(order => order.status > 2);

            // Process orders with status over 2 using bulkAssign
            const batchSize = 25;
            for (let i = 0; i < ordersOverStatus2.length; i += batchSize) {
                const batch = ordersOverStatus2.slice(i, i + batchSize).map(order => order.id);
                try {
                    const response = await orderService.bulkAssign(batch, selectedUser.value);
                    const results = response.data.results;

                    for (const [orderId, success] of Object.entries(results)) {
                        const orderName = orderIdToNameMap[orderId];
                        setOrderStatus(prev => ({
                            ...prev,
                            [orderName]: success ? 'success' : `already assigned to ${selectedUser.label}`,
                        }));
                    }

                    setTotalSucceeded(prev => prev + Object.values(results).filter(x => x).length);
                    setTotalFailed(prev => prev + batch.length - Object.values(results).filter(x => x).length);
                } catch (error) {
                    setTotalFailed(prev => prev + batch.length);
                    for (const orderId of batch) {
                        const orderName = orderIdToNameMap[orderId];
                        setOrderStatus(prev => ({
                            ...prev,
                            [orderName]: 'failed',
                        }));
                    }
                }
                await new Promise(resolve => setTimeout(resolve, 0));
            }

            setIsAssigning(false);
            // Check if there are any orders with status 2 or less
            const ordersStatus2OrLess = selectedOrders.filter(order => order.status <= 2);
            if (ordersStatus2OrLess.length > 0) {
                setShowNextStepMessage(true);
            } else {
                setAssigningOverStatus2(false);
            }
        }
    };

    const handleManualAssign = async () => {
        if (selectedArtist && selectedPrintFileDesigner && selectedOrders.length > 0) {
            setStartTime(new Date());
            setIsAssigning(true);
            setTotalFailed(0);
            setTotalSucceeded(0);

            const orderIdToNameMap = {};
            selectedOrders.forEach(order => {
                orderIdToNameMap[order.id] = order.orderName;
            });

            // Filter orders with status 2 or less
            const ordersStatus2OrLess = selectedOrders.filter(order => order.status <= 2);

            // Process orders with status 2 or less using manuallyAssign
            for (const order of ordersStatus2OrLess) {
                try {
                    await orderService.manuallyAssign(order.workObjectId, selectedArtist.value, selectedPrintFileDesigner.value);
                    setOrderStatus(prev => ({
                        ...prev,
                        [order.orderName]: 'success',
                    }));
                    setTotalSucceeded(prev => prev + 1);
                } catch (error) {
                    setOrderStatus(prev => ({
                        ...prev,
                        [order.orderName]: 'failed',
                    }));
                    setTotalFailed(prev => prev + 1);
                }
                await new Promise(resolve => setTimeout(resolve, 0));
            }

            setIsAssigning(false);
            setAssigningOverStatus2(false);
        }
    };

    const handleNextStep = () => {
        setAssigningOverStatus2(false);
        setShowNextStepMessage(false);
    };

    const exportOrderList = () => {
        const now = new Date();
        const formattedDate = `${now.getMonth() + 1}-${now.getDate()}-${now.getFullYear()}-${now.getHours()}-${now.getMinutes()}-bulk-assign-report.csv`;
        const csvContent =
            'data:text/csv;charset=utf-8,' +
            'Order Name,Order ID,Assigned To,Status,Timestamp\n' +
            selectedOrders
                .map(
                    order =>
                        `${order.orderName},${order.id},${selectedUser.label},${orderStatus[order.orderName] || 'pending'},${new Date().toLocaleString()}`
                )
                .join('\n');
        const encodedUri = encodeURI(csvContent);
        const link = document.createElement('a');
        link.setAttribute('href', encodedUri);
        link.setAttribute('download', formattedDate);
        link.click();
    };

    const progressOverStatus2 = selectedOrders.length ? (totalFailed + totalSucceeded) / selectedOrders.filter(order => order.status > 2).length * 100 : 0;
    const progressStatus2OrLess = selectedOrders.length ? (totalFailed + totalSucceeded) / selectedOrders.filter(order => order.status <= 2).length * 100 : 0;

    return (
        <>
            <Modal show={showBulkAssignModal} onHide={handleCloseModal} size="xl">
                <Modal.Header closeButton>
                    <Modal.Title>Bulk Assign</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {isAssigning && <LoadingComponent />}
                    {assigningOverStatus2 && (
                        <div className="text-center">
                            <Button className="btn-sm rounded-start rounded-0" variant={orderType === 'artfile' ? 'dark' : 'light'} onClick={() => handleTypeChange('artfile')}>
                                Art File
                            </Button>
                            <Button className="btn-sm rounded-end rounded-0" variant={orderType === 'printfile' ? 'dark' : 'light'} onClick={() => handleTypeChange('printfile')}>
                                Print File
                            </Button>
                            <div className="mt-2 mb-3">
                                You're Assigning <Badge className="bg-secondary">{orderType}</Badge>
                                <hr />
                                <Badge className="bg-dark ms-2">Total Orders Selected: {selectedOrders.length}</Badge> |
                                <Badge className="bg-danger ms-2">Failed: {totalFailed}</Badge> |
                                <Badge className="bg-success ms-2">Succeeded: {totalSucceeded}</Badge>
                            </div>
                            <ProgressBar variant="success" now={progressOverStatus2} label={`${Math.round(progressOverStatus2)}%`} className="mt-3" />
                            <div className="mt-2">{elapsedTime && `Elapsed Time: ${elapsedTime}`}</div>
                        </div>
                    )}
                    {!assigningOverStatus2 && (
                        <div className="text-center">
                            <div className="mt-2 mb-3">
                                <Badge className="bg-dark ms-2">Total Orders Selected: {selectedOrders.length}</Badge> |
                                <Badge className="bg-danger ms-2">Failed: {totalFailed}</Badge> |
                                <Badge className="bg-success ms-2">Succeeded: {totalSucceeded}</Badge>
                            </div>
                            <ProgressBar variant="success" now={progressStatus2OrLess} label={`${Math.round(progressStatus2OrLess)}%`} className="mt-3" />
                            <div className="mt-2">{elapsedTime && `Elapsed Time: ${elapsedTime}`}</div>
                        </div>
                    )}
                    {assigningOverStatus2 ? (
                        <div className="mb-3 mt-3 text-center">
                            <Select options={users} value={selectedUser} onChange={setSelectedUser} placeholder="Select User" className="mb-3" styles={{
                                // Fixes the overlapping problem of the component
                                menu: provided => ({ ...provided, zIndex: 9999 })
                            }} />
                        </div>
                    ) : (
                        <div className="mb-3 mt-3 text-center">
                            <Select options={artists} value={selectedArtist} onChange={setSelectedArtist} placeholder="Select Artist" className="mb-3" styles={{
                                // Fixes the overlapping problem of the component
                                menu: provided => ({ ...provided, zIndex: 9999 })
                            }} />
                            <Select options={printFileDesigners} value={selectedPrintFileDesigner} onChange={setSelectedPrintFileDesigner} placeholder="Select Print File Designer" className="mb-3" styles={{
                                // Fixes the overlapping problem of the component
                                menu: provided => ({ ...provided, zIndex: 9999 })
                            }} />
                        </div>
                    )}
                    {selectedOrders.length > 0 && (
                        <div className="assigned-orders-list mt-2 text-center bg-light border border-secondary rounded pb-0 shadow-sm" style={{ maxHeight: '400px', overflowY: 'auto' }}>
                            <p className="mt-3 mb-0">Orders to be assigned:</p>
                            <hr />
                            {selectedOrders.map(order => (
                                <div key={order.id}>
                                    {order.orderName} -
                                    <Badge className={`ms-1 ${orderStatus[order.orderName] === 'success' ? 'bg-success' : orderStatus[order.orderName]?.startsWith('already assigned') ? 'bg-warning' : 'bg-danger'}`}>
                                        {orderStatus[order.orderName] || 'pending'}
                                    </Badge>
                                </div>
                            ))}

                            <div className="sticky-bottom text-center mt-3 mb-0 w-100">
                                {totalSucceeded + totalFailed === selectedOrders.length ? (
                                    <Button variant="dark" className="shadow-sm w-100 rounded-0" onClick={exportOrderList}>
                                        Export List
                                    </Button>
                                ) : showNextStepMessage ? (
                                    <Button variant="primary" className='shadow-sm w-100 rounded-0' onClick={handleNextStep}>
                                        Next Step
                                    </Button>
                                ) : (
                                    <Button variant="dark" className="shadow-sm w-100 rounded-0" onClick={assigningOverStatus2 ? handleBulkAssign : handleManualAssign} disabled={isAssigning}>
                                        {isAssigning ? (
                                            <>
                                                <LoadingComponent variant='light' />
                                            </>
                                        ) : (
                                            'Confirm and Assign'
                                        )}
                                    </Button>
                                )}
                            </div>
                        </div>
                    )}
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseModal}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}