import 'chart.js/auto'; 
import { Card, Col, Badge } from 'react-bootstrap';
import { Bar, Line, Pie, PolarArea } from "react-chartjs-2";

export function ChartComponent({chart}) {
    const name = chart?.name;
    const kind = chart?.kind;
    const data = chart?.data;

    console.log(chart);

    const RenderChart = () => {
        if (data === null || data === undefined) {
            return <></>
        }

        switch(kind) {
            case 10: // Pie Chart
                return <Pie data={getChartData(data)} options = {{legend: { display: window.innerWidth > 500, position: 'right', align: 'center' }}} />
            case 11: // Bar Chart
                return <Bar data={getChartData(data)} />
            case 12: // Line Chart
                return <Line data={getChartData(data)} />
            case 13: // Polaris
                return <PolarArea data={getChartData(data)} options = {{legend: { display: window.innerWidth > 500, position: 'right', align: 'center' }}} />
            case 20:
                return <>Table</>
            case 21: // Data card (text only)
                return <Badge pill bg="secondary"> {data.value?.toLocaleString()}</Badge>
            default: 
                <span>Unsupported chart type.</span>
        }
    };

    const getSize = (kind) => {
        switch(kind) {
            case 21:
                return 2;
            default:
                return 6;
        }  
    }

    const getChartData = (res) => {
        if (res) {
            const colors = getBootstrapColors();
            const dataSets = [ ];
            var yAxisLabels = [ ];
            
            // Get name of series
            for (var series in res) {
                console.log(series);

                const data = res[series];

                dataSets.push({
                    label: series,
                    data: data.map((d) => d.count),
                    borderColor: (kind === 12 || kind === 11) ? colors[0] : colors,
                    borderWidth: kind === 12 ? 4 : 1,
                    pointBackgroundColor: colors[0],
                    backgroundColor: (kind === 12 || kind === 11) ? getTransparentColorSet(colors[0]) :  getBgColors(),
                    hoverBackgroundColor: (kind === 11) ? colors[0] : getBootstrapColors(),
                })

                yAxisLabels = data.map((d) => d.label);
            }

            
            console.log(dataSets);

            return {
                labels: yAxisLabels,
                datasets: dataSets
            };
        }
    }

    const getTransparentColorSet = (c) => {
        let color = c;
        if (c.length === 7)
            color = c + '55';
        else if (c.length === 4)
            color = c + c.substring(1) + '55';
    
        return color;
    }
    
    const getBgColors = () => {
        const colors = getBootstrapColors();
        for (let i = 0; i < colors.length; i++) {
            colors[i] = getTransparentColorSet(colors[i]);
        }
    
        return colors;
    }
    
    const getBootstrapColors = () => {
        const style = getComputedStyle(document.body);
        const color = [ "#f19a3e", "#659157" ,"#00b4d8", "#d88c9a", "#8e7dbe" ];
        color.push(style.getPropertyValue('--success'));
        color.push(style.getPropertyValue('--warning'));
        color.push(style.getPropertyValue('--danger'));
        color.push(style.getPropertyValue('--light'));
        color.push(style.getPropertyValue('--info'));
        color.push(style.getPropertyValue('--secondary'));
        color.push(style.getPropertyValue('--dark'));
        return color;
    }
    
    if (name === undefined || name === null || kind === undefined || kind === null|| data === undefined || data === null) {
        return <p>Error loading chart data.</p>
    } else {
        return <>
            <Col sm={12} lg={getSize(kind)} className="mb-3">
                <Card>
                    <Card.Body>
                        <Card.Title className="text-center">
                            {name}
                        </Card.Title>

                        <Card.Body className="text-center">
                            <RenderChart />
                        </Card.Body>
                    </Card.Body>
                </Card>
            </Col>
        </>
    }
}