import { Card } from "react-bootstrap";
import OrderAssignedComponent from "./shared/order.assigned.component";
export default function Welcome({ user, details, greeting = "Welcome", totalAssigned }) {
    const displayName = user?.firstName && user?.lastName
        ? `${user.firstName} ${user.lastName}`
        : user.email;

    return (
        <>
            {user !== null && details !== null && (
                <div className="d-flex justify-content-between flex-column flex-md-row">
                    <Card.Title>{greeting} {displayName}</Card.Title>
                    {totalAssigned !== undefined && (<OrderAssignedComponent totalAssigned={totalAssigned} />)}
                </div>
            )}
            <hr className="mt-1"/>
        </>
    );
}
