import React, { useState, useContext } from "react";
import CacheContext from "../../services/cache.context";
import { Modal, Button, Form, InputGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
export default function ShowTagsModalComponent({ show, onHide, workObject, fetchWorkObject, setAlert, setShowTagsModal }) {
    const [ tag, setTag ] = useState("");
    const { orderService } = useContext(CacheContext);

    const handleAddTag = () => {
        orderService
            .addTag(workObject.workObject.id, tag)
            .then((res) => {
                setAlert({ show: true, message: `Tag ${tag} succesfully added `, variant: 'success' })
                fetchWorkObject();
            })
            .catch((err) => {
                setAlert({ show: true, message: 'Failed to add tag', variant: 'danger' });
            }).finally(() => {
                setShowTagsModal(false);
            })
    };
    const handleDeleteTag = (tag) => {
        orderService
            .deleteTag(workObject.workObject.id, tag)
            .then((res) => {
                setAlert({ show: true, message: `Tag ${tag} succesfully deleted `, variant: 'success' })
                fetchWorkObject();
            })
            .catch((err) => {
                setAlert({ show: true, message: 'Failed to delete tag', variant: 'danger' });
            }).finally(() => {
                setShowTagsModal(false);
            })
    }

    return (
        <Modal show={show} onHide={onHide}>
            <Modal.Header closeButton>
                <Modal.Title>Edit Tags</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <InputGroup>
                        <Form.Control
                            type="text"
                            placeholder="Enter tag"
                            value={tag}
                            onChange={(e) => setTag(e.target.value)}
                        />
                    </InputGroup>
                </Form>
                {workObject.workObject?.tags?.length > 0 ? (
                    <div className="mt-3 rounded p-2 border border-light">
                        <div className="mt-3 text-center">
                            <h5 className="mb-0">Current Tags</h5>
                            <small className="text-muted">Click to delete a tag</small>
                        </div>
                        <div className="mt-3">
                            {workObject.workObject.tags.map((tag) => (
                                <Button variant="danger btn-sm" className="me-2" onClick={() => handleDeleteTag(tag)}>
                                    <FontAwesomeIcon icon={faTrash} className="me-1" /> {tag}
                                </Button>
                            ))}
                        </div>
                    </div>
                ) : (
                    <div className="mt-3 text-center">
                        <h5 className="mb-0">No Tags</h5>
                    </div>
                )}
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}>
                    Close
                </Button>
                <Button variant="dark" onClick={handleAddTag}>
                    Add Tag
                </Button>
            </Modal.Footer>
        </Modal>
    );
};
