import React, { useState } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';

export default function EditSkuModal({ show, handleClose, sku, handleEditChange, updateSku, productshippingprofiles, errorMessage, handleIgnoreSku}) {
    const [showConfirmation, setShowConfirmation] = useState(false);

    const handleIgnoreClick = () => {
        setShowConfirmation(true);
    };

    const handleConfirmIgnore = () => {
        setShowConfirmation(false);
        handleIgnoreSku();
    };

    const handleCancelIgnore = () => {
        setShowConfirmation(false);
    };

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Edit SKU</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {errorMessage && <p className="text-danger me-auto">{errorMessage}</p>}
                <Form>
                    <Form.Group className="mb-3">
                        <Form.Label>Title</Form.Label>
                        <Form.Control
                            type="text"
                            name="title"
                            value={sku.title}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Handle</Form.Label>
                        <Form.Control
                            type="text"
                            name="handle"
                            value={sku.handle.replace(/\s/g, '-')}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 1</Form.Label>
                        <Form.Control
                            type="text"
                            name="option1"
                            value={sku.option1}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 2</Form.Label>
                        <Form.Control
                            type="text"
                            name="option2"
                            value={sku.option2}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 3</Form.Label>
                        <Form.Control
                            type="text"
                            name="option3"
                            value={sku.option3}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Sensaria PID</Form.Label>
                        <Form.Control
                            type="number"
                            name="sensariaProductId"
                            value={sku.sensariaProductId || ''}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Shipping Method</Form.Label>
                        <Form.Select
                            name="shippingMethod"
                            value={sku.shippingMethod || ''}
                            onChange={handleEditChange}>
                            <option value={null}>NONE</option>
                            {productshippingprofiles.map(profile => (
                                <option key={profile.id} value={profile.id}>
                                    {profile.method} ({profile.id})
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Artfile Needed</Form.Label>
                        <Form.Check
                            type="checkbox"
                            name="artfileNeeded"
                            checked={sku.artfileNeeded}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>File Type</Form.Label>
                        <Form.Select
                            name="fileType"
                            value={sku.fileType || ''}
                            onChange={handleEditChange}>
                            <option value={null}>NONE</option>
                            <option value=".jpg">.jpg</option>
                            <option value=".png">.png</option>
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Number of CX Images</Form.Label>
                        <Form.Control
                            type="number"
                            name="numberOfCxImages"
                            value={sku.numberOfCxImages || 0}
                            onChange={handleEditChange}
                        />
                    </Form.Group>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                {errorMessage && <p className="text-danger me-auto">{errorMessage}</p>}
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
                <Button className='btn-brand' onClick={updateSku}>
                    Save Changes
                </Button>
                <Button variant="danger" onClick={handleIgnoreClick}>
                    Ignore SKU
                </Button>
            </Modal.Footer>

            <Modal show={showConfirmation} onHide={handleCancelIgnore}>
                <Modal.Header closeButton className='bg-danger text-white'>
                    <Modal.Title>Confirm Ignore SKU</Modal.Title>
                </Modal.Header>
                <Modal.Body className='bg-danger text-white'>
                    <p>This action can affect many orders. Are you sure you want to ignore this SKU?</p>
                </Modal.Body>
                <Modal.Footer className='bg-danger text-white'>
                    <Button variant="secondary" onClick={handleCancelIgnore}>
                        Cancel
                    </Button>
                    <Button variant="danger" className='border border-white' onClick={handleConfirmIgnore}>
                        Confirm Ignore
                    </Button>
                </Modal.Footer>
            </Modal>
        </Modal>
    );
}