import React from 'react';
import { Modal, Button, ListGroup, Badge } from 'react-bootstrap';

export default function DeleteSkuModal({ show, handleClose, sku, deleteSku, deletionStatus, deletionMessage, errorMessage }) {
    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Delete SKU</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {errorMessage && <p className="text-danger me-auto">{errorMessage}</p>}
                {sku && !deletionStatus && (
                    <div>
                        <p>Are you sure you want to delete the SKU: <Badge pill bg="dark">{sku.variantSku}</Badge></p>
                        <ListGroup>
                            <ListGroup.Item><strong>Title:</strong> {sku.title}</ListGroup.Item>
                            <ListGroup.Item><strong>Handle:</strong> {sku.handle}</ListGroup.Item>
                            <ListGroup.Item><strong>Option 1:</strong> {sku.option1}</ListGroup.Item>
                            <ListGroup.Item><strong>Option 2:</strong> {sku.option2}</ListGroup.Item>
                            <ListGroup.Item><strong>Option 3:</strong> {sku.option3}</ListGroup.Item>
                            <ListGroup.Item><strong>Variant SKU:</strong> {sku.variantSku}</ListGroup.Item>
                            <ListGroup.Item><strong>Sensaria PID:</strong> {sku.sensariaProductId}</ListGroup.Item>
                            <ListGroup.Item><strong>Ship Method:</strong> {sku.shippingMethod}</ListGroup.Item>
                            <ListGroup.Item><strong>Artfile Needed:</strong> {sku.artfileNeeded ? "Yes" : "No"}</ListGroup.Item>
                            <ListGroup.Item><strong>Type:</strong> {sku.fileType}</ListGroup.Item>
                            <ListGroup.Item><strong># of CX Images:</strong> {sku.numberOfCxImages}</ListGroup.Item>
                        </ListGroup>
                    </div>
                )}
                {deletionStatus === 'success' && (
                    <p className="text-success">{deletionMessage}</p>
                )}
                {deletionStatus === 'error' && (
                    <p className="text-danger">{deletionMessage}</p>
                )}
            </Modal.Body>
            <Modal.Footer>
                {errorMessage && <p className="text-danger me-auto">{errorMessage}</p>}
                {deletionStatus ? (
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                ) : (
                    <>
                        <Button variant="secondary" onClick={handleClose}>
                            Cancel
                        </Button>
                        <Button variant="danger" onClick={deleteSku}>
                            Delete
                        </Button>
                    </>
                )}
            </Modal.Footer>
        </Modal>
    );
}