import { useContext, useEffect, useState, useRef } from "react";
import AuthContext from "../../services/auth.context";
import ConfigContext from "../../services/config.context";
import CacheContext from "../../services/cache.context";
import { copyToClipboard } from "../../utils/copyToClipboard";
import OrderTableComponent from "./order.table.component";
import PaginationComponent from "../shared/pagination.component";
import LoadingComponent from "../shared/loading.component";
import OrderPopoverComponent from "./order.popover.component";
import OrderFilterComponent from "./order.filter.component";
import OrderBulkAssignComponent from "./order.bulkassign.component";
import OrderBulkDownloadComponent from "./order.bulkdownload.component";

export default function SearchComponent() {
  const { user } = useContext(AuthContext);
  const { config } = useContext(ConfigContext);
  const [ isError, setIsError ] = useState(false);
  const [ error, setError ] = useState(null);
  const [ page, setPage ] = useState(1);
  const [ pageSize, setPageSize ] = useState(10);
  const [ results, setResults ] = useState(null);
  const [ searchFilter, setSearchFilter] = useState(() => {
    const saved = sessionStorage.getItem('searchFilter');
    return saved ? JSON.parse(saved) : {};
  });
  const [ isLoading, setIsLoading ] = useState(false);
  const [ currentOrderId, setcurrentOrderId ] = useState(null);
  const { orderService } = useContext(CacheContext);
  const [ showPopOver, setShowPopOver ] = useState(false);
  const [ popoverTarget, setPopoverTarget ] = useState(null);
  const [ selectedOrders, setSelectedOrders ] = useState([]);
  const [ selectedOrderNames, setSelectedOrderNames ] = useState([]);
  const [ selectedWorkObjectIds, setSelectedWorkObjectIds ] = useState([]);
  const lastCheckedIndexRef = useRef(null);
  const shiftKeyPressedRef = useRef(false);
  const [ showBulkAssignModal, setShowBulkAssignModal ] = useState(false);
  const [ showBulkDownloadModal, setShowBulkDownloadModal ] = useState(false);

  useEffect(() => {
    orderService.getAssignedOrders(user.id, page, pageSize, searchFilter)
      .then((res) => {
        setResults(res.data);
        setIsLoading(false);
        setError(null);
      }, (err) => {
        setIsError(true);
        setError(err.message);
      }).finally(() => {
        setIsLoading(false);
      });
  }, [page, searchFilter, user.id, pageSize, config, orderService]);

  useEffect(() => {
    // Persist searchFilter to sessionStorage whenever it changes
    sessionStorage.setItem('searchFilter', JSON.stringify(searchFilter));
  }, [searchFilter]);

  const handleCheckboxChange = (event, isChecked, shopifyOrderId, orderName, workObjectId, status, index) => {
    if (shiftKeyPressedRef.current && lastCheckedIndexRef.current !== null) {
      // Determine the range of checkboxes between the first and last clicked
      const start = Math.min(lastCheckedIndexRef.current, index);
      const end = Math.max(lastCheckedIndexRef.current, index);
  
      // Extract the order IDs, names, and statuses within the selected range
      const ordersToToggle = results.results.slice(start, end + 1).map(order => ({
        id: order.shopifyOrderId,
        orderName: order.orderName,
        workObjectId: order.id,
        status: order.status
      }));
  
      let newSelectedOrders;
      // If the checkbox is being checked, add the new IDs and statuses to the existing list
      if (isChecked) {
        // Combine the existing selected orders with the new range of orders
        newSelectedOrders = [
          ...selectedOrders,
          ...ordersToToggle.filter(order => !selectedOrders.some(selectedOrder => selectedOrder.id === order.id))
        ];
      } else {
        // Otherwise, remove the IDs and statuses from the existing list
        newSelectedOrders = selectedOrders.filter(order => !ordersToToggle.some(toggleOrder => toggleOrder.id === order.id));
      }
  
      // Update state with the new lists
      setSelectedOrders(newSelectedOrders);
    } else {
      // No Shift key, handle a single checkbox click
      if (isChecked) {
        // Checkbox is checked: add the ID and status to the list
        setSelectedOrders(prevOrders => [
          ...prevOrders,
          { id: shopifyOrderId, workObjectId, orderName, status }
        ]);
      } else {
        // Checkbox is unchecked: remove the ID and status from the list
        setSelectedOrders(prevOrders => prevOrders.filter(order => order.id !== shopifyOrderId));
      }
    }
    // Update the index of the last clicked checkbox
    lastCheckedIndexRef.current = index;
  };

  useEffect(() => {
    setSelectedWorkObjectIds(selectedOrders.map(order => order.workObjectId));
  }, [selectedOrders]);

  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === 'Shift') {
        shiftKeyPressedRef.current = true;
      }
    };

    const handleKeyUp = (e) => {
      if (e.key === 'Shift') {
        shiftKeyPressedRef.current = false;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('keyup', handleKeyUp);
    };
  }, []);
  
  useEffect(() => {
    const handleKeyPress = (e) => {
      if (e.shiftKey && e.code === 'KeyC') {
        // Copy selectedOrderNames to clipboard as a list
        const orderNamesList = selectedOrderNames.join('\n'); // Joining as a list with newline
        copyToClipboard(orderNamesList); // Then copy text to clipboard
      }
    };

    window.addEventListener('keydown', handleKeyPress);

    return () => {
      window.removeEventListener('keydown', handleKeyPress);
    };
  }, [selectedOrderNames, isError]);

  const handleSearch = (params) => {
    setIsError(false);
    setIsLoading(true);
    setSearchFilter(params);
    setPage(1);
  };

  const handlePageChange = (page) => {
    setPage(page);
  };

  const handleOpenPopOver = (event, orderID) => {
    setcurrentOrderId(orderID);
    setPopoverTarget(event.target);
    setShowPopOver(true);
  };

  const handleClosePopOver = () => {
    setShowPopOver(false);
  };

  const handleDeselectAll = () => {
    setSelectedOrders([]);
    setSelectedOrderNames([]);
    setSelectedWorkObjectIds([]);
  }
  return (
    <>
      <OrderFilterComponent 
        onSearch={handleSearch}
        selectedOrders={selectedOrders}
        selectedWorkObjectIds={selectedWorkObjectIds}
        showBulkAssignModal={showBulkAssignModal} 
        setShowBulkAssignModal={setShowBulkAssignModal} 
        showBulkDownloadModal={showBulkDownloadModal} 
        setShowBulkDownloadModal={setShowBulkDownloadModal} 
        handleDeselectAll={handleDeselectAll}
      />
      <hr />
      {
        isLoading && !isError ? (
          <LoadingComponent message="Searching..." />
        ) : isError ? (
          <div className="alert alert-danger" role="alert">
            {error}
          </div>
        ) : (
          <>
            <OrderTableComponent
              results={results}
              selectedOrders={selectedOrders}
              selectedOrderNames={selectedOrderNames}
              onPopOverOpen={handleOpenPopOver}
              onCheckboxChange={handleCheckboxChange}
            />
            <PaginationComponent
              page={page}
              pageSize={pageSize}
              setPageSize={setPageSize}
              totalItems={results?.totalItems}
              setPage={(page) => handlePageChange(page)}
            />
          </>
        )
      }
      {currentOrderId && (
        <OrderPopoverComponent
          show={showPopOver}
          target={popoverTarget}
          orderId={currentOrderId}
          onClose={handleClosePopOver}
        />
      )}
      <OrderBulkAssignComponent 
        selectedOrders={selectedOrders} 
        selectedOrderNames={selectedOrderNames}
        setShowBulkAssignModal={setShowBulkAssignModal}
        showBulkAssignModal={showBulkAssignModal}
        handleDeselectAll={handleDeselectAll}
      />
      <OrderBulkDownloadComponent 
        selectedWorkObjectIds={selectedWorkObjectIds}
        showBulkDownloadModal={showBulkDownloadModal}
        setShowBulkDownloadModal={setShowBulkDownloadModal}
        handleDeselectAll={handleDeselectAll}
      />
    </>
  );
}
