import { useState, useRef, useContext } from 'react';
import { Alert } from 'react-bootstrap';
import CacheContext from '../../services/cache.context';
import LoadingComponent from './loading.component';
import MySwal from 'sweetalert2';
import AuthContext from '../../services/auth.context';
import { shouldDisplayElement } from '../../utils/shouldDisplayElement';
export default function UploadComponent({
  fetchWorkObject,
  workObjectId,
  imageType,
  lineItemId,
  fileTypes,
  config,
  cxPhotoIndex,
  isReplacing = false,
  isAddonProduct,
  workObject,
  lineItemIndex
}) {
  const { hasPermission } = useContext(AuthContext);
  const [ isOver, setIsOver ] = useState(false);
  const [ isUploading, setIsUploading ] = useState(false);
  const { attachmentService, showToastNotification } = useContext(CacheContext);
  const fileInputRef = useRef(null);
  const showUpload = shouldDisplayElement(hasPermission, isAddonProduct, imageType, workObject);

  fileTypes = fileTypes.map(fileType => fileType.replace('.', ''));
  const handleAutomaticUpload = (file, workObjectId, imageType, lineItemId, cxPhotoIndex) => {
    if (file) {
      setIsUploading(true);
      attachmentService.uploadAttachment(file, workObjectId, imageType, lineItemId, cxPhotoIndex)
        .then(response => {
          showToastNotification(`Successfully uploaded ${response.data.fileName}`, 'success');
          fetchWorkObject();
        })
        .catch(error => {
          console.log(error);
          showToastNotification(error.response.data.Message, 'danger');
        }).finally(() => {
          setIsUploading(false);
        })
    }
  };

  const handleDragOver = (event) => {
    event.preventDefault();
    setIsOver(true);
  };

  const handleDragLeave = (event) => {
    event.preventDefault();
    setIsOver(false);
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsOver(false);
    processFiles(event.dataTransfer.files);
  };

  const handleFileSelect = (event) => {
    processFiles(event.target.files);
  };

  const openFilePicker = () => {
    fileInputRef.current.click();
  };
  const fileAcceptTypes = imageType === 'artfile' ? config.artfileTypes : fileTypes;
  const processFiles = (rawFiles) => {
  
    const selectedFiles = [];
    const rejectedFiles = [];
  
    Array.from(rawFiles).forEach(file => {
      let isValid = false;
  
      for (const fileType of fileAcceptTypes) {
        if (file.name.toLowerCase().endsWith(`.${fileType}`)) {
          isValid = true;
          break;
        }
      }
  
      if (isValid) {
        selectedFiles.push(file);
      } else {
        rejectedFiles.push(file);
        const reason = `File ${file.name} was rejected because it's not a ${fileAcceptTypes.join(', ')} file.`;
        showToastNotification(reason, 'warning');
      }
    });
  
    if (isReplacing) {
      MySwal.fire({
        title: 'Are you sure?',
        text: 'Do you want to replace the existing files?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
      }).then((result) => {
        if (result.isConfirmed) {
          uploadFiles(selectedFiles);
        }
      });
    } else {
      uploadFiles(selectedFiles);
    }
  };  
  
  const uploadFiles = (files) => {
    files.forEach((file) => {
      handleAutomaticUpload(file, workObjectId, imageType, lineItemId, cxPhotoIndex);
    });
  };


  if ((imageType === 'cx' || imageType === 'artfile') && isAddonProduct) {
    if (imageType === 'artfile' && (workObject.workObject.status > 3 && workObject.workObject.status < 6)) {
      return (
        <Alert variant='info' className='mb-0 text-center'>
          <Alert.Heading>Warning</Alert.Heading>
          <p className='mb-0'>This is a linked line item and must be managed via the parent line item</p>
        </Alert>
      );
    } else if(imageType === 'artfile') {
      return (
        <Alert variant='info' className='mb-0 text-center'>
          <Alert.Heading>Warning</Alert.Heading>
          <p className='mb-0'>This is a linked line item. Once you upload to the parent line item the image will display here as well</p>
        </Alert>
      );
    } else {
      return null;
    }
  }
  
if (imageType === 'printfile' && workObject.workObject.status === 3) {
    return (
      <Alert variant='warning' className='mb-0 text-center'>
        <Alert.Heading>Warning</Alert.Heading>
        <p className='mb-0'>Artfile is pending please upload artfile</p>
      </Alert>
    );
  } else if (imageType === 'printfile' && workObject.workObject.status === 9003) {
    return (
      <Alert variant='warning' className='mb-0 text-center'>
        <Alert.Heading>Warning!</Alert.Heading>
        <p className='mb-0'>Customer photo failure. Please resolve error first</p>
      </Alert>
    );
  }

  if (imageType === 'artfile' && workObject.workObject.status === 4) {
    return (
      <Alert variant='warning' className='mb-0 text-center'>
        <Alert.Heading>Warning</Alert.Heading>
        <p className='mb-0'>Please return this order. Once returned you may edit the artfile</p>
      </Alert>
    );
  } else if (imageType === 'artfile' && workObject.workObject.status === 9003) {
    return (
      <Alert variant='warning' className='mb-0 text-center'>
        <Alert.Heading>Warning</Alert.Heading>
        <p className='mb-0'>Customer photo failure. Please resolve error first</p>
      </Alert>
    );
  } 
  console.log(workObject)
  return (
    <>
      {showUpload && (
        <section>
          <h6 className="text-center bg-brand rounded-top p-1 mb-0 text-white">{isReplacing ? 'Replace the existing file(s)' : `Upload a new ${imageType}`}</h6>
          <div
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
            className={
              isOver ? 'display-flex justify-content-center align-items-center uploadBoxDragged rounded-bottom shadow position-relative p-2 bg-brand-light text-white text-center' : 'display-flex justify-content-center align-items-center uploadBox rounded-bottom shadow-sm position-relative p-2 text-muted text-center'
            }
          > 
          {imageType === 'artfile' && (
            <p className='p-2 rounded fw-bold mb-0 alert alert-warning'>File Name: {workObject.workObject.metadata.orderName.replace('#', '')}-{lineItemIndex + 1}_{cxPhotoIndex}.{imageType === 'artfile' ? config.artfileTypes[0].toLowerCase() : fileTypes.join(', ').toLowerCase()}</p>
          )}
          {imageType === 'printfile' && (
            <p className='p-2 rounded fw-bold mb-0 alert alert-warning'>File Name: {workObject.workObject.metadata.orderName.replace('#', '')}-{lineItemIndex + 1}.{imageType === 'artfile' ? config.artfileTypes[0].toLowerCase() : fileTypes.join(', ').toLowerCase()}</p>
          )}
            Drag and drop some files here, or
            <button className={
              isOver ? 'ms-2 btn btn-sm btn-light my-1' : 'ms-2 btn btn-sm btn-outline-secondary my-1'
            } onClick={openFilePicker}>
              click here to select files
            </button>
            <br />
            <em>(Only {imageType === 'artfile' ? config.artfileTypes[0].toUpperCase() : fileTypes.join(', ').toUpperCase()} files will be accepted)</em>
          </div>
          <input
            ref={fileInputRef}
            type="file"
            style={{ display: 'none' }}
            accept={fileAcceptTypes.map(type => `.${type}`).join(',')}
            multiple
            onChange={handleFileSelect}
          />
          {isUploading ? (
            <div className='mt-3'>
              <LoadingComponent message="Uploading..." />
            </div>
          ) : null}
        </section>
      )}
    </>
  );
}