import React, { useContext } from 'react';
import { Badge } from 'react-bootstrap';
import ConfigContext from '../../services/config.context'; 

export default function StatusBadgeComponent({status}) {
  const { config } = useContext(ConfigContext);
  const currentStatus = config.statusMap[status.toString()] || { text: 'Unknown Status', variant: 'bg-dark' };

  return (
    <Badge className={currentStatus.variant}>{currentStatus.text}</Badge>
  );
}
