import { createContext, useContext, useState, useCallback, useEffect } from "react";
import jwtDecode from "jwt-decode";
import axios from "axios";
import CopyrightComponent from "../components/copyright.component";
import NavComponent from "../components/nav.component";
import ConfigContext from "./config.context";
import { Card, Container } from "react-bootstrap";
import authHeader from "./auth.header";
import Welcome from "../components/welcome.component";
import OrderService from "./order.service";
const AuthContext = createContext();

export const AuthContextProvider = ({ children }) => {

    const { config } = useContext(ConfigContext);
    const baseUrl = `${config?.baseUrl}`;
    const userUrl = `${config?.authUrl}/v1/user`;
    const authRefreshTokenUrl = `${config?.authUrl}/v1/Authentication/refresh`;
    const [ totalAssigned, setTotalAssigned ] = useState(0);

    const [ orderService, setOrderService ] = useState(null);
    const [ details, setDetails ] = useState(() => {
        if (localStorage.getItem("userDetails")) {
            const details = JSON.parse(localStorage.getItem("userDetails"));

            return details;
        }

        return null;
    });

    const [originalAdminSession, setOriginalAdminSession] = useState(() => {
        if (localStorage.getItem("adminSession")) {
            return JSON.parse(localStorage.getItem("adminSession"));
        }
        return null;
    });

    const [ user, setUser ] = useState(() => {
        if (localStorage.getItem("accessToken")) {
            const accessToken = localStorage.getItem('accessToken');
            const details = jwtDecode(accessToken);
            const expiresOn = details.exp * 1000;
            const now = Date.now();

            if (now > expiresOn) {
                return null;
            }
            return details;
        }

        return null;
    });

    const revertToAdminSession = () => {
        const adminSession = JSON.parse(localStorage.getItem("adminSession"));
        if (adminSession) {
            localStorage.setItem("accessToken", adminSession.accessToken);
            localStorage.setItem("refreshToken", adminSession.refreshToken);
            localStorage.setItem("userDetails", JSON.stringify(adminSession.details));
            setUser(adminSession.user);
            setDetails(adminSession.details);
            localStorage.removeItem("adminSession");
            setOriginalAdminSession(null);
        }
    };

    const logout = useCallback(async () => {
        localStorage.removeItem("accessToken");
        localStorage.removeItem("userDetails");
        localStorage.removeItem("refreshToken");
        window.location.href = `${baseUrl}/idm/login`;
    }, [baseUrl]);


    useEffect(() => {
        if (!user) return;
        if (config !== null && orderService === null) {
            setOrderService(new OrderService(config.baseUrl));
        }

        const fetchAssignedOrders = () => {
            if (orderService !== null) {
                orderService.getAssignedOrders(user.id, 1, 0, null)
                    .then((res) => {
                        setTotalAssigned(res.data.totalItems);
                    });
            }
        };

        //should these be in an auth service?
        const refreshToken = async () => {
            const existingRefreshToken = localStorage.getItem('refreshToken');
            if (!existingRefreshToken) return
    
            try {
                const { data } = await axios.post(`${authRefreshTokenUrl}/${existingRefreshToken}`, null, authHeader());
                localStorage.setItem("accessToken", data.accessToken);
                localStorage.setItem("refreshToken", data.refreshToken);
                localStorage.setItem("userDetails", JSON.stringify(data.user));
            } catch (error) {
                logout();
            }
        };

        const checkTokenExpiration = () => {
            const accessToken = localStorage.getItem('accessToken');
            if (!accessToken) return;

            const decodedToken = jwtDecode(accessToken);
            const expiresOn = decodedToken.exp * 1000;
            const now = Date.now();
            const minutesToExpire = (expiresOn - now) / (1000 * 60);

            if (minutesToExpire < 5) { // Refresh token 5 minutes before it expires
                refreshToken();
            }
        };

        const tokenIntervalId = setInterval(() => {
            checkTokenExpiration();
        }, 30000); // Check every 30 seconds

        // Fetch assigned orders the first time the component is loaded
        if (orderService !== null && totalAssigned === 0) {
            fetchAssignedOrders();
        }

        const intervalId = setInterval(() => {
            fetchAssignedOrders();
        }, 300000); // Check every 5 minute

        // Remove intervals on component unmount
        return () => {
            clearInterval(intervalId);
            clearInterval(tokenIntervalId);
        };
    }, [config, orderService, user, totalAssigned, logout, authRefreshTokenUrl]);

    const hasPermission = (permission) => {
        if(!user) return false;
        if (!user.role) return false;

        return user.role.indexOf(permission) > -1;
    }

    const getUser = async (id) => {
        return await axios.get(`${userUrl}/${id}`, authHeader());
    }

    return (
        <AuthContext.Provider value={{ 
            user, 
            details, 
            logout, 
            baseUrl, 
            hasPermission, 
            getUser, 
            totalAssigned, 
            setTotalAssigned,
            revertToAdminSession,
            originalAdminSession
            }}>
            {user ? (
                <>
                    <NavComponent />
                    <Container fluid className="pt-5 mt-4">
                        <Welcome user={user} totalAssigned={totalAssigned} />
                        {children}
                    </Container>
                    <CopyrightComponent />
                </>
            ) : (
                <>
                    <Container fluid className="login d-flex justify-content-center align-items-center min-vh-100">
                        <Card className="col-12 col-lg-6 shadow mx-auto p-3">
                            <Card.Body className="text-center">
                                {children}
                            </Card.Body>
                            <CopyrightComponent />
                        </Card>
                    </Container>
                </>
            )}
        </AuthContext.Provider>
    );
};

export default AuthContext;