import { useContext } from 'react';
import CacheContext from '../../services/cache.context';
import ToastAlert from './toast.alert.component';

const ToastContainer = () => {
  const { toasts, hideToastNotification } = useContext(CacheContext);

  return (
    <div className="toast-container position-fixed bottom-0 end-0 p-3">
      {toasts.map((toast) => (
        <div key={toast.id} className="toast-box">
          <ToastAlert
            show={true}
            onClose={() => hideToastNotification(toast.id)}
            message={toast.message}
            variant={toast.variant}
          />
        </div>
      ))}
    </div>
  );
};

export default ToastContainer;
