import React, { useState, useRef, useContext, useEffect } from 'react';
import CacheContext from '../../services/cache.context';
import AuthContext from '../../services/auth.context';
import { Modal, Button, ToggleButton, ToggleButtonGroup, Badge, ProgressBar } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloudUpload } from '@fortawesome/free-solid-svg-icons';
import LoadingComponent from '../shared/loading.component';

export default function BulkUploadComponent() {
    const { attachmentService } = useContext(CacheContext);
    const { hasPermission } = useContext(AuthContext);
    const [ showModal, setShowModal ] = useState(false);
    const [ uploadedFiles, setUploadedFiles ] = useState([]);
    const [ uploadStatus, setUploadStatus ] = useState({});
    const [ isOver, setIsOver ] = useState(false);
    const [ isUploadComplete, setIsUploadComplete ] = useState(false);
    const [ isUploading, setIsUploading ] = useState(false);
    const [ totalFiles, setTotalFiles ] = useState(0);
    const [ totalFailed, setTotalFailed ] = useState(0);
    const [ totalSucceeded, setTotalSucceeded ] = useState(0);
    const [ startTime, setStartTime ] = useState(null);
    const [ elapsedTime, setElapsedTime ] = useState('');

    const isArtFileDesigner = hasPermission('image_artfile:write');
    const isPrintFileDesigner = hasPermission('image_printfile:write');
    const initialFileType = isArtFileDesigner ? 'artfile' : (isPrintFileDesigner ? 'printfile' : null);
    const [fileType, setFileType] = useState(initialFileType);
    const inputFileRef = useRef();

    const handleOpenModal = () => setShowModal(true);
    const handleCloseModal = () => {
        setUploadedFiles([]);
        setUploadStatus({});
        setShowModal(false);
        setIsUploadComplete(false);
        setStartTime(null);
        setElapsedTime('');
    };
    const openFilePicker = () => {
        inputFileRef.current.click();
    };
    const handleDragOver = (e) => {
        e.preventDefault();
        setIsOver(true);
    };
    const handleDragLeave = () => {
        setIsOver(false);
    };

    const traverseFileTree = async (item, path = "") => {
        return new Promise(resolve => {
            if (item.isFile) {
                item.file(file => {
                    file.fullPath = path + file.name;
                    resolve(file);
                });
            } else if (item.isDirectory) {
                const dirReader = item.createReader();
                dirReader.readEntries(async entries => {
                    const files = [];
                    for (const entry of entries) {
                        const fullPath = `${path}${entry.name}/`;
                        const file = await traverseFileTree(entry, fullPath);
                        files.push(file);
                    }
                    resolve(files.flat());
                });
            }
        });
    };

    const handleDrop = async (e) => {
        e.preventDefault();
        setIsOver(false);
        const items = e.dataTransfer.items;
        let allEntries = [];

        for (const item of items) {
            const entry = item.webkitGetAsEntry ? item.webkitGetAsEntry() : null;
            if (entry) {
                allEntries.push(entry);
            } else if (item.kind === 'file') {
                allEntries.push(item.getAsFile());
            }
        }

        let files = [];
        for (const entry of allEntries) {
            if (entry.isFile || entry instanceof File) {
                if (entry instanceof File) {
                    files.push(entry);
                } else {
                    const promise = new Promise((resolve) => {
                        entry.file((file) => {
                            resolve(file);
                        });
                    });
                    const file = await promise;
                    files.push(file);
                }
            } else if (entry.isDirectory) {
                const folderFiles = await traverseFileTree(entry);
                files = [...files, ...folderFiles];
            }
        }

        files = files.filter(
            file => file.type === "image/jpeg" || file.type === "image/png"
        );

        handleUploadFiles(files);
    };

    const handleUploadFiles = (files) => {
        const uploaded = [...uploadedFiles, ...files];
        setUploadedFiles(uploaded);
        setTotalFiles(uploaded.length);
    };

    const exportFileList = () => {
        const now = new Date();
        const formattedDate = `${now.getMonth() + 1}-${now.getDate()}-${now.getFullYear()}-${now.getHours()}-${now.getMinutes()}-bulk-upload-report.csv`;
        const csvContent = 'data:text/csv;charset=utf-8,'
            + 'File Name,Status,Error Message,Timestamp\n'
            + Object.keys(uploadStatus).map(key =>
                `${key},${uploadStatus[key].status},${uploadStatus[key].message || ''},${uploadStatus[key].timestamp || ''}`
            ).join('\n');
        const encodedUri = encodeURI(csvContent);
        const link = document.createElement('a');
        link.setAttribute('href', encodedUri);
        link.setAttribute('download', formattedDate);
        link.click();
    };

    useEffect(() => {
        let timer;
        if (isUploading && startTime) {
            timer = setInterval(() => {
                const now = new Date();
                const diffInSeconds = Math.floor((now - startTime) / 1000);
                const minutes = Math.floor(diffInSeconds / 60);
                const seconds = diffInSeconds % 60;
                setElapsedTime(`${minutes ? `${minutes} minutes` : ''} ${seconds} seconds`);
            }, 1000);
        } else if (!isUploading && startTime) {
            clearInterval(timer);
        }
        return () => {
            if (timer) {
                clearInterval(timer);
            }
        };
    }, [isUploading, startTime]);

    const handleConfirmedUploads = async () => {
        setIsUploading(true);
        setStartTime(new Date());
        for (const file of uploadedFiles) {
            const timestamp = new Date();
            const formattedTime = `${timestamp.getMonth() + 1}/${timestamp.getDate()}/${timestamp.getFullYear()} - ${timestamp.getHours() % 12 || 12}:${String(timestamp.getMinutes()).padStart(2, '0')} ${timestamp.getHours() >= 12 ? 'PM' : 'AM'} EST`;
            try {
                await attachmentService.bulkUploadAttachment(file, fileType);
                setUploadStatus(prev => ({
                    ...prev,
                    [file.name]: {
                        status: 'success',
                        message: '',
                        timestamp: formattedTime
                    }
                }));
                setTotalSucceeded(prev => prev + 1);
            } catch (error) {
                const errorMessage = error?.response?.data?.Message ?? 'Unknown error';
                setUploadStatus(prev => ({
                    ...prev,
                    [file.name]: {
                        status: 'failure',
                        message: errorMessage,
                        timestamp: formattedTime
                    }
                }));
                setTotalFailed(prev => prev + 1);
            }
        }
        setIsUploadComplete(true);
        setIsUploading(false);
    };
    const progress = totalFiles ? (totalSucceeded / totalFiles) * 100 : 0;

    return (
        <>
            {(isArtFileDesigner || isPrintFileDesigner) && (
                <>
                    <Button
                        className='rounded-circle bulk-upload-button btn-brand shadow '
                        onClick={handleOpenModal}>
                        <FontAwesomeIcon icon={faCloudUpload} />
                    </Button>
                    <Modal show={showModal} onHide={handleCloseModal} size='xl'>
                        <Modal.Header closeButton>
                            <Modal.Title>Bulk Upload</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <div className="text-center mb-3">
                                <ToggleButtonGroup
                                    type="radio"
                                    name="fileType"
                                    size='sm'
                                    defaultValue={fileType}
                                    onChange={(val) => setFileType(val)}
                                >
                                    {isArtFileDesigner && (
                                        <ToggleButton
                                            id="tbg-artfile"
                                            value='artfile'
                                            variant={fileType === 'artfile' ? 'dark' : 'light'}
                                        >
                                            Art File
                                        </ToggleButton>
                                    )}
                                    {isPrintFileDesigner && (
                                        <ToggleButton
                                            id="tbg-printfile"
                                            value='printfile'
                                            variant={fileType === 'printfile' ? 'dark' : 'light'}
                                        >
                                            Print File
                                        </ToggleButton>
                                    )}
                                </ToggleButtonGroup>
                                <div className='mt-2'>
                                    You're uploading <Badge className="bg-secondary">{fileType}</Badge>
                                    <hr />
                                    <Badge className="bg-dark ms-2">Total Files: {totalFiles}</Badge> |
                                    <Badge className="bg-danger ms-2">Failed: {totalFailed}</Badge> |
                                    <Badge className="bg-success ms-2">Succeeded: {totalSucceeded}</Badge>
                                </div>
                                <ProgressBar variant="success" now={progress} label={`${Math.round(progress)}%`} className="mt-3" />
                                <div className="mt-2">{elapsedTime && `Elapsed Time: ${elapsedTime}`}</div>
                            </div>
                            <div className={`bulkuploader rounded ${isOver ? 'over' : ''}`}
                                onDragOver={handleDragOver}
                                onDragLeave={handleDragLeave}
                                onDrop={handleDrop}>
                                {isOver ? 'Release to drop files' : 'Drag & Drop Files Here'}
                            </div>
                            <div className="text-center mt-2">
                                <p className='mb-0'>or</p>
                                <Button variant="outline-dark" className="mt-2" onClick={openFilePicker}>
                                    Select Files
                                </Button>
                            </div>
                            <input ref={inputFileRef} id='fileUpload' type='file' multiple
                                accept='image/jpeg, image/png'
                                onChange={(e) => handleUploadFiles(Array.from(e.target.files))}
                                style={{ display: 'none' }}
                            />
                            {uploadedFiles.length > 0 && (
                                <div className="uploaded-files-list mt-2 text-center bg-light border border-secondary rounded pb-0 shadow-sm"
                                    style={{ maxHeight: '400px', overflowY: 'auto' }}>
                                    <p className='mt-3 mb-0'>Files to be uploaded:</p>
                                    <hr />
                                    {uploadedFiles.map(file => (
                                        <div key={file.name}>
                                            {file.name} -
                                            <Badge className={`ms-1 ${uploadStatus[file.name]?.status === 'success' ? 'bg-success' : uploadStatus[file.name]?.status === 'failure' ? 'bg-danger' : 'bg-warning'}`}>
                                                {uploadStatus[file.name]?.status || 'pending'}
                                            </Badge>
                                            {uploadStatus[file.name]?.timestamp && (
                                                <span className="text-muted ms-2">({uploadStatus[file.name]?.timestamp})</span>
                                            )}
                                            {uploadStatus[file.name]?.status === 'failure' && (
                                                <span className="text-danger ms-2">({uploadStatus[file.name]?.message})</span>
                                            )}
                                        </div>
                                    ))}
                                    <div className='sticky-bottom text-center mt-3 mb-0 w-100'>
                                        {isUploadComplete ? (
                                            <Button variant="dark" className="shadow-sm w-100 rounded-0" onClick={exportFileList}>
                                                Export List
                                            </Button>
                                        ) : (
                                            <Button variant="dark" className="shadow-sm w-100 rounded-0" onClick={handleConfirmedUploads}>
                                                {isUploading ? (
                                                        <LoadingComponent variant='light' />
                                                ) : (
                                                    <>
                                                        <FontAwesomeIcon icon={faCloudUpload} /> Confirm and Upload
                                                    </>
                                                )}
                                            </Button>
                                        )}
                                    </div>
                                </div>
                            )}
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" className="shadow" onClick={handleCloseModal}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </>
            )}
        </>
    );
}
