import { useContext, useEffect, useState } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWarning } from '@fortawesome/free-solid-svg-icons';
import AuthContext from '../services/auth.context';
import ConfigContext from "../services/config.context";
export default function Unauthorized() {
    const { user } = useContext(AuthContext);
    const { config } = useContext(ConfigContext);
    const [ isLoading, setIsLoading ] = useState(true);

    useEffect(() => {
        if (config) {
            setIsLoading(false);
        }
    }, [config]);

    if (isLoading) {
        return (
            <>
                <h1 className='text-center'>Not Authorized</h1>
                <FontAwesomeIcon icon={faWarning} className="text-warning" size="6x" />
                <h5 className='text-center'>You're not authorized to view this page</h5>
                <h5 className='text-center'>Click <a href="/">here</a> to return.</h5>
            </>
        );
    }
    return (
        <>
            {user !== null ? (
                <>
                    <h1 className='text-center'>Not Authorized</h1>
                    <FontAwesomeIcon icon={faWarning} className="text-warning" size="6x" />
                    <h5 className='text-center'>You're not authorized to view this page</h5>
                    <h5 className='text-center'>Click <a href="/">here</a> to return.</h5>
                </>
            ) : (
                <>
                    <h1 className='text-center'>Not Logged In</h1>
                    <FontAwesomeIcon icon={faWarning} className="text-warning" size="6x" />
                    <h5 className='text-center'>You're not logged in</h5>
                    <h5 className='text-center'>Login <a href={`${config.baseUrl}/idm/login`}>here</a></h5>
                </>
            )}
            
        </>
    );
}
