import { useContext, useEffect, useState } from "react"
import { ChartComponent } from "../shared/chart.component";
import CacheContext from "../../services/cache.context";
import { Row } from "react-bootstrap";
import DashboardOrderTableComponent from "./dashboard.table.component";
import LoadingComponent from "../shared/loading.component";
export default function DashboardHomeComponent() {
    const { dashboardService } = useContext(CacheContext);
    const [ charts, setCharts ] = useState([ ]);
    const [ isLoading, setIsLoading ] = useState(true);

    useEffect(() => {
        setIsLoading(true);
        if (dashboardService !== undefined) {
            dashboardService.getCharts()
            .then((res) => {
                setCharts(res.data);
                console.log(res.data);
            }, (err) => {
                console.log(err);
            }).finally(() => {
                setIsLoading(false);
            });
        }
    }, [ dashboardService ])

    if (isLoading) {
        return (
            <LoadingComponent size="lg" variant="dark"/>
        );
    }

    return (
        <div>
            {charts?.map((row, rowIndex) => (
                <Row key={rowIndex}>
                    {row.charts.map((chart, chartIndex) => (
                        <ChartComponent key={chartIndex} chart={chart} />
                        ))}
                </Row>
            ))}

            <DashboardOrderTableComponent />
        </div>
    );
}