import React, { useState, useEffect, useContext, useCallback } from 'react';
import CacheContext from '../../services/cache.context'
import { Modal, Button, Form } from 'react-bootstrap';
import Select from 'react-select';

export default function ShowAssignModalComponent({ show, onHide, orderService, workObject, fetchWorkObject, setAlert, setShowAssignModal}){
  const [ users, setUsers] = useState([]);
  const [ selectedUser, setSelectedUser ] = useState(null);
  const { distributionService, getUserById } = useContext(CacheContext);
  const [ orderType, setOrderType ] = useState('artfile');

  const loadUsers = useCallback(() => {
    distributionService.getUsersCapacity(orderType)
      .then(capacityData => {
        const options = [];
        const processUser = (index) => {
          if (index >= capacityData.data.length) {
            setUsers(options);
            return;
          }
  
          const userId = capacityData.data[index].userId;
          getUserById(userId)
            .then(user => {
              options.push({ value: userId, label: user });
              processUser(index + 1);
            })
            .catch(error => {
              console.error('Error processing user:', error);
              processUser(index + 1);
            });
        };
  
        processUser(0);
      })
      .catch(error => console.error('Error fetching users:', error));
  }, [distributionService, getUserById, orderType]);
  

  useEffect(() => {
    loadUsers();
  }, [loadUsers]);
  
  const assignOrder = () => {
    orderService.bulkAssign([workObject.workObject.shopifyOrderId], selectedUser.value)
      .then(res => {
        setAlert({ show: true, message: 'Order assigned successfully', variant: 'success' });
        setShowAssignModal(false);
        fetchWorkObject();
      })
      .catch(err => {
        setAlert({ show: true, message: 'Failed to assign order', variant: 'danger' });
      });
  };

  const handleOrderTypeChange = (type) => {
    setOrderType(type);
  };

  return (
    <>
    {users && (
    <Modal show={show} onHide={onHide}>
      <Modal.Header closeButton>
        <Modal.Title>Assign Order</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center">
          <Button className="btn-sm rounded-start rounded-0" variant={orderType === 'artfile' ? 'dark' : 'light'} onClick={() => handleOrderTypeChange('artfile')}>
            Art File
          </Button>
          <Button className="btn-sm rounded-end rounded-0" variant={orderType === 'printfile' ? 'dark' : 'light'} onClick={() => handleOrderTypeChange('printfile')}>
            Print File
          </Button>
        </div>
        <Form className="mt-3">
          <Form.Group className="mb-3">
            <Form.Label>Assign To</Form.Label>
            <Select options={users} value={selectedUser} onChange={setSelectedUser} placeholder="Select User" />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>
          Close
        </Button>
        <Button variant="dark" onClick={assignOrder}>
          Assign Order
        </Button>
      </Modal.Footer>
    </Modal>
    )}
    </>
  );
};
