import { useContext, useEffect, useState } from "react";
import CacheContext from "../../services/cache.context";
import AuthContext from "../../services/auth.context";
import { Button, Badge } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faEdit, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { faShopify } from "@fortawesome/free-brands-svg-icons";
import ConfigContext from "../../services/config.context";
import StatusBadgeComponent from "../shared/status.component";
import SensariaStatusBadgeComponent from "../shared/sensariaStatus.component";
import { convertToEasternTime } from "../../utils/convertToEasternTime";

export default function OrderTableRowComponent({ order, onPopOverOpen, onCheckboxChange, index, selectedOrders, selectedOrderNames }) {
  const { hasPermission } = useContext(AuthContext);
  const { config } = useContext(ConfigContext);
  const { getUserList } = useContext(CacheContext);
  const [ assignedTo, setAssignedTo ] = useState("");

  const isSelected = selectedOrders.some(selectedOrder => selectedOrder.id === order.shopifyOrderId);

  const handleBadgeClick = (event) => {
    onCheckboxChange(event, !isSelected, order.shopifyOrderId, order.orderName, order.id, order.status, index);
  };  

  useEffect(() => {
    Promise.all([
      getUserList()
    ]).then(([userList]) => {
      const assignedUser = userList.find(user => user.id === order.assignedTo);
      if (assignedUser !== undefined) {
        const { firstName, lastName } = assignedUser.metadata;
        setAssignedTo(`${firstName} ${lastName}`);
      } else {
        setAssignedTo(order.assignedTo);
      }
    });
  }, [order, getUserList]);   

  const style1 = {
    maxWidth: '2rem',
    overflow: 'hidden',
  };

  return (
    <tr className={isSelected ? 'table-active' : ''}>
      <td style={style1}>
        <Badge
          pill
          bg={isSelected ? 'dark text-white' : 'secondary'}
          onClick={handleBadgeClick}
          style={{ cursor: 'pointer' }}
        >
          {order.orderName} {isSelected ? <FontAwesomeIcon icon={faCheckCircle} /> : null}
        </Badge>
      </td>
      <td style={style1}>{convertToEasternTime(order.workObjectCreatedOn)}</td>
      <td style={style1}><StatusBadgeComponent status={order.status} /></td>
      <td style={style1}>
      {order.metadata?.fulfillmentTracker?.LineItems?.map((lineItem, index) => (
        <SensariaStatusBadgeComponent status={lineItem.Status} key={index} />
      ))}
      </td>
      <td className="td-tags">
      {order.metadata?.lineItems?.map((lineItem, index) => (
        <Badge key={`${order.id}-${order.shopifyOrderId}-${order.orderNumber}-${lineItem.id}-lineItemSkuTag-${index}`} pill bg="light border text-dark" className="me-1" size="sm">
          {lineItem?.LineItem?.Name}
        </Badge>
      ))}
      {order.tags.map((tag, index) => (
        <Badge key={`${order.orderId}-${order.shopifyOrderId}-${order.orderNumber}-orderTag-${index}`} pill bg="light border text-dark" className="me-1" size="sm">
          {tag}
        </Badge>
      ))}
      </td>
      <td style={style1}>
        <Badge pill bg="dark">{assignedTo}</Badge>
      </td>
      <td className="text-end">
        <Button
          onClick={(event) => onPopOverOpen(event, order.shopifyOrderId)}
          variant="secondary"
          className="me-1"
          size="sm"
        >
          <FontAwesomeIcon icon={faEye} />
        </Button>
        <Link to={`/details/${order.shopifyOrderId}`}>
          <Button variant="" className="me-1 btn-brand" size="sm">
            <FontAwesomeIcon icon={faEdit} />
          </Button>
        </Link>
        {hasPermission('user:write') ? (
          <Button href={`${config.shopifyUrl}/admin/orders/${order.shopifyOrderId}`} rel="noopener noreferrer" target="_blank" variant="success" className="me-1 bg-shopify" size="sm">
            <FontAwesomeIcon icon={faShopify} />
          </Button>
        ) : null}
      </td>
    </tr>
  );
}
