import React from "react";
import { Pagination, Form } from "react-bootstrap";

export default function PaginationComponent({ page, totalItems, pageSize, setPage, setPageSize }) {
    const totalPages = Math.ceil(totalItems / pageSize);

    const startPage = Math.max(1, page - 2);
    const endPage = Math.min(totalPages, page + 3);

    const pages = [];
    for (let number = startPage; number <= endPage; number++) {
        pages.push(
            <Pagination.Item key={number} active={number === page} onClick={() => setPage(number)}>
                {number}
            </Pagination.Item>
        );
    }

    return (
        <div className="text-center">
            <div className="d-flex justify-content-center">
                <Pagination className="custom-pagination">
                    <Pagination.First onClick={() => setPage(1)} disabled={page === 1} />
                    <Pagination.Prev onClick={() => setPage(page - 1)} disabled={page === 1} />
                    {startPage > 1 && <Pagination.Ellipsis />}
                    {pages}
                    {endPage < totalPages && <Pagination.Ellipsis />}
                    <Pagination.Next onClick={() => setPage(page + 1)} disabled={page === totalPages} />
                    <Pagination.Last onClick={() => setPage(totalPages)} disabled={page === totalPages} />
                </Pagination>
                <Form.Group className="d-inline ms-2">
                    <Form.Select
                        value={pageSize}
                        onChange={(e) => setPageSize(Number(e.target.value))}
                    >
                        <option value={10}>10</option>
                        <option value={20}>20</option>
                        <option value={30}>30</option>
                        <option value={40}>40</option>
                        <option value={50}>50</option>
                    </Form.Select>
                </Form.Group>
            </div>
            <span className="ml-3">{`Showing page ${page} of ${totalPages} for ${totalItems} results`}</span>
        </div>
    );
}
