import authHeader from "./auth.header";
import axios from "axios";
export default class OrderService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    getDetails(orderId) {
        return axios.get(`${this.baseUrl}/portal/api/v1/workobject/order/${orderId}/details`, authHeader());
    }

    async getAssignedOrders(userID, pageNumber = 1, pageSize = 10, searchParams = {}) {
        let payload = {
            page: pageNumber,
            pageSize: pageSize,
            assignedTo: [userID],
            ...searchParams
        };
        return axios.post(`${this.baseUrl}/portal/api/v1/WorkObject/search`, payload, authHeader());
    }

    uploadAttachment(file, workObjectId, imageType, lineItemId) {
        let data = new FormData();
        data.append('file', file);

        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `${this.baseUrl}/portal/api/v1/Attachment/upload/${workObjectId}/${imageType}/${lineItemId}`,
            headers: {
                'Content-Type': 'multipart/form-data',
                ...authHeader().headers
            },
            data: data
        };

        return axios.request(config);
    }

    bulkAssign(orderIds, userId) {
        return axios.post(`${this.baseUrl}/portal/api/v1/workobject/assign/bulk`, {
            userId: userId,
            shopifyOrderIds: orderIds,
        }, authHeader());
    }

    manuallyAssign(workObjectId, ArtistId, PrintFileDesignerId) {
        return axios.patch(`${this.baseUrl}/portal/api/v1/workobject/${workObjectId}/assign/override`, {
            ArtistId: ArtistId,
            PrintFileDesignerId: PrintFileDesignerId
        }, authHeader());
    }

    returnOrder(workObjectId, status, comment, resetAttachments) {
        return axios.patch(`${this.baseUrl}/portal/api/v1/WorkObject/${workObjectId}/return/${status}` , {
            "comment": comment,
            "resetAttachments": resetAttachments
        }, authHeader());
    }

    cancelOrder(workObjectId) {
        let config = {
            method: 'patch',
            maxBodyLength: Infinity,
            url: `${this.baseUrl}/portal/api/v1/WorkObject/${workObjectId}/status/8000`,
            headers: { 
              'accept': 'text/plain', 
              ...authHeader().headers
            },
        }

        return axios.request(config);
    }

    updateShippingAddress(orderId, shippingAddress) {
        return axios.patch(`${this.baseUrl}/portal/api/v1/order/${orderId}/address`, shippingAddress, authHeader());
    }

    updateLineItem(orderId, lineItemId, qauntity, sku, shippingMethod) {
        return axios.patch(`${this.baseUrl}/portal/api/v1/order/${orderId}/lineitem/${lineItemId}`, {
            quantity: parseInt(qauntity),
            sku: sku,
            shippingMethod: parseInt(shippingMethod)
        }, authHeader());
    }

    addTag(workObjectId, tag) {
        let config = {
            method: 'patch',
            url: `${this.baseUrl}/portal/api/v1/WorkObject/${workObjectId}/tag/${tag}`,
            headers: { 
              'accept': 'text/plain', 
              ...authHeader().headers
            },
        }
        return axios.request(config);
    }
    
    deleteTag(workObjectId, tag) {
        return axios.delete(`${this.baseUrl}/portal/api/v1/WorkObject/${workObjectId}/tag/${tag}`, authHeader());
    }

    forceTransition(workObjectId) {
        console.log(workObjectId)
        let config = {
            method: 'patch',
            url: `${this.baseUrl}/portal/api/v1/WorkObject/${workObjectId}/push`,
            headers: { 
              'accept': 'text/plain', 
              ...authHeader().headers
            },
        }
        return axios.request(config);
    }
}
