import React from 'react';
import { Modal, Button, Form } from 'react-bootstrap';

export default function CreateSkuModal({ show, handleClose, newSku, setNewSku, createSku, productshippingprofiles, errorMessage }) {
    const handleChange = (e) => {
        const { name, value, type, checked } = e.target;
        if (name === "handle") {
            setNewSku(prevState => ({
                ...prevState,
                [name]: value.replace(/\s/g, '-')
            }));
        } else if (type === 'number') {
            setNewSku(prevState => ({
                ...prevState,
                [name]: parseInt(value) || 0
            }));
        } else if (type === 'checkbox') {
            setNewSku(prevState => ({
                ...prevState,
                [name]: checked
            }));
        } else {
            setNewSku(prevState => ({
                ...prevState,
                [name]: value
            }));
        }
    };

    return (
        <Modal show={show} onHide={handleClose} size="lg">
            <Modal.Header closeButton>
                <Modal.Title>Create New SKU</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {errorMessage && <p className="text-danger">{errorMessage}</p>}
                <Form>
                        <Form.Group className="mb-3">
                            <Form.Label>Title</Form.Label>
                            <Form.Control
                                type="text"
                                name="title"
                                value={newSku.title}
                                onChange={handleChange}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Handle</Form.Label>
                            <Form.Control
                                type="text"
                                name="handle"
                                value={newSku.handle.replace(/\s/g, '-')}
                                onChange={handleChange}
                            />
                        </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 1</Form.Label>
                        <Form.Control
                            type="text"
                            name="option1"
                            value={newSku.option1}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 2</Form.Label>
                        <Form.Control
                            type="text"
                            name="option2"
                            value={newSku.option2}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Option 3</Form.Label>
                        <Form.Control
                            type="text"
                            name="option3"
                            value={newSku.option3}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Variant SKU</Form.Label>
                        <Form.Control
                            type="text"
                            name="variantSku"
                            value={newSku.variantSku}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Sensaria PID</Form.Label>
                        <Form.Control
                            type="number"
                            name="sensariaProductId"
                            value={newSku.sensariaProductId || ''}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    {productshippingprofiles && (
                        <Form.Group className="mb-3">
                            <Form.Label>Shipping Method</Form.Label>
                            <Form.Select
                                name="shippingMethod"
                                value={newSku.shippingMethod}
                                onChange={handleChange}>
                                    <option value={null}>NONE</option>
                                {productshippingprofiles.map(profile => (
                                    <option key={profile.id} value={profile.id}>
                                        {profile.method} ({profile.id})
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>
                    )}
                    <Form.Group className="mb-3">
                        <Form.Label>Artfile Needed</Form.Label>
                        <Form.Check
                            type="checkbox"
                            name="artfileNeeded"
                            checked={newSku.artfileNeeded}
                            onChange={handleChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Type</Form.Label>
                        <Form.Select
                            name="fileType"
                            value={newSku.fileType}
                            onChange={handleChange}>
                            <option value={null}>NONE</option>
                            <option value=".jpg">.jpg</option>
                            <option value=".png">.png</option>
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label># of CX Images</Form.Label>
                        <Form.Control
                            type="number"
                            name="numberOfCxImages"
                            value={newSku.numberOfCxImages || 0}
                            onChange={handleChange}
                        />
                    </Form.Group>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                {errorMessage && <p className="text-danger me-auto">{errorMessage}</p>}
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
                <Button className='btn-brand' onClick={createSku} disabled={!newSku.title || !newSku.handle || !newSku.variantSku || !newSku.option1}>
                    Submit
                </Button>
            </Modal.Footer>
        </Modal>
    );
}
