import React, { useContext, useEffect, useState } from "react";
import { Table, Button, Form, InputGroup, OverlayTrigger, Tooltip, Alert } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCirclePlus, faEdit, faTrash, faArrowCircleLeft, faExclamationCircle, faCircleCheck } from "@fortawesome/free-solid-svg-icons";
import { useLocation, useNavigate } from 'react-router-dom'; // Import useLocation and useNavigate from react-router-dom
import CacheContext from "../../services/cache.context";
import LoadingComponent from "../shared/loading.component";
import EditSkuModal from "./editskumodal.component";
import DeleteSkuModal from "./deleteskumodal.component";
import CreateSkuModal from "./createskumodal.component";


export default function SkuLookUpComponent() {
    const { lookupService, getProductShippingProfiles, productshippingprofiles } = useContext(CacheContext);
    const [skuList, setSkuList] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");
    const [filteredSkuList, setFilteredSkuList] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isError, setIsError] = useState(false);
    const [error, setError] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [deletionStatus, setDeletionStatus] = useState(null);
    const [deletionMessage, setDeletionMessage] = useState("");
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [selectedSku, setSelectedSku] = useState(null);
    const [newSku, setNewSku] = useState({
        title: "",
        handle: "",
        option1: "",
        option2: "",
        option3: "",
        variantSku: "",
        sensariaProductId: null,
        shippingMethod: null,
        artfileNeeded: false,
        fileType: null,
        numberOfCxImages: 0
    });
    const [editedSku, setEditedSku] = useState({
        title: "",
        handle: "",
        option1: "",
        option2: "",
        option3: "",
        sensariaProductId: null,
        shippingMethod: null,
        artfileNeeded: false,
        fileType: null,
        numberOfCxImages: 0
    });
    const [showOnlyMisconfigured, setShowOnlyMisconfigured] = useState(false); // State for showing only misconfigured SKUs

    const location = useLocation(); // Use useLocation to get the current location
    const navigate = useNavigate(); // Use useNavigate to navigate programmatically

    useEffect(() => {
        setIsLoading(true);
        lookupService.getProducts()
            .then((productsRes) => {
                setSkuList(productsRes.data);
                setFilteredSkuList(productsRes.data);
                setError("");

                const queryParams = new URLSearchParams(location.search);
                const editSku = queryParams.get('edit');
                const newSku = queryParams.get('new');
                if (editSku) {
                    const skuToEdit = productsRes.data.find(sku => sku.variantSku === editSku);
                    if (skuToEdit) {
                        openEditModal(skuToEdit);
                    }
                }
                if (newSku) {
                    // auto fill the variant sku field with the new sku
                    setNewSku(prevState => ({
                        ...prevState,
                        variantSku: newSku
                    }));
                    setShowCreateModal(true);
                }
            })
            .catch((err) => {
                setIsError(true);
                setError(err.message);
            })
            .finally(() => {
                setIsLoading(false);
            });

        getProductShippingProfiles().then((res) => {
            console.log(res);
        }).catch((err) => {
            console.log(err);
        });
    }, [lookupService, getProductShippingProfiles, location]);

    const renderTooltip = (props, text) => (
        <Tooltip id="button-tooltip" {...props}>
            {text}
        </Tooltip>
    );

    const handleSearch = (event) => {
        const query = event.target.value;
        setSearchQuery(query);
        const filtered = query
            ? skuList.filter(sku => sku.variantSku.toLowerCase().includes(query.toLowerCase()))
            : skuList;
        setFilteredSkuList(filtered);
    };

    const openEditModal = (sku) => {
        setSelectedSku(sku);
        setEditedSku({ ...sku });
        setShowEditModal(true);
    };

    const closeEditModal = () => {
        setSelectedSku(null);
        setShowEditModal(false);
    };

    const handleEditChange = (e) => {
        const { name, value, type, checked } = e.target;
        if (type === 'checkbox') {
            setEditedSku(prevState => ({
                ...prevState,
                [name]: checked
            }));
        } else if (type === 'number') {
            setEditedSku(prevState => ({
                ...prevState,
                [name]: parseInt(value) || 0
            }));
        } else {
            setEditedSku(prevState => ({
                ...prevState,
                [name]: value
            }));
        }
    };

    const openDeleteModal = (sku) => {
        setSelectedSku(sku);
        setShowDeleteModal(true);
    };

    const closeDeleteModal = () => {
        setSelectedSku(null);
        setShowDeleteModal(false);
        setDeletionStatus(null);
        setDeletionMessage("");
    };

    const openCreateModal = () => {
        setShowCreateModal(true);
    };

    const closeCreateModal = () => {
        setShowCreateModal(false);
    };

    const updateSku = () => {
        lookupService.updateProduct(selectedSku.variantSku, editedSku)
            .then(() => {
                const updatedSkus = skuList.map(sku =>
                    sku.variantSku === editedSku.variantSku ? editedSku : sku
                );
                setSkuList(updatedSkus);
                setFilteredSkuList(updatedSkus);
                closeEditModal();
                setErrorMessage("");
            })
            .catch((err) => {
                console.error("Error updating SKU:", err);
                setErrorMessage(err.response.data.Message);
            });
    };

    const deleteSku = () => {
        if (selectedSku) {
            lookupService.deleteProduct(selectedSku.variantSku)
                .then(() => {
                    setDeletionStatus('success');
                    setDeletionMessage(`SKU ${selectedSku.variantSku} deleted successfully.`);

                    const updatedSkuList = skuList.filter(sku => sku.variantSku !== selectedSku.variantSku);
                    setSkuList(updatedSkuList);

                    const updatedFilteredSkuList = filteredSkuList.filter(sku => sku.variantSku !== selectedSku.variantSku);
                    setFilteredSkuList(updatedFilteredSkuList);
                    setErrorMessage("");
                })
                .catch((err) => {
                    setDeletionStatus('error');
                    setErrorMessage(err.response.data.Message);
                    setDeletionMessage(`Failed to delete SKU: ${err.message}`);
                });
        }
    };

    const createSku = () => {
        lookupService.createProduct(newSku)
            .then(response => {
                const updatedSkuList = skuList.concat(response.data);
                setFilteredSkuList(updatedSkuList);
                //clear the form fields
                setNewSku({
                    title: "",
                    handle: "",
                    option1: "",
                    option2: "",
                    option3: "",
                    variantSku: "",
                    sensariaProductId: null,
                    shippingMethod: null,
                    artfileNeeded: false,
                    fileType: "",
                    numberOfCxImages: 0
                });
                closeCreateModal();
                setErrorMessage("");
            })
            .catch(err => {
                setErrorMessage(err.response.data.Message);
                console.log(err);
            });
    };

    const handleBackClick = () => {
        const state = location.state;
        if (state && state.from) {
            navigate(state.from);
        } else {
            navigate(-1); // fallback to browser back if state is not available
        }
    };

    const isFileTypeMisconfigured = (fileType) => {
            return ['.png', '.jpg', '.jpeg'].includes(fileType);
        };
    const misconfiguredSkusCount = filteredSkuList.filter(sku => {
        return (!sku.sensariaProductId || !sku.shippingMethod) && (sku.artfileNeeded ||  isFileTypeMisconfigured(sku.fileType) || sku.numberOfCxImages > 0);
    }).length;

    const handleFilterMisconfigured = () => {
        setShowOnlyMisconfigured(prevState => !prevState);
    };
    const displayList = showOnlyMisconfigured
        ? filteredSkuList.filter(sku => {
            return (!sku.sensariaProductId || !sku.shippingMethod) && (sku.artfileNeeded || isFileTypeMisconfigured(sku.fileType) || sku.numberOfCxImages > 0);
        })
        : filteredSkuList;

    if (isLoading || !productshippingprofiles) {
        return <LoadingComponent size="lg" message="Loading skus..." variant="dark" />;
    }

    if (isError) {
        return <div>Error: {error}</div>;
    }
        //function for button handleIgnoreSku that sets the shippingProfile to NONe, artfileNeeded false, fileType NONE and numberOfCxImages to 0
    const handleIgnoreSku = (sku) => {
        const ignoredSku = {
            ...sku,
            shippingMethod: 'None',
            artfileNeeded: false,
            fileType: 'None',
            numberOfCxImages: 0
        };
        lookupService.updateProduct(sku.variantSku, ignoredSku)
            .then(() => {
                const updatedSkus = skuList.map(sku =>
                    sku.variantSku === ignoredSku.variantSku ? ignoredSku : sku
                );
                setSkuList(updatedSkus);
                setFilteredSkuList(updatedSkus);
                setErrorMessage("");
                closeEditModal();
            })
            .catch((err) => {
                console.error("Error updating SKU:", err);
                setErrorMessage(err.response.data.Message);
            }
        );
    }
    return (
        <>
            {location.state?.from && (
                <Button className="btn-brand btn-sm mb-2" onClick={handleBackClick}>
                    <FontAwesomeIcon icon={faArrowCircleLeft} className="text-white me-2" />
                    Back
                </Button>
            )}
            <InputGroup className="mb-3 sku-sticky-top shadow-sm">
                <Form.Control
                    type="text"
                    placeholder="Start typing to search by SKU..."
                    value={searchQuery}
                    onChange={handleSearch}
                />
                <OverlayTrigger
                    placement="top"
                    overlay={props => renderTooltip(props, 'Create New SKU')}
                >
                    <Button variant="" onClick={openCreateModal} className="btn-brand">
                        <FontAwesomeIcon icon={faCirclePlus} className="text-white" />
                    </Button>
                </OverlayTrigger>
            </InputGroup>

            {misconfiguredSkusCount > 0 && (
                <Alert variant="warning">
                    Detected {misconfiguredSkusCount} SKU(s) that may be misconfigured.
                    <Button className="btn btn-brand btn-sm ms-2" onClick={handleFilterMisconfigured}>
                        {showOnlyMisconfigured ? 'Show All SKUs' : 'Show Misconfigured SKUs'}
                    </Button>
                </Alert>
            )}

            {displayList && (
                <Table bordered hover className="rounded-table">
                    <thead>
                        <tr>
                            <th>Info</th>
                            <th>Title</th>
                            {/* <th>Handle</th> */}
                            <th>Option 1</th>
                            <th>Option 2</th>
                            <th>Option 3</th>
                            <th>Variant SKU</th>
                            <th>Sensaria PID</th>
                            <th>Ship Method</th>
                            <th>Artfile Needed</th>
                            <th>Type</th>
                            <th># of CX Images</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {displayList.map((sku) => (
                            <tr key={sku.variantSku} className={(!sku.sensariaProductId || !sku.shippingMethod) && (sku.artfileNeeded ||  isFileTypeMisconfigured(sku.fileType) || sku.numberOfCxImages > 0) ? 'table-danger' : ''}>
                                <td className="align-middle text-center">
                                        {(!sku.sensariaProductId || !sku.shippingMethod) && (sku.artfileNeeded ||  isFileTypeMisconfigured(sku.fileType) || sku.numberOfCxImages > 0) ? (
                                            <OverlayTrigger
                                                placement="top"
                                                overlay={props => {
                                                    let missing = [];
                                                    let set = [];

                                                    if (!sku.sensariaProductId) missing.push("Sensaria Product ID");
                                                    if (!sku.shippingMethod) missing.push("Shipping Method");
                                                    if (sku.artfileNeeded) set.push("Artfile Needed");
                                                    if ( isFileTypeMisconfigured(sku.fileType)) set.push("File Type");
                                                    if (sku.numberOfCxImages > 0) set.push("Number of CX Images");

                                                    let message = `This SKU may be set incorrectly. You have set ${set.join(", ")} without setting ${missing.join(", ")}.`;
                                                    return renderTooltip(props, message);
                                                }}
                                            >
                                                <FontAwesomeIcon icon={faExclamationCircle} className="text-danger" />
                                            </OverlayTrigger>
                                        ) : (
                                            <FontAwesomeIcon icon={faCircleCheck} className="text-success" />
                                        )}
                                    </td>
                                <td className="text-truncate align-middle" style={{ maxWidth: '200px' }} title={sku.title}>{sku.title}</td>
                                {/* <td className="text-truncate" style={{ maxWidth: '150px' }} title={sku.handle}>{sku.handle}</td> */}
                                <td className="align-middle">{sku.option1}</td>
                                <td className="align-middle">{sku.option2}</td>
                                <td className="align-middle">{sku.option3}</td>
                                <td className="align-middle">{sku.variantSku}</td>
                                <td className="align-middle">{sku.sensariaProductId}</td>
                                <td className="align-middle">{sku.shippingMethod}</td>
                                <td className="align-middle">{sku.artfileNeeded ? "Yes" : "No"}</td>
                                <td className="align-middle">{sku.fileType}</td>
                                <td className="align-middle">{sku.numberOfCxImages}</td>
                                <td className="text-end d-flex align-middle">
                                    <Button
                                        variant="secondary"
                                        className="me-1"
                                        size="sm"
                                        onClick={() => openEditModal(sku)}
                                    >
                                        <FontAwesomeIcon icon={faEdit} className="text-white" />
                                    </Button>
                                    <Button
                                        variant="danger"
                                        className="me-1"
                                        size="sm"
                                        onClick={() => openDeleteModal(sku)}
                                    >
                                        <FontAwesomeIcon icon={faTrash} className="text-white" />
                                    </Button>
                                    {/* <Button variant="primary" size="sm" onClick={() => handleIgnoreSku(sku)}>
                                            <FontAwesomeIcon icon={faCircleCheck} className="text-white" />
                                    </Button> */}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            )}

            <EditSkuModal
                show={showEditModal}
                handleClose={closeEditModal}
                sku={editedSku}
                handleEditChange={handleEditChange}
                updateSku={updateSku}
                errorMessage={errorMessage}
                productshippingprofiles={productshippingprofiles}
                handleIgnoreSku={() => handleIgnoreSku(editedSku)}
            />

            <DeleteSkuModal
                show={showDeleteModal}
                handleClose={closeDeleteModal}
                sku={selectedSku}
                deleteSku={deleteSku}
                deletionStatus={deletionStatus}
                errorMessage={errorMessage}
                deletionMessage={deletionMessage}
            />

            <CreateSkuModal
                show={showCreateModal}
                handleClose={closeCreateModal}
                newSku={newSku}
                setNewSku={setNewSku}
                createSku={createSku}
                errorMessage={errorMessage}
                productshippingprofiles={productshippingprofiles}
            />
        </>
    );
}
