import { ListGroup, Accordion } from "react-bootstrap";

export default function OrderLineItemProperties({ properties }) {
    return (
        <>
            {properties && properties.length > 0 && (
                <Accordion className="mt-2">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>
                            <strong>Properties</strong>
                        </Accordion.Header>
                        <Accordion.Body>
                            <ListGroup>
                                {properties.map((item, index) => (
                                    <div key={`${item.name}-${index}`} className="rounded mb-2">
                                        <ListGroup.Item><strong>Title:</strong> {item.name}</ListGroup.Item>
                                        <ListGroup.Item><strong>Value:</strong> {item.value}</ListGroup.Item>
                                    </div>
                                ))}
                            </ListGroup>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            )}
        </>
    );
}
