import React, { useContext, useEffect, useState, useCallback } from "react";
import { Col, Row, Table, Button, Form, Alert, OverlayTrigger, Tooltip } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faSave, faCircleInfo } from "@fortawesome/free-solid-svg-icons";
import CacheContext from "../../services/cache.context";

export default function DistributionComponent() {
    const {
        distributionService,
        averageDailyOrderCaches,
        getUserById
    } = useContext(CacheContext);

    const [ distributionData, setDistributionData ] = useState(null);
    const [ userNames, setUserNames ] = useState({});
    const [ distributionType, setDistributionType ] = useState('artfile');
    const [ editingId, setEditingId ] = useState(null);
    const [ newDistribution, setNewDistribution ] = useState("");
    const [ totalDistributionCapacity, setTotalDistributionCapacity ] = useState(0);
    const [ totalAssignedOrders, setTotalAssignedOrders ] = useState(0);

    const fetchDistribution = useCallback((type, averageDailyOrderCaches) => {
        distributionService.getUsersCapacity(type, averageDailyOrderCaches)
            .then((res) => {
                setDistributionData(res.data);
                const userNamesPromises = res.data.map(({ userId }) =>
                    getUserById(userId).then(userName => [userId, userName])
                );
                const totalDistribution = res.data.reduce((acc, cur) => acc + cur.distribution, 0);
                setTotalDistributionCapacity(totalDistribution);
                const totalAssigned = res.data.reduce((acc, cur) => acc + cur.averageAssignedPerDay, 0);
                setTotalAssignedOrders(totalAssigned);
                Promise.all(userNamesPromises).then(names => {
                    setUserNames(prev => ({ ...prev, ...Object.fromEntries(names) }));
                });
            })
            .catch((err) => {
                console.log(err);
            });
    }, [distributionService, getUserById]);

    useEffect(() => {
        fetchDistribution('artfile', averageDailyOrderCaches);
    }, [fetchDistribution, averageDailyOrderCaches]);

    const handleEditClick = (userId, currentDistribution) => {
        setEditingId(userId);
        setNewDistribution(currentDistribution);
    };

    const handleSubmit = (userId) => {
        distributionService.setUserDistribution(userId, distributionType, newDistribution)
            .then(() => {
                setEditingId(null);
                fetchDistribution(distributionType, averageDailyOrderCaches);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    let capacityStatus = 'Healthy Capacity';
    let alertVariant = 'success';
    let capacityTooltip = 'Distribution is healthy';
    if (totalDistributionCapacity > totalAssignedOrders) {
        capacityStatus = 'Over Capacity';
        alertVariant = 'danger';
        capacityTooltip = 'Distribution is over capacity some users will not get assigned order in a timely manner.';
    } else if (totalDistributionCapacity / totalAssignedOrders >= 0.8) {
        capacityStatus = 'Near Capacity';
        alertVariant = 'warning';
        capacityTooltip = 'Distribution is nearing capacity please adjust distribution to avoid over capacity and users not getting assigned orders in a timely manner.';
    }

    return (
        <>
            <div className="d-flex flex-column flex-md-row justify-content-center">
                {totalDistributionCapacity !== null && <h6 className="me-2 mb-1 border p-1 rounded mb-md-0">Daily distribution <span className="badge bg-dark">{totalDistributionCapacity}</span></h6>}
                {totalAssignedOrders !== null && <h6 className="me-2 mt-md-0 border p-1 rounded mb-1 mb-md-0"> Daily average assigned orders <span className="badge bg-dark">{totalAssignedOrders}</span></h6>}
                <div className="p-1">
                <OverlayTrigger
                    placement="right"
                    overlay={
                        <Tooltip id={`tooltip-top`}>
                            {capacityTooltip}
                        </Tooltip>
                    }
                >
                    <Alert variant={alertVariant} className="mb-0 ps-1 pe-1 pt-0 pb-0 fw-bold mt-md-0 distribution-alert">
                        {capacityStatus} <FontAwesomeIcon icon={faCircleInfo} />
                    </Alert>
                </OverlayTrigger>
                </div>

            </div>
            <hr />
            <Row>
                <Col>
                    <div className="d-flex justify-content-start mb-3">
                        <Button variant={distributionType === 'artfile' ? 'dark' : 'light'}
                            className="me-2"
                            onClick={() => {
                                setDistributionType('artfile');
                                fetchDistribution('artfile', averageDailyOrderCaches);
                            }}>
                            Art File
                        </Button>
                        <Button variant={distributionType === 'printfile' ? 'dark' : 'light'}
                            onClick={() => {
                                setDistributionType('printfile');
                                fetchDistribution('printfile', averageDailyOrderCaches);
                            }}>
                            Print File
                        </Button>
                    </div>
                </Col>
            </Row>
            {distributionData &&
                <>
                    <h5>{distributionType === 'artfile' ? 'Art File Distribution' : 'Print File Distribution'}</h5>
                    <Table striped bordered hover className="rounded-table">
                        <thead>
                            <tr>
                                <th>User</th>
                                <th>Distribution</th>
                                <th>Orders Per Day</th>
                            </tr>
                        </thead>
                        <tbody>
                            {distributionData.map((user) => (
                                <tr key={user.userId}>
                                    <td>{userNames[user.userId]}</td>
                                    <td>
                                        {editingId === user.userId ? (
                                            <div className="d-flex">
                                                <Form.Control
                                                    type="text"
                                                    value={newDistribution}
                                                    onChange={(e) => setNewDistribution(e.target.value)}
                                                />
                                                <Button variant="dark btn-sm" className="ms-2" onClick={() => handleSubmit(user.userId)}>
                                                    <FontAwesomeIcon size="sm" icon={faSave} />
                                                </Button>
                                            </div>
                                        ) : (
                                            <>
                                                {user.distribution}
                                                <Button onClick={() => handleEditClick(user.userId, user.distribution)} variant="dark btn-sm" className="ms-2">
                                                    <FontAwesomeIcon size="sm" icon={faEdit} />
                                                </Button>
                                            </>
                                        )}
                                    </td>
                                    <td>{user.averageAssignedPerDay}</td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </>
            }
        </>
    );
}
