import { Toast } from 'react-bootstrap';

export default function ToastAlert({ show, onClose, message, variant = 'success' }) {
  const isWhiteText = ['dark', 'danger', 'info', 'success', 'warning'].includes(variant);

  return (
    <Toast 
    onClose={onClose} 
    show={show} 
    delay={5000} 
    autohide 
    bg={variant} 
    className='mb-2 shadow-sm'>
      <Toast.Header>
        <strong className={`me-auto text-${variant}`}>{variant.toUpperCase()}</strong>
      </Toast.Header>
      <Toast.Body className={isWhiteText ? 'text-white' : ''}>{message}</Toast.Body>
    </Toast>
  );
};
