import { useState } from "react";
import { Accordion, ListGroup } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTruckArrowRight } from "@fortawesome/free-solid-svg-icons";
import SensariaStatusBadgeComponent from "../shared/sensariaStatus.component";
import { copyToClipboard } from "../../utils/copyToClipboard";

export default function OrderFulfillmentLineItems({ workObject, lineItem }) {
    const [buttonText, setButtonText] = useState("Copy Email");

    const lineItems = workObject?.workObject?.metadata?.fulfillmentTracker?.LineItems || [];
    const matchedLineItem = lineItems.find(item => item.LineItemId === lineItem?.Id);

    const handleGetSensariaOrderFailureEmail = (orderID, lineItemID) => {
        return `
        We have an order that has a held status can you please look into this order. \n
        order id: ${orderID} \n
        line item id: ${lineItemID} \n
        thank you, \n
        `;
    }

    const handleCopyEmail = (orderID, lineItemID) => {
        const email = handleGetSensariaOrderFailureEmail(orderID, lineItemID);
        copyToClipboard(email);
        setButtonText("Copied!");
        setTimeout(() => {
            setButtonText("Copy Email");
        }, 2000); // Revert to original text after 2 seconds
    }

    // const handlePrepPopulateEmail = (orderID, lineItemID) => {
    //     const emailBody = handleGetSensariaOrderFailureEmail(orderID, lineItemID);

    //     const url =`https://mail.google.com/a/ecomgraduates.com/mail?compose=new&to=glen@ecomgraduates.com&su=Order ${orderID} Issue&body=${emailBody}&cc=ben@westandwillow.com`
    //     window.open(url, '_blank');
    // }
    const sensariaErrors = [3, 5, 16, 23, 37];

    return (
        <>
            {matchedLineItem && (
                <Accordion className="mt-2">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>
                            <strong className="me-2">Sensaria Details</strong>
                            <SensariaStatusBadgeComponent status={matchedLineItem.Status} />
                        </Accordion.Header>
                        <Accordion.Body>
                            <ListGroup>
                                <div className="rounded mb-2">
                                    {matchedLineItem.TrackingNumber ? <ListGroup.Item><strong>Tracking Number:</strong> {matchedLineItem.TrackingNumber}</ListGroup.Item> : null}
                                    <ListGroup.Item>
                                        <strong>Sensaria Order ID: </strong>
                                        {workObject?.workObject?.metadata?.fulfillmentTracker?.SensariaOrderNumber}
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <strong>Sensaria Line Item ID: </strong>
                                        {matchedLineItem.LineItemId}
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <strong>Status: </strong>
                                        <SensariaStatusBadgeComponent status={matchedLineItem.Status} />
                                    </ListGroup.Item>
                                    {matchedLineItem.TrackingUrl ? <ListGroup.Item><strong>Tracking Url:</strong> <a className="btn btn-outline-secondary btn sm" href={matchedLineItem.TrackingUrl} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faTruckArrowRight} /> Track Package</a></ListGroup.Item> : null}
                                    {sensariaErrors.includes(matchedLineItem.Status) ? (
                                        <ListGroup.Item>
                                            <strong>Sensaria Email Template:</strong>
                                            <button className="btn btn-outline-secondary btn-sm ms-2" onClick={() => handleCopyEmail(workObject?.workObject?.metadata?.fulfillmentTracker?.SensariaOrderNumber, matchedLineItem.LineItemId)}>{buttonText}</button>
                                            {/* or
                                            <button className="btn btn-outline-secondary btn-sm ms-2" onClick={() => handlePrepPopulateEmail(workObject?.workObject?.metadata?.fulfillmentTracker?.SensariaOrderNumber, matchedLineItem.LineItemId)}>Email</button> */}
                                        </ListGroup.Item>
                                    ) : null}
                                </div>
                            </ListGroup>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            )}
        </>
    );
}
