
import { Col, Row } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faClock, faUser, faUserClock } from "@fortawesome/free-solid-svg-icons";
import { useContext, useEffect, useState } from "react";
import CacheContext from "../../services/cache.context";
import { timeSince } from "../../utils/timeSince";
export default function WorkObjectDetails({ workObject }) {
  const { getUserById } = useContext(CacheContext);
  const [ assignedTo, setAssignedTo ] = useState(null);
  const [ previouslyAssignedTo, setPreviouslyAssignedTo ] = useState(null);
  const [ designatedDesigner, setDesignatedDesigner ] = useState(null);

  useEffect(() => {
    Promise.all([
      getUserById(workObject.assignedTo),
      getUserById(workObject.assignedToPrevious),
      getUserById(workObject.designatedPrintFileDesigner)
    ]).then((res) => {
      setAssignedTo(res[0]);
      setPreviouslyAssignedTo(res[1]);
      setDesignatedDesigner(res[2]);
    })
  }, [ workObject, getUserById ])

  return (
    <>
      <Row>
        <WorkObjectDetail icon={faClock} 
          name="Created On" 
          value={new Date(workObject.workObjectCreatedOn).toLocaleString()} 
          muted={timeSince(workObject.workObjectCreatedOn)}
        />

        <WorkObjectDetail 
          icon={faUser} 
          name="Assigned To" 
          value={assignedTo} 
        />

        <WorkObjectDetail 
          icon={faUserClock} 
          name="Assigned On" 
          value={new Date(workObject.assignedOn).toLocaleString()} 
          muted={timeSince(workObject.assignedOn)}
        />

        <WorkObjectDetail 
          icon={faUser} 
          name="Previously Assigned" 
          value={previouslyAssignedTo} 
        />

        <WorkObjectDetail 
          icon={faUser} 
          name="Print File Designer" 
          value={designatedDesigner} 
        />
      </Row>
    </>
  );
}


function WorkObjectDetail ({icon, name, value, muted}) {
  return (
    <Col className="text-center p-3">
      <FontAwesomeIcon icon={icon} size="2x"/> <br />
      <b>{name}</b><br />
      {value}
      { muted !== undefined && <><br /><span className="text-muted">{muted}</span></>}
    </Col>
  )
};