import React, { useState, useEffect, useContext, useRef } from 'react';  // Added useRef
import CacheContext from '../../services/cache.context';
import { Container, Button, Badge } from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDownWideShort, faArrowUpWideShort, faEye, faEyeSlash, faArrowUp } from "@fortawesome/free-solid-svg-icons";  // Added faArrowUp
import { convertToEasternTime } from "../../utils/convertToEasternTime";
import StatusBadgeComponent from '../shared/status.component';
const ActivityFeed = ({ activityData }) => {
  const { getUserById } = useContext(CacheContext);
  const [ sortAsc, setSortAsc] = useState(true);
  const [ collapsed, setCollapsed ] = useState({});
  const [ userNames, setUserNames ] = useState({});
  const [ toggleAll, setToggleAll ] = useState(false);
  const [ showScrollTop, setShowScrollTop ] = useState(false);
  const [totalEvents, setTotalEvents] = useState(0); // Added totalEvents
  const containerRef = useRef(null);


  const handleScroll = () => {
    if (containerRef.current.scrollTop > 0) {
      setShowScrollTop(true);
    } else {
      setShowScrollTop(false);
    }
  };

  useEffect(() => {
    const initialCollapsedState = {};
    const userIds = new Set();
    let eventCount = 0;
    activityData.results.forEach((activity) => {
      activity.changes.forEach((change) => {
        eventCount++;
        const changeId = `${activity.id}-${change.id}`;
        initialCollapsedState[changeId] = true;

        if (change.field === 'assignedTo' || change.field === 'userId' || change.field === 'assignedToPrevious') {
          if (change.currentValue) userIds.add(change.currentValue);
          if (change.modifiedValue) userIds.add(change.modifiedValue);
        }
      });

      if (activity.userId) {
        userIds.add(activity.userId);
      }
    });
    
    setTotalEvents(eventCount);
    setCollapsed(initialCollapsedState);

    (async () => {
      const fetchedNames = {};
      for (const userId of userIds) {
        const name = await getUserById(userId);
        fetchedNames[userId] = name || 'Unavailable';
      }
      setUserNames(fetchedNames);
    })();


  }, [activityData, getUserById]);

  useEffect(() => {
    const containerElement = containerRef.current;
    containerElement.addEventListener('scroll', handleScroll);
    return () => containerElement.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleAllCollapsed = () => {
    const newState = {};
    Object.keys(collapsed).forEach(key => {
      newState[key] = toggleAll;
    });
    setToggleAll(!toggleAll);
    setCollapsed(newState);
  };

  const scrollToTop = () => {
    containerRef.current.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  const sortedResults = activityData.results.filter(activity => activity.auditType.name !== 'Transition')
    .sort((a, b) => {
      const dateA = new Date(a.timestamp);
      const dateB = new Date(b.timestamp);
      return sortAsc ? dateA - dateB : dateB - dateA;
    });

  const toggleCollapsed = (changeId) => {
    setCollapsed(prevState => ({ ...prevState, [changeId]: !prevState[changeId] }));
  };

  const formatActivity = (activity) => {
    const dynamicArrayFieldRegex = /^(.+)\[(\d+)\](\..+)?$/;
    let result = {
      type: '',
      activity: '',
      oldValue: activity.currentValue,
      newValue: activity.modifiedValue
    };
  
    const setUserValue = (value) => value ? userNames[value] || 'Fetching...' : 'System';
  
    if (dynamicArrayFieldRegex.test(activity.field)) {
      const match = dynamicArrayFieldRegex.exec(activity.field);
      const arrayBase = match[1];
      const arrayIndex = match[2];
      const property = match[3] ? match[3].substring(1) : '';
  
      result.type = property || arrayBase;
      result.activity = `${property || arrayBase} changed`;
  
      switch (property) {
        case 'sourceUser':
        case 'assignedTo':
        case 'designatedPrintFileDesigner':
          result.oldValue = setUserValue(activity.currentValue);
          result.newValue = setUserValue(activity.modifiedValue);
          break;
        case 'originalFileName':
          result.type = 'fileName';
          result.activity = 'File name changed';
          break;
        case 'metadata.lineItemId':
        case 'LineItem.Name':
        case 'LineItem.VariantTitle':
        case 'LineItem.VariantId':
        case 'LineItem.SKU':
        case 'fileSize':
          break;
        default:
          if (!property) {
            result.activity = `${arrayBase} at index ${arrayIndex} changed`;
            if (arrayBase === 'comments') {
              result.activity = 'New comment';
              result.type = 'comment';
              result.oldValue = null;
              result.newValue = null;
            } else {
              result.type = 'arrayChange';
            }
          }
          break;
      }
    } else {
      result.type = activity.field;
      result.activity = `${activity.field} changed`;
  
      switch (activity.field) {
        case 'status':
          result.oldValue = <StatusBadgeComponent status={parseInt(activity.currentValue)} />;
          result.newValue = <StatusBadgeComponent status={parseInt(activity.modifiedValue)} />;
          break;
        case 'shopifyOrderId':
        case 'orderName':
        case 'orderNumber':
          break;
        case 'sourceUser':
        case 'assignedTo':
        case 'assignedToPrevious':
        case 'designatedPrintFileDesigner':
          result.oldValue = setUserValue(activity.currentValue);
          result.newValue = setUserValue(activity.modifiedValue);
          break;
        case 'customerEmail':
        case 'lastModified':
          break;
        default:
          break;
      }
    }
  
    return result;
  };
  

  return (
    <Container fluid style={{ maxHeight: '500px', overflowY: 'auto' }} ref={containerRef}>
      <div className='d-flex sticky-top bg-white w-100 shadow-sm p-2 rounded-bottom'>
        <div className='flex-grow-1'>
          <div className='bg-light text-dark d-inline-block me-2 p-2 rounded'>
            <strong className='mb-0'>Events</strong> <Badge className='bg-dark text-white d-inline-block'>{totalEvents}</Badge>
          </div>
        </div>
        <div className=''>
          <Button variant="light me-2" onClick={() => setSortAsc(!sortAsc)}>
            <FontAwesomeIcon icon={sortAsc ? faArrowDownWideShort : faArrowUpWideShort} /> Sort
          </Button>
          <Button variant="light" onClick={toggleAllCollapsed}>
            <FontAwesomeIcon icon={toggleAll ? faEye : faEyeSlash} /> Toggle All
          </Button>
        </div>
      </div>
      <ul className="timeline">
        {sortedResults.map((activity, index) =>
          activity.changes.map((change, changeIndex) => {
            const formattedChange = formatActivity(change);
            const changeId = `${activity.id}-${change.id}`;
            return (
              <li key={`${changeId}`}>
                <div className="timeline-icon">
                  <div>&nbsp;</div>
                </div>
                <div className="timeline-body bg-light border border-dark shadow-sm">
                  <div className='position-absolute end-0 top-0'>
                    <Button className='bg-transparent text-muted border-0 p-2' onClick={() => toggleCollapsed(changeId)}>
                      <FontAwesomeIcon icon={collapsed[changeId] ? faEyeSlash : faEye} />
                    </Button>
                  </div>
                  <div className="timeline-header">
                    <span className="username">
                      <Badge className='bg-dark text-white'>{formattedChange.activity}</Badge>
                      <div className="d-flex justify-content-start flex-column">
                      <strong>User: <span className='text-muted'>{activity.userId ? userNames[activity.userId] || 'Fetching...' : 'System'}</span></strong>
                      <strong>On: <span className="date text-muted">{convertToEasternTime(activity.timestamp, false, false)}</span></strong>
                      
                      </div>
                    </span>
                  </div>
                  <div className={`timeline-content ${collapsed[changeId] ? '' : 'open'}`}>
                    <hr />
                    <p className='mb-0'><strong>User:</strong> {activity.userId ? userNames[activity.userId] || 'Fetching...' : 'System'}</p>
                    <div>
                      <p><strong>Activity:</strong> {formattedChange.activity}</p>
                      {formattedChange.oldValue && (
                        <p><strong>Previous Value:</strong> {formattedChange.oldValue}</p>
                      )}
                      {formattedChange.newValue && (
                        <p><strong>Updated Value:</strong> {formattedChange.newValue}</p>
                      )}
                    </div>
                  </div>
                </div>
              </li>
            );
          })
        )}
      </ul>

      {showScrollTop && (
        <Button
          variant="dark"
          style={{
            position: 'absolute',
            right: '3rem',
            bottom: '1rem',
            zIndex: 1
          }}
          onClick={scrollToTop}
        >
          <FontAwesomeIcon icon={faArrowUp} />
        </Button>
      )}
    </Container>
  );
};

export default ActivityFeed;
