import { useState, useContext, useEffect } from 'react';
import { ListGroup, Form, Button, Alert, OverlayTrigger, Tooltip, Badge } from "react-bootstrap";
import AuthContext from '../../services/auth.context';
import LoadingComponent from '../shared/loading.component';
import SkuPopoverComponent from './sku.popover.component';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import Select from 'react-select';

export default function OrderLineItemSkuDetails({ workObject, lineItem, product, fetchWorkObject, productshippingprofiles, productsCache, orderService }) {
    const [ selectedVariant, setSelectedVariant ] = useState(lineItem.SKU);
    const [ quantity, setQuantity ] = useState(lineItem.Quantity);
    const [ selectedShippingMethod, setSelectedShippingMethod ] = useState(product?.ShippingMethod);
    const [ success, setSuccess ] = useState(false);
    const [showPopover, setShowPopover] = useState(false);
    const [popoverTarget, setPopoverTarget] = useState(null);

    const { hasPermission } = useContext(AuthContext);

    useEffect(() => {
        if (success) {
            const timer = setTimeout(() => {
                fetchWorkObject();
                setSuccess(false);
            }, 3000);
            return () => clearTimeout(timer);
        }
    }, [success, fetchWorkObject]);
    
    if (!productsCache || !lineItem) {
        return <LoadingComponent variant='dark' size="sm" />;
    }
    

    const allowEditing = hasPermission("order:write") && hasPermission("line_item:write");

    const handleShippingMethodChange = e => {
        setSelectedShippingMethod(e.target.value);
    };

    const handleSave = async () => {
        orderService.updateLineItem(workObject.workObject.shopifyOrderId, lineItem.Id, quantity, selectedVariant, selectedShippingMethod).then((res) => {
            setSuccess(true);
        }).catch((err) => {
            console.log(err);
        }).finally(() => {});
    };

    const isShippableProduct = productsCache?.find(product => product.variantSku === lineItem.SKU && product.shippingMethod !== null && product.sensariaProductId !== null);
    const skuExists = productsCache?.find(product => product.variantSku === lineItem.SKU);
    const hasSensariaPID = productsCache?.find(product => product.variantSku === lineItem.SKU && product.sensariaProductId !== null);
    const hasShipMethod = productsCache?.find(product => product.variantSku === lineItem.SKU && product.shippingMethod !== null);
    const hasNumberOfCxImages = productsCache?.find(product => product.variantSku === lineItem.SKU && product.numberOfCxImages !== null && product.numberOfCxImages > 0);
    const hasFileType = productsCache?.find(product => product.variantSku === lineItem.SKU && product.fileType !== null);
    const hasArtfileNeeded = productsCache?.find(product => product.variantSku === lineItem.SKU && product.artfileNeeded !== null && product.artfileNeeded === true);

    const filteredProductsCache = productsCache.filter(product =>
        productshippingprofiles.some(profile => profile.id === product.shippingMethod)
    );

    let groupedOptions = {};
    filteredProductsCache.forEach(product => {
        const groupKey = `${product.title} ${product.option3 ? ` - ${product.option3}` : ''}`;
        if (!groupedOptions[groupKey]) {
            groupedOptions[groupKey] = [];
        }
        groupedOptions[groupKey].push(product);
    });

    const handlePopoverToggle = (event) => {
        setShowPopover(!showPopover);
        setPopoverTarget(event.target);
    };

    const renderIgnoredMessage = () => {
        let message = "This SKU is ignored because the following were set to 0, null, or false:";
        let reasons = [];

        if (!hasArtfileNeeded) reasons.push("Artfile Needed");
        if (!hasFileType) reasons.push("File Type");
        if (!hasNumberOfCxImages) reasons.push("Number of CX Images");
        if (!hasSensariaPID) reasons.push("Sensaria PID");
        if (!hasShipMethod) reasons.push("Shipping Method");

        if (reasons.length > 0) {
            return (
                <>
                    {message}
                    <ul>
                        {reasons.map((reason, index) => (
                            <li key={index}><Badge bg="secondary">{reason}</Badge></li>
                        ))}
                    </ul>
                </>
            );
        } else {
            return "You may not have set the SKU correctly.";
        }
    };

     groupedOptions = Object.keys(groupedOptions).map(group => ({
        label: group,
        options: groupedOptions[group].map(product => ({
            label: `${product.option1} / ${product.option2} / ${product.option3} (${product.variantSku})`,
            value: product.variantSku
        }))
    }));

    const selectedOption = groupedOptions
        .flatMap(group => group.options)
        .find(option => option.value === selectedVariant);

    return (
        <>
            {success && <Alert variant="success">Update successful!</Alert>}
            <Form className='sticky-top-2'>
                {isShippableProduct?.shippingMethod && filteredProductsCache.length > 0 ? (
                    <>
                        <p className="fs-5 mb-1">Details</p>
                        <ListGroup>
                            <ListGroup.Item>
                                <strong>SKU:</strong> {lineItem.SKU}
                                <OverlayTrigger
                                    placement="top"
                                    overlay={<Tooltip id="button-tooltip">View SKU Details</Tooltip>}
                                >
                                    <FontAwesomeIcon icon={faInfoCircle} className="ms-2" style={{ cursor: 'pointer' }} onClick={handlePopoverToggle} />
                                </OverlayTrigger>
                                <SkuPopoverComponent
                                    show={showPopover}
                                    target={popoverTarget}
                                    onClose={() => setShowPopover(false)}
                                    sku={lineItem.SKU}
                                />
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <strong>Variant:</strong>
                                <Select
                                    value={selectedOption}
                                    onChange={(selectedOption) => setSelectedVariant(selectedOption.value)}
                                    options={groupedOptions}
                                    isDisabled={!allowEditing}
                                />
                            </ListGroup.Item>


                            <ListGroup.Item><strong>Qty:</strong>
                                <Form.Group className="mb-3 d-flex align-items-center">
                                    <Form.Control
                                        disabled={!allowEditing}
                                        type="number"
                                        value={quantity}
                                        onChange={(e) => setQuantity(e.target.value)}
                                        aria-label="Quantity"
                                    />
                                </Form.Group>
                            </ListGroup.Item>


                            <ListGroup.Item>
                                <strong>Shipping Method:</strong>
                                <Form.Select value={selectedShippingMethod} onChange={handleShippingMethodChange} disabled={!allowEditing}>
                                    {productshippingprofiles.map(profile => (
                                        <option key={profile.id} value={profile.id}>
                                            {profile.method} ({profile.id})
                                        </option>
                                    ))}
                                </Form.Select>
                            </ListGroup.Item>

                        </ListGroup>

                        <div className="mt-2">
                            <Button variant="" className="w-100 btn-brand" onClick={handleSave}>Save</Button>
                        </div>
                    </>
                ) : (
                    <>
                    <div>
                        {skuExists ? (
                            <>
                                 <p className="text-muted mb-0">{renderIgnoredMessage()}</p>
                            </>
                        ) : (
                            <Badge bg="danger" className='mb-2'>SKU NOT FOUND</Badge>
                        )}
                    </div>
                    <strong>SKU:</strong> {lineItem.SKU}
                    <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id="button-tooltip">View SKU Details</Tooltip>}
                    >
                        <FontAwesomeIcon icon={faInfoCircle} className="ms-2" style={{ cursor: 'pointer' }} onClick={handlePopoverToggle} />
                    </OverlayTrigger>
                    <SkuPopoverComponent
                        show={showPopover}
                        target={popoverTarget}
                        onClose={() => setShowPopover(false)}
                        sku={lineItem.SKU}
                    />
                </>
                )}
            </Form>
        </>
    );
}