import authHeader from "./auth.header";
import axios from "axios";
export default class DashboardService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    getCharts() {
        return axios.get(`${this.baseUrl}/portal/api/v1/dashboard`, authHeader());
    }

    getOrdersByStatus(payload) {
        return axios.post(`${this.baseUrl}/portal/api/v1/WorkObject/search`, payload, authHeader());
    }
}

