import React, { useContext } from 'react';
import { Badge, OverlayTrigger, Tooltip } from 'react-bootstrap';
import ConfigContext from '../../services/config.context';

export default function SensariaStatusBadgeComponent({ status }) {
  const { config } = useContext(ConfigContext);
  const renderTooltip = (props, text) => (
    <Tooltip id="button-tooltip" {...props}>
      {text}
    </Tooltip>
  );
  const currentStatus = config.sensariaStatusMap[status.toString()] || { text: 'Unknown Status', variant: 'bg-dark' };

  return (
    <OverlayTrigger
      placement="top"
      overlay={props => renderTooltip(props, currentStatus.text)}
    >
      <Badge className={currentStatus.variant}>{currentStatus.title}</Badge>
    </OverlayTrigger>
  );
}
