import authHeader from "./auth.header";
import axios from "axios";
export default class CommentsService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    newComment(orderId, message) {
        return axios.post(`${this.baseUrl}/portal/api/v1/comment/${orderId}`, { message: message }, authHeader());
    }

    replyToComment(orderId, message, commentId) {
        return axios.post(`${this.baseUrl}/portal/api/v1/comment/${orderId}/${commentId}`, { message: message }, authHeader());
    }

    deleteComment(orderId, commentId) {
        return axios.delete(`${this.baseUrl}/portal/api/v1/comment/${orderId}/${commentId}`, authHeader());
    }
}

