import { useContext, useEffect, useState } from 'react';
import { Popover, Overlay, ListGroup, Badge, Button } from 'react-bootstrap';
import { useNavigate, useLocation } from 'react-router-dom'; // Import useNavigate and useLocation from react-router-dom
import CacheContext from '../../services/cache.context';
import LoadingComponent from '../shared/loading.component';

const renderSkuData = (skuData) => {
    if (!skuData) {
        return (
            <ListGroup.Item>
                <div className="text-center text-muted">SKU details not found</div>
            </ListGroup.Item>
        );
    }
    return (
        <ListGroup.Item className='mb-2'>
            <span className='fw-bold'>SKU: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.variantSku}</Badge> <br />
            <span className='fw-bold'>Option 1: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.option1}</Badge> <br />
            <span className='fw-bold'>Option 2: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.option2}</Badge> <br />
            <span className='fw-bold'>Option 3: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.option3}</Badge> <br />
            <span className='fw-bold'>Shipping Method: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.shippingMethod}</Badge> <br />
            <span className='fw-bold'>Sensaria Product ID: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.sensariaProductId}</Badge> <br />
            <span className='fw-bold'>Artfile Required: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.artfileNeeded ? 'Yes' : 'No'}</Badge> <br />
            <span className='fw-bold'>CX Images: </span><Badge className="bg-brand mb-1 rounded-pill">{skuData.numberOfCxImages}</Badge> <br />
        </ListGroup.Item>
    );
};

export default function SkuPopoverComponent({ show, target, onClose, sku }) {
    const { lookupService } = useContext(CacheContext);
    const [skuData, setSkuData] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const navigate = useNavigate(); // Use useNavigate to navigate programmatically
    const location = useLocation(); // Use useLocation to get the current location

    useEffect(() => {
        setIsLoading(true);
        lookupService.getProducts()
            .then((res) => {
                const foundSku = res.data.find(product => product.variantSku === sku);
                setSkuData(foundSku);
            })
            .catch((err) => {
                console.log(err);
            })
            .finally(() => {
                setIsLoading(false);
            });

    }, [sku, lookupService]);

    const handleEditClick = () => {
        onClose();
        navigate(`/skus?edit=${sku}`, { state: { from: location } }); // Pass current location as state
    };

    const handleCreateClick = () => {
        onClose();
        navigate(`/skus?new=${sku}`, { state: { from: location } }); // Pass current location as state
    };
    return (
        <Overlay
            show={show}
            target={target}
            placement="right"
            rootClose={true}
            onHide={onClose}
        >
            <Popover id="popover-contained" className='shadow'>
                <Popover.Header as="h3" className='text-white'>
                    {isLoading ? (
                        <strong>Loading...</strong>
                    ) : skuData ? (
                        <strong>{skuData.title}</strong>
                    ) : (
                        <strong>SKU not found</strong>
                    )}
                </Popover.Header>

                <Popover.Body>
                    {isLoading ? (
                        <LoadingComponent message="Loading SKU details..." size="sm" variant='dark'/>
                    ) : skuData ? (
                        <ListGroup className='shadow-sm'>
                            {renderSkuData(skuData)}
                        </ListGroup>
                    ) : (
                        <div className="alert alert-danger" role="alert">
                            SKU not found
                        </div>
                    )}
                    <div className='text-end mt-2'>
                    {skuData ? (
                        <Button className="btn-sm btn-brand" onClick={handleEditClick}>Edit</Button>
                    ) : (
                        <Button className="btn-sm btn-brand" onClick={handleCreateClick}>Create</Button>
                    )}
                    </div>
                </Popover.Body>
            </Popover>
        </Overlay>
    );
}
