import React, { useState, useEffect, useContext } from 'react';
import ConfigContext from "../../services/config.context";
import { Button, OverlayTrigger, Tooltip, Alert } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCancel, faRotateLeft, faTags, faUsers, faForward } from '@fortawesome/free-solid-svg-icons';
import { faShopify } from "@fortawesome/free-brands-svg-icons"
import ShowReturnModalComponent from './showreturn.modal.component';
import ShowAssignModalComponent from './showassign.modal.component';
import ManuallyAssignModalComponent from './manuallyassign.modal';
import ShowCancelModalComponent from './showcancel.modal.component';
import ShowTagsModalComponent from './showtags.modal.component';

export default function WorkObjectNav({ workObject, orderService, fetchWorkObject, hasPermission, user }) {
  const { config } = useContext(ConfigContext);
  const [orderName, setOrderName] = useState('');
  const [showModal, setShowReturnModal] = useState(false);
  const [showCancelModal, setShowCancelModal] = useState(false);
  const [showAssignModal, setShowAssignModal] = useState(false);
  const [showManuallyAssignModal, setShowManuallyAssignModal] = useState(false);
  const [showTagsModal, setShowTagsModal] = useState(false);
  const [alert, setAlert] = useState({ show: false, message: '', variant: '' });
  const isManager = hasPermission('user:write');

  useEffect(() => {
    if (workObject?.workObject?.metadata) {
      setOrderName(workObject.workObject.metadata.orderName);
    } else {
      setOrderName('No order name');
    }
  }, [workObject]);

  useEffect(() => {
    if (alert.show) {
      const timer = setTimeout(() => {
        setAlert({ show: false, message: '', variant: '' });
      }, 3000);

      return () => clearTimeout(timer);
    }
  }, [alert]);

  const handleReturn = () => {
    setShowReturnModal(true);
  };

  const handleCancel = () => {
    setShowCancelModal(true);
  };

  const handleAssign = () => {
    if (workObject.workObject.status <= 2) {
      setShowManuallyAssignModal(true);
    } else {
      setShowAssignModal(true);
    }
  };

  const handleTags = () => {
    setShowTagsModal(true);
  };

  const renderTooltip = (props, text) => (
    <Tooltip id="button-tooltip" {...props}>
      {text}
    </Tooltip>
  );

  const handleForceTransition = (workObjectId) => {
    orderService.forceTransition(workObjectId).then(() => {
      setAlert({ show: true, message: 'Successfully transitioned to next status', variant: 'success' });
      fetchWorkObject();
    }).catch(() => {
      setAlert({ show: true, message: 'Failed to transition to next status', variant: 'danger' });
    });
  }
  return (
    <>
      <div className="bg-brand text-white p-2 mb-3 rounded border-primary border">
        <div className="d-flex">
          <div className="d-flex align-items-center mb-0 me-auto">
            <h3 className="mb-0">{orderName || 'Not Available'}</h3>
            {user?.email?.includes('ecomgraduates.com') ? <h4 className="mb-0 ms-2"> workObjectID: #{workObject.workObject.id}</h4> : null}
          </div>
          {isManager && (workObject.workObject.status === 9003 || workObject.workObject.status === 3 || workObject.workObject.status === 4) && (
            <OverlayTrigger
              placement="top"
              overlay={props => renderTooltip(props, `Force transition to status ${workObject?.workObject?.status === 9003 ? 'Pending Assignment' : 'Pending Printfile'}`)}
            >
              <Button variant="primary" size="md" className="mx-2 bg-transparent text-white border-0" onClick={()=> handleForceTransition(workObject.workObject.id)}>
                <FontAwesomeIcon icon={faForward} />
              </Button>
            </OverlayTrigger>
          )}
          {isManager && (
            <OverlayTrigger
              placement="top"
              overlay={props => renderTooltip(props, 'Manage Tags')}
            >
              <Button variant="primary" size="md" className="mx-2 bg-transparent text-white border-0" onClick={handleTags}>
                <FontAwesomeIcon icon={faTags} />
              </Button>
            </OverlayTrigger>
          )}
          {workObject?.workObject?.status !== 8000 && isManager && (
            <OverlayTrigger
              placement="top"
              overlay={props => renderTooltip(props, 'Assign Order')}
            >
              <Button variant="primary" size="md" className="mx-2 bg-transparent text-white border-0" onClick={handleAssign}>
                <FontAwesomeIcon icon={faUsers} />
              </Button>
            </OverlayTrigger>
          )}
          <OverlayTrigger
            placement="top"
            overlay={props => renderTooltip(props, 'Return Order to Previous Status')}
          >
            <Button variant="primary" size="md" className="mx-2 bg-transparent text-white border-0" onClick={handleReturn}>
              <FontAwesomeIcon icon={faRotateLeft} />
            </Button>
          </OverlayTrigger>
          {workObject?.workObject?.status !== 8000 && isManager && (
            <OverlayTrigger
              placement="top"
              overlay={props => renderTooltip(props, 'Cancel Order')}
            >
              <Button variant="primary" size="md" className="mx-2 bg-transparent text-white border-0" onClick={handleCancel}>
                <FontAwesomeIcon icon={faCancel} />
              </Button>
            </OverlayTrigger>
          )}
          {workObject?.workObject?.status !== 8000 && isManager && (
            <OverlayTrigger
              placement="top"
              overlay={props => renderTooltip(props, 'View in Shopify')}
            >
              <Button variant="primary" size="md" href={`${config.shopifyUrl}/admin/orders/${workObject.workObject.shopifyOrderId}`} rel="noopener noreferrer" target="_blank" className="mx-2 bg-transparent text-white border-0">
                <FontAwesomeIcon icon={faShopify} />
              </Button>
            </OverlayTrigger>
          )}
        </div>
      </div>
      <hr />
      {alert.show && <Alert dismissible variant={alert.variant}>{alert.message}</Alert>}

      <ShowReturnModalComponent
        show={showModal}
        onHide={() => setShowReturnModal(false)}
        workObject={workObject}
        fetchWorkObject={fetchWorkObject}
        setShowReturnModal={setShowReturnModal}
        setAlert={setAlert}
        orderService={orderService}
      />

      <ShowAssignModalComponent
        show={showAssignModal}
        onHide={() => setShowAssignModal(false)}
        orderService={orderService}
        workObject={workObject}
        fetchWorkObject={fetchWorkObject}
        setShowAssignModal={setShowAssignModal}
        setAlert={setAlert}
      />

      <ManuallyAssignModalComponent
        show={showManuallyAssignModal}
        onHide={() => setShowManuallyAssignModal(false)}
        orderService={orderService}
        workObject={workObject}
        fetchWorkObject={fetchWorkObject}
        setAlert={setAlert}
        setShowManuallyAssignModal={setShowManuallyAssignModal}
      />

      <ShowCancelModalComponent
        show={showCancelModal}
        onHide={() => setShowCancelModal(false)}
        orderService={orderService}
        workObject={workObject}
        setShowCancelModal={setShowCancelModal}
        setAlert={setAlert}
        fetchWorkObject={fetchWorkObject}
      />

      <ShowTagsModalComponent
        show={showTagsModal}
        onHide={() => setShowTagsModal(false)}
        orderService={orderService}
        workObject={workObject}
        setShowTagsModal={setShowTagsModal}
        setAlert={setAlert}
        fetchWorkObject={fetchWorkObject}
      />
    </>
  );
}
