import { useState, useEffect } from 'react';
import { Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowAltCircleDown } from '@fortawesome/free-solid-svg-icons'; // Changed icon for downward arrow

const ScrollDownComponent = () => {
  const [isVisible, setIsVisible] = useState(true);

  const toggleVisibility = () => {
    if (window.pageYOffset < 300) { // Changed condition to show button when scrolled down
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', toggleVisibility);

    return () => {
      window.removeEventListener('scroll', toggleVisibility);
    };
  }, []);

  const scrollToBottom = () => {
    window.scrollTo({
      top: document.documentElement.scrollHeight, // Changed to scroll to the bottom
      behavior: 'smooth'
    });
  };

  return (
    <>
      {isVisible && (
        <Button
          className='scroll-top-button btn-brand shadow rounded-circle'
          variant="primary"
          onClick={scrollToBottom}
        >
          <FontAwesomeIcon icon={faArrowAltCircleDown} /> {/* Changed icon */}
        </Button>
      )}
    </>
  );
};

export default ScrollDownComponent;
