import { Row, Col, Card } from "react-bootstrap";
import OrderLineItemSkuDetails from "./order.lineitem.skudetails.component";
import OrderLineItemProperties from "./order.lineitem.properties.component";
import OrderLineItemAttachments from "./order.lineitem.attatchments";
import OrderFulfillmentLineItems from "./order.lineitem.tracking.component";
import LoadingComponent from "../shared/loading.component";
import UploadComponent from "../shared/upload.component";

export default function OrderLineItems({
  workObject,
  productsCache,
  productshippingprofiles,
  fetchWorkObject,
  config,
  orderService,
}) {
  if (!workObject?.workObject || !workObject?.workObject?.attachments) {
    return <LoadingComponent message="Loading line items..." />;
  }

  const isUploadNeeded = (lineItem) => {
    const { Product } = lineItem;
    if (!Product) return false;
    const { ArtfileNeeded, NumberOfCxImages, ShippingMethod, SensariaProductId } = Product;
    return ArtfileNeeded || (!ArtfileNeeded && NumberOfCxImages === 0 && ShippingMethod && SensariaProductId);
  };

  const getFileTypes = (attachmentType, artFileTypeRequired) => {
    if (attachmentType === 'artfile' || attachmentType === 'printfile') {
      return artFileTypeRequired ? [artFileTypeRequired] : [attachmentType === 'artfile' ? '.png' : '.jpg'];
    }
    return [];
  };

  const parentProductMap = new Map();
  workObject.workObject.metadata.lineItems.forEach((LineItem) => {
    const parentIdProperty = LineItem.LineItem.Properties?.find(property => property.Name === 'id');
    if (parentIdProperty) {
      parentProductMap.set(parentIdProperty.Value, LineItem);
    }
  });

  const checkAddonProductParentId = (LineItem) => {
    const parentIdProperty = LineItem.LineItem.Properties?.find(property => property.Name === 'parentId');
    if (parentIdProperty) {
      return parentProductMap.has(parentIdProperty.Value);
    }
    return false;
  };

  const handleScrollTo = (id) => {
    setTimeout(() => {
      const element = document.getElementById(id);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    }, 0);
  };

  return (
    <div>
      <Row>
        <Col md={12}>
          <div className="text-start mb-2">
            <h4 className="mb-0 ms-1">Line Items</h4>
          </div>
        </Col>
      </Row>
      <Row className="mb-2">
        {workObject.workObject.metadata.lineItems.map((LineItem, lineItemIndex) => {
          const workObjectId = workObject.workObject.id;
          const lineItemId = LineItem.LineItem.Id;
          const isAddonProduct = LineItem.LineItem.Properties?.some(property => property.Name === 'parentId');
          const isParentProduct = LineItem.LineItem.Properties?.some(property => property.Name === 'id');
          const childId = LineItem.LineItem.Properties?.find(property => property.Name === 'parentId')?.Value;
          const parentId = LineItem.LineItem.Properties?.find(property => property.Name === 'id')?.Value;
          const artFileTypeRequired = LineItem?.Product?.FileType;
          const uploadNeeded = isUploadNeeded(LineItem);
          const imageCountNeeded = LineItem?.Product?.NumberOfCxImages;
          const lineItemAttachments = workObject.workObject.attachments.filter(attachment => attachment.metadata.lineItemId === lineItemId);
          const isShippableProduct = productsCache?.find(product => product.variantSku === LineItem.LineItem.SKU && product.shippingMethod !== null && product.sensariaProductId !== null);
          const parentIdMatch = isAddonProduct ? checkAddonProductParentId(LineItem) : true;
          const skuExists = productsCache?.find(product => product.variantSku === LineItem.LineItem.SKU);
          return (
            <Col key={lineItemIndex} md={12} className="mb-3" id={isAddonProduct && parentIdMatch ? `child-${childId}` : isParentProduct ? `parent-${parentId}` : null}>
              <Card>
                <Card.Header className="bg-brand text-white d-flex">
                  {LineItem.LineItem.Name.split('-')[0]} | <b className="ms-1">Line Item ID:</b>{LineItem.LineItem.Id}
                  <div className="ms-auto">
                    {!isShippableProduct && skuExists ? (
                      <span className="p-1 rounded bg-danger ms-1">IGNORED</span>
                    ) : !skuExists ? (
                      <span className="p-1 rounded bg-danger ms-1">SKU NOT FOUND</span>
                    ) : null}
                    {isAddonProduct && parentIdMatch ? (
                      <span className="bg-secondary rounded p-1 text-white ms-1 parent-child-label" onClick={() => handleScrollTo(`parent-${childId}`)}>Child Product <span className="child-parent-id">{childId}</span></span>
                    ) : (
                      isAddonProduct && !parentIdMatch && (
                        <span className="bg-danger rounded p-1 text-white ms-1">Parent ID {childId} Not Found</span>
                      )
                    )}
                    {isParentProduct && !isAddonProduct && (
                      <span className="bg-secondary rounded p-1 text-white ms-1 parent-child-label" onClick={() => handleScrollTo(`child-${parentId}`)}>Parent Product {parentId}</span>
                    )}
                    {uploadNeeded && (
                      imageCountNeeded < 1 ? (
                        <span className="bg-danger rounded p-1 text-white ms-1">No CX Images Needed</span>
                      ) : (
                        <>
                          <span className="bg-warning rounded p-1 text-white ms-1">{imageCountNeeded} CX Images Needed</span>
                          <span className="bg-info rounded p-1 text-white ms-1">{imageCountNeeded} Artfiles Needed</span>
                        </>
                      )
                    )}
                  </div>
                </Card.Header>
                <Card.Body>
                  <Row>
                    <Col md={3}>
                      <OrderLineItemSkuDetails
                        lineItem={LineItem.LineItem}
                        product={LineItem.Product}
                        productsCache={productsCache}
                        fetchWorkObject={fetchWorkObject}
                        productshippingprofiles={productshippingprofiles}
                        orderService={orderService}
                        workObject={workObject}
                      />
                      <OrderFulfillmentLineItems workObject={workObject} lineItem={LineItem.LineItem} />
                      <OrderLineItemProperties properties={LineItem.LineItem.properties} />
                    </Col>

                    {uploadNeeded && ["cx", "artfile", "printfile"].map((attachmentType) => {
                      const lineItemAttachmentsOfType = lineItemAttachments.filter(attachment => attachment.type === attachmentType);

                      return <Col key={`${lineItemIndex}-${attachmentType}`} md={3}>
                        
                        <p className="fs-5 mb-1 text-capitalize">{attachmentType}</p>
                        {attachmentType === 'cx' && (
                          <>
                            {lineItemAttachmentsOfType.map((attachment, attachmentIndex) => (
                              <OrderLineItemAttachments
                                key={`${attachment.id}-${attachmentIndex}`}
                                cxPhotoIndex={attachmentIndex}
                                workObjectId={workObjectId}
                                attachment={attachment}
                                fetchWorkObject={fetchWorkObject}
                                lineItemId={lineItemId}
                                imageType={attachmentType}
                                fileTypes={config.cxImageTypes}
                                config={config}
                                isAddonProduct={isAddonProduct && parentIdMatch}
                                workObject={workObject}
                                lineItemIndex={lineItemIndex}
                              />
                            ))}
                            {Array.from({ length: Math.max(0, imageCountNeeded - lineItemAttachmentsOfType.length) }).map((_, index) => (
                              <div key={index} className="alert alert-light mb-4">
                                <UploadComponent
                                  cxPhotoIndex={index + lineItemAttachmentsOfType.length + 1}  // Adding 1 here
                                  lineItemId={lineItemId}
                                  workObjectId={workObjectId}
                                  imageType={attachmentType}
                                  fileTypes={config.cxImageTypes}
                                  fetchWorkObject={fetchWorkObject}
                                  config={config}
                                  isReplacing={false}
                                  isAddonProduct={isAddonProduct && parentIdMatch}
                                  workObject={workObject}
                                  lineItemIndex={lineItemIndex}
                                />
                              </div>
                            ))}
                          </>
                        )}
                        {attachmentType === 'artfile' && (
                          <>
                            {lineItemAttachmentsOfType.map((attachment, attachmentIndex) => (
                              <OrderLineItemAttachments
                                key={`${attachment.id}-${attachmentIndex}`}
                                cxPhotoIndex={attachmentIndex}
                                workObjectId={workObjectId}
                                attachment={attachment}
                                fetchWorkObject={fetchWorkObject}
                                lineItemId={lineItemId}
                                imageType={attachmentType}
                                fileTypes={config.cxImageTypes}
                                config={config}
                                isAddonProduct={isAddonProduct && parentIdMatch}
                                workObject={workObject}
                                lineItemIndex={lineItemIndex}
                              />
                            ))}
                            {Array.from({ length: Math.max(0, imageCountNeeded - lineItemAttachmentsOfType.length) }).map((_, index) => (
                              <div key={index} className="alert alert-light mb-4">
                                <UploadComponent
                                  cxPhotoIndex={index + lineItemAttachmentsOfType.length + 1}  // Adding 1 here
                                  lineItemId={lineItemId}
                                  workObjectId={workObjectId}
                                  imageType={attachmentType}
                                  fileTypes={config.cxImageTypes}
                                  fetchWorkObject={fetchWorkObject}
                                  config={config}
                                  isReplacing={false}
                                  isAddonProduct={isAddonProduct && parentIdMatch}
                                  workObject={workObject}
                                  lineItemIndex={lineItemIndex}
                                />
                              </div>
                            ))}
                          </>
                        )}
                        {(attachmentType === 'printfile') && (
                          <OrderLineItemAttachments
                            key={`${lineItemIndex}-${attachmentType}`}
                            workObjectId={workObjectId}
                            attachment={lineItemAttachmentsOfType[0]}
                            fetchWorkObject={fetchWorkObject}
                            lineItemId={lineItemId}
                            imageType={attachmentType}
                            fileTypes={getFileTypes(attachmentType, artFileTypeRequired)}
                            config={config}
                            isAddonProduct={isAddonProduct}
                            workObject={workObject}
                            lineItemIndex={lineItemIndex}
                          />
                        )}
                      </Col>
                    })}
                  </Row>
                </Card.Body>
              </Card>
            </Col>
          );
        })}
      </Row>
    </div>
  );
}
