import React, { useContext, useEffect, useState } from "react";
import { Tab, Row, Col, Nav, Table, Container } from "react-bootstrap";
import CacheContext from "../../services/cache.context";
import PaginationComponent from "../shared/pagination.component";
import DashboardTableRowComponent from "./dashboard.table.row.component";
import { getStartDate } from "../../utils/getStartDate";

/*
statuses
sensaria failure: 9004
missing artwork: 3 //orders that are 24 hours old
missing printfile: 4 orders that are 72 hours old
missing CX: 9003 
unknown sku: 9001

completed orders from today: 7 (start of day and to current point in time today)
*/

export default function DashboardOrderTableComponent() {
    const { dashboardService } = useContext(CacheContext);
    const [tableData, setTableData] = useState([]);
    const [key, setKey] = useState('missingArtwork');
    const [status, setStatus] = useState(3);
    const [page, setPage] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [totalItems, setTotalItems] = useState(0);

    const statuses = {
        "missingArtwork": 3,
        "sensariaFailure": 9004,
        "missingPrintfile": 4,
        "missingCXImages": 9003,
        "unknownSku": 9001,
        "completedToday": 7
    };

    useEffect(() => {
        let payload = {
            "page": page,
            "pageSize": pageSize,
            "status": [status],
            "createdOnStart": getStartDate(key),
            "createdOnEnd": new Date().toISOString(),
            "descendingOrder": true
        };

        if (dashboardService !== undefined) {
            dashboardService.getOrdersByStatus(payload)
                .then((res) => {
                    setTableData(res.data.results);
                    setTotalItems(res.data.totalItems);
                }, (err) => {
                    console.error(err);
                });
        }
    }, [page, pageSize, dashboardService, status, key]);

    const handleTabClick = (statusKey) => {
        setKey(statusKey);
        setStatus(statuses[statusKey]);
    };


    return (
        <Container fluid>
            <Row className="mb-3">
                <Tab.Container id="dashboard-tabs" defaultActiveKey="missingArtwork">
                    <Row>
                        <Col sm={3} className="p-2 bg-light rounded border border-muted sticky-top" style={{
                            height: "max-content"
                        }}>
                            <Nav variant="pills" className="flex-column">
                                {Object.keys(statuses).map(statusKey => (
                                    <Nav.Item key={statusKey}>
                                        <Nav.Link 
                                            eventKey={statusKey} 
                                            onClick={() => handleTabClick(statusKey)} 
                                            className={key === statusKey ? "bg-brand" : "text-muted"}>
                                            {statusKey.replace(/([A-Z])/g, ' $1').toUpperCase()}
                                        </Nav.Link>
                                    </Nav.Item>
                                ))}
                            </Nav>
                        </Col>
                        <Col sm={9}>
                            <Tab.Content>
                                <Tab.Pane eventKey={key}>
                                    <Table striped bordered hover className="rounded-table">
                                        <thead>
                                            <tr>
                                                <th>Order #</th>
                                                <th>Status</th>
                                                <th>Shopify Order ID</th>
                                                <th>First</th>
                                                <th>Last</th>
                                                <th>Email</th>
                                                <th>Products</th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {tableData.length > 0 ? (
                                                tableData.map((item) => (
                                                    <DashboardTableRowComponent key={item.id} item={item} />
                                                ))
                                            ) : (
                                                <tr>
                                                    <td colSpan="100%" className="text-center">No data available</td>
                                                </tr>
                                            )}
                                        </tbody>
                                    </Table>
                                </Tab.Pane>
                            </Tab.Content>
                        </Col>
                    </Row>
                </Tab.Container>
                <PaginationComponent
                    page={page}
                    totalItems={totalItems}
                    pageSize={pageSize}
                    setPage={setPage}
                    setPageSize={setPageSize}
                />
            </Row>
        </Container>
    );
}
