import React from 'react';
import { Modal, Button, Form } from 'react-bootstrap';

export default function ShowCancelModalComponent({ show, onHide,  orderService, workObject, fetchWorkObject, setAlert, setShowCancelModal }) {
  const submitCancelOrder = () => {
    orderService.cancelOrder(workObject.workObject.id)
      .then(res => {
          setAlert({ show: true, message: 'Order cancelled successfully', variant: 'success' });
          setShowCancelModal(false);
          fetchWorkObject();
      })
      .catch(err => {
        setAlert({ show: true, message: 'Failed to cancel order', variant: 'danger' });
      });
  };
  return (
    <Modal show={show} onHide={onHide}>
      <Modal.Header closeButton>
        <Modal.Title>Cancel Order</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>Are you sure you want to cancel this order?</p>
        <Form>
          <div className='text-end'>
            <Button variant="dark" onClick={submitCancelOrder}>
              Yes
            </Button>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
};
