import authHeader from "./auth.header";
import axios from "axios";
export default class DistributionService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    getAverageDailyOrders(days) {
        return axios.get(`${this.baseUrl}/portal/api/v1/WorkObject/average/daily/${days}`, authHeader());
    }

    setUserDistribution(userId, distributionType, distribution) {
         return axios.patch(`${this.baseUrl}/portal/api/v1/distribution/user/${userId}/${distributionType}/${distribution}`, {}, authHeader());
    }
    

    getUsersCapacity = (type, average) => {
        return axios.get(`${this.baseUrl}/portal/api/v1/distribution/queue/${type}/avg/30`, authHeader());
    }
}

