import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Col, Row, Tab, Tabs, Card } from "react-bootstrap";
import WorkObjectNav from "./workobject.nav.component";
import LoadingComponent from "../shared/loading.component";
import CacheContext from "../../services/cache.context";
import ConfigContext from "../../services/config.context";
import SubwayLineComponent from './subwayline.component';
import WorkObjectDetails from "./workobject.details.component";
import OrderLineItems from "./order.lineitems.component";
import AuthContext from "../../services/auth.context";
import OrderCustomerInformation from "./order.customer.component";
import OrderCommentsComponent from "./order.comments.component";
import ActivityFeed from "./order.activity.component";

export default function WorkObject() {
    const { orderId } = useParams();
    const { config } = useContext(ConfigContext);
    const { hasPermission, user } = useContext(AuthContext);
    const { orderService, lookupService, getProductsCache, productscache, getProductShippingProfiles, productshippingprofiles } = useContext(CacheContext);
    const [ isLoading, setIsLoading ] = useState(true);
    const [ workObject, setWorkObject ] = useState(null);
    const [ refreshWorkObject, setRefreshWorkObject ] = useState(true);
    const [ error, setError ] = useState(null);
    useEffect(() => {
        if (refreshWorkObject) {
            setRefreshWorkObject(false);
            orderService.getDetails(orderId)
            .then((res) => {
                setWorkObject(res.data);
            }, (err) => {
                if (err.response) {
                    if (err.response.status === 404) {
                        setError("Order not found");
                    } else {
                        setError(`${err.response.data.Message.replace('workobject', 'order')} You're seeing this message because you made a change that affected this order's status.`);
                    }
                } else {
                    setError(err);
                }
                setWorkObject(null);
            }).finally(() => {
                setIsLoading(false);
            });

            getProductsCache()
            .then((res) => {
                
            }).catch((err) => {
                console.log(err);
            });

            getProductShippingProfiles()
            .then((res) => {
              
            }).catch((err) => {
                console.log(err);
            })
        }

    }, [orderId, orderService, user, lookupService, productscache, refreshWorkObject, productshippingprofiles, getProductsCache, getProductShippingProfiles]);

    return (
        <>
            {isLoading && <LoadingComponent variant="dark" />}
            {!isLoading && workObject === null &&
                <div className="alert alert-warning" role="alert">
                    {error ? (
                        <>{error}</>
                    ) : (
                    <>Failed to locate order '{orderId}' or you do not have permission to view this order.</>
                    )}
                </div>
            }
            {!isLoading && workObject !== null &&
                <>
                    <WorkObjectNav 
                        workObject={workObject} 
                        orderService={orderService} 
                        hasPermission={hasPermission}
                        user={user}
                        fetchWorkObject={() => setRefreshWorkObject(true)}
                    />
                    <SubwayLineComponent workObject={workObject.workObject} />
                    <WorkObjectDetails workObject={workObject.workObject} />
                    <OrderLineItems 
                        config={config}
                        orderId={orderId} 
                        workObject={workObject} 
                        orderService={orderService}
                        setIsLoading={setIsLoading} 
                        productsCache={productscache} 
                        productshippingprofiles={productshippingprofiles}
                        fetchWorkObject={() => setRefreshWorkObject(true)}
                    />
                    <Row>
                        {hasPermission('order:read') ? <OrderCustomerInformation 
                                                            workObject={workObject}
                                                            orderService={orderService}
                                                            fetchWorkObject={() => setRefreshWorkObject(true)} 
                                                            customer={workObject.workObject.metadata.customer} 
                                                            shippingAddress={workObject.workObject.metadata.shippingAddress} 
                                                        /> : null}
                        <Col md={6} sm={12}>
                            <div className="text-start mb-2">
                                <h4 className="mb-0 ms-1">Comments/Activity</h4>
                            </div>
                            <Card>
                                <Card.Body>
                                    <Tabs
                                        defaultActiveKey="comments"
                                        id="comments-activity-tabs"
                                        className="mb-3">
                                        <Tab eventKey="comments" title="Comments">
                                            <OrderCommentsComponent orderId={orderId} comments={workObject.comments} fetchWorkObject={() => setRefreshWorkObject(true)} />
                                        </Tab>
                                        <Tab eventKey="activity" title="Activity">
                                            <ActivityFeed activityData={workObject.audits} />
                                        </Tab>
                                    </Tabs>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </>
            }
        </>
    )
}