export function getStartDate(statusKey) {
    const now = new Date();
        switch (statusKey) {
            case "missingArtwork":
                return new Date(now - 24 * 3600 * 1000).toISOString(); // 24 hours ago
            case "missingPrintfile":
                return new Date(now - 72 * 3600 * 1000).toISOString(); // 72 hours ago
            case "completedToday":
                return new Date(now.setHours(0, 0, 0, 0)).toISOString(); // start of today
            default:
                return new Date(0).toISOString(); // default to epoch start
        }
}