import React, { useState, useEffect, useContext, useCallback } from 'react';
import CacheContext from '../../services/cache.context';
import { Modal, Button, Form } from 'react-bootstrap';
import Select from 'react-select';

export default function ManuallyAssignModalComponent({ show, onHide, orderService, workObject, fetchWorkObject, setAlert, setShowManuallyAssignModal }) {
  const [artists, setArtists] = useState([]);
  const [printFileDesigners, setPrintFileDesigners] = useState([]);
  const [selectedArtist, setSelectedArtist] = useState(null);
  const [selectedPrintFileDesigner, setSelectedPrintFileDesigner] = useState(null);
  const { distributionService, getUserById } = useContext(CacheContext);
  
  const loadUsers = useCallback(() => {
    const fetchUsers = (userType, setUsers) => {
      distributionService.getUsersCapacity(userType)
        .then(capacityData => {
          const options = [];
          const processUser = (index) => {
            if (index >= capacityData.data.length) {
              setUsers(options);
              return;
            }

            const userId = capacityData.data[index].userId;
            getUserById(userId)
              .then(user => {
                options.push({ value: userId, label: user });
                processUser(index + 1);
              })
              .catch(error => {
                console.error('Error processing user:', error);
                processUser(index + 1);
              });
          };

          processUser(0);
        })
        .catch(error => console.error(`Error fetching ${userType} users:`, error));
    };

    fetchUsers('artfile', setArtists);
    fetchUsers('printfile', setPrintFileDesigners);
  }, [distributionService, getUserById]);

  useEffect(() => {
    loadUsers();
  }, [loadUsers]);

  const handleArtistChange = (selectedOption) => {
    setSelectedArtist(selectedOption);
    console.log('Selected Artist:', selectedOption);
  };

  const handlePrintFileDesignerChange = (selectedOption) => {
    setSelectedPrintFileDesigner(selectedOption);
    console.log('Selected Print File Designer:', selectedOption);
  };

  const assignOrder = () => {
    if (selectedArtist && selectedPrintFileDesigner) {
      orderService.manuallyAssign(workObject.workObject.id, selectedArtist.value, selectedPrintFileDesigner.value)
        .then(res => {
          setAlert({ show: true, message: 'Order assigned successfully', variant: 'success' });
          setShowManuallyAssignModal(false);
          fetchWorkObject();
        })
        .catch(err => {
          console.error('Error assigning order:', err);
          setAlert({ show: true, message: 'Failed to assign order', variant: 'danger' });
        });
    } else {
      setAlert({ show: true, message: 'Please select both an artist and a print file designer', variant: 'warning' });
    }
  };

  return (
    <>
      <Modal show={show} onHide={onHide}>
        <Modal.Header closeButton>
          <Modal.Title>Assign Order</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form className="mt-3">
            <Form.Group className="mb-3">
              <Form.Label>Assign Artist</Form.Label>
              <Select options={artists} value={selectedArtist} onChange={handleArtistChange} placeholder="Select Artist" />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Assign Print File Designer</Form.Label>
              <Select options={printFileDesigners} value={selectedPrintFileDesigner} onChange={handlePrintFileDesignerChange} placeholder="Select Print File Designer" />
            </Form.Group>
          </Form>
          {selectedArtist && selectedPrintFileDesigner && (
            <div className="mt-3">
              <p><strong>Order will be assigned to:</strong></p>
              <p>Artist: <b>{selectedArtist.label}</b></p>
              <p>Print File Designer: <b>{selectedPrintFileDesigner.label}</b></p>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onHide}>
            Close
          </Button>
          <Button variant="dark" onClick={assignOrder} disabled={!selectedArtist || !selectedPrintFileDesigner}>
            Assign Order
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
