import authHeader from "./auth.header";
import axios from "axios";

export default class LookupService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    getWorkObjectStatusLookup() {
        return axios.get(`${this.baseUrl}/portal/api/v1/lookup/workobject/status`, authHeader())
    }

    getUsers() {
        const payload = { page: 1, pageSize: 1000 };
        return axios.post(`${this.baseUrl}/api/v1/user/search`, payload, authHeader());
    }

    getProductShippingProfiles() {
        return axios.get(`${this.baseUrl}/portal/api/v1/lookup/shopify/shippingprofiles`, authHeader());
    }

    getProducts() {
        return axios.get(`${this.baseUrl}/portal/api/v1/lookup/shopify/products`, authHeader());
    }

    getProductBySku(sku) {
        return axios.get(`${this.baseUrl}/portal/api/v1/lookup/shopify/products/${sku}`, authHeader());
    }

    createProduct(product) {
        return axios.post(`${this.baseUrl}/portal/api/v1/storeproduct`, product, authHeader());
    }

    updateProduct(sku, product) {
        product.shippingMethod = parseInt(product.shippingMethod);
        product.sensariaProductId = parseInt(product.sensariaProductId);
        product.numberOfCxImages = parseInt(product.numberOfCxImages);
        return axios.post(`${this.baseUrl}/portal/api/v1/storeproduct/${sku}`, product, authHeader());
    }

    deleteProduct(sku) {
        return axios.delete(`${this.baseUrl}/portal/api/v1/storeproduct/${sku}`, authHeader());
    }
}
