import { useContext, useEffect, useState } from "react";
import { Button, Container, Nav, Navbar, OverlayTrigger, Tooltip } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLock, faUser, faRightFromBracket, faHouse, faScaleBalanced, faGauge, faBarcode } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import AuthContext from "../services/auth.context";
import ConfigContext from "../services/config.context";

export default function NavComponent() {
    const { config } = useContext(ConfigContext);
    const clientName = config?.clientName;
    const uiVersion = config?.version;
    const { user, logout, hasPermission, originalAdminSession, revertToAdminSession } = useContext(AuthContext);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        if (config) {
            setIsLoading(false);
        }
    }, [config]);

    const isAuthAdmin = hasPermission("user:write") || hasPermission("user:delete") || hasPermission("role:create") || hasPermission("role:delete");

    const navigate = useNavigate();

    const renderTooltip = (props, text) => (
        <Tooltip id="button-tooltip" {...props}>
            {text}
        </Tooltip>
    );

    if (isLoading) {
        return (
            <Navbar hidden={user === null} variant="dark" bg="dark" expand="lg" collapseOnSelect fixed="top" className="shadow-sm">
                <Container fluid className="justify-content-end p-3">
                    <Navbar.Brand className="mx-auto"></Navbar.Brand>
                </Container>
            </Navbar>
        );
    }

    return (
        <Navbar hidden={user === null} variant="dark" bg="dark" expand="lg" collapseOnSelect fixed="top" className="shadow-sm">
            <Container fluid className="justify-content-end">
                <Navbar.Brand className="mx-auto">{clientName} - v{uiVersion}</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto">
                        {hasPermission('user:write') &&
                            <OverlayTrigger
                                placement="bottom"
                                overlay={props => renderTooltip(props, 'Reports')}
                            >
                                <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                    <Button
                                        variant="light btn-sm"
                                        className="w-100"
                                        type="button"
                                        onClick={() => navigate("/dashboard")}
                                    ><FontAwesomeIcon icon={faGauge} className="text-dark" /></Button>
                                </Nav.Item>
                            </OverlayTrigger>
                        }
                        {isAuthAdmin &&
                            <OverlayTrigger
                                placement="bottom"
                                overlay={props => renderTooltip(props, 'Administration')}
                            >
                                <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                    <Button
                                        variant="light btn-sm"
                                        className="w-100"
                                        type="button"
                                        // onClick={() => navigate("/administration")}
                                        href={`${config.baseUrl}/idm/administration`}
                                    ><FontAwesomeIcon icon={faLock} className="text-dark" /></Button>
                                </Nav.Item>
                            </OverlayTrigger>
                        }
                        {originalAdminSession && 
                        <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                            <Button
                                variant="warning btn-sm"
                                className="w-100"
                                type="button"
                                onClick={revertToAdminSession}
                            >Revert to Admin</Button>
                        </Nav.Item>
                        }
                        {isAuthAdmin &&
                            <OverlayTrigger
                                placement="bottom"
                                overlay={props => renderTooltip(props, 'Distribution')}
                            >
                                <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                    <Button
                                        variant="light btn-sm"
                                        className="w-100"
                                        type="button"
                                        onClick={() => navigate("/distribution")}
                                    ><FontAwesomeIcon icon={faScaleBalanced} className="text-dark" /></Button>
                                </Nav.Item>
                            </OverlayTrigger>
                        }
                        {isAuthAdmin &&
                            <OverlayTrigger
                                placement="bottom"
                                overlay={props => renderTooltip(props, 'Sku Management')}
                            >
                                <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                    <Button
                                        variant="light btn-sm"
                                        className="w-100"
                                        type="button"
                                        onClick={() => navigate("/skus")}
                                    ><FontAwesomeIcon icon={faBarcode} className="text-dark" /></Button>
                                </Nav.Item>
                            </OverlayTrigger>
                        }
                        <OverlayTrigger
                            placement="bottom"
                            overlay={props => renderTooltip(props, 'Account Management')}
                        >
                            <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                <Button
                                    variant="light btn-sm"
                                    className="w-100"
                                    type="button"
                                    // onClick={() => navigate("/home")}
                                    href={`${config.baseUrl}/idm/home`}
                                ><FontAwesomeIcon icon={faUser} className="text-dark" /></Button>
                            </Nav.Item>
                        </OverlayTrigger>
                        <OverlayTrigger
                            placement="bottom"
                            overlay={props => renderTooltip(props, 'Home')}
                        >
                            <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                <Button
                                    variant="light btn-sm"
                                    className="w-100"
                                    type="button"
                                    onClick={() => navigate("/")}
                                ><FontAwesomeIcon icon={faHouse} className="text-dark" /></Button>
                            </Nav.Item>
                        </OverlayTrigger>
                        <OverlayTrigger
                            placement="bottom"
                            overlay={props => renderTooltip(props, 'Logout')}
                        >
                            <Nav.Item className="mx-md-2 mb-3 mb-md-0">
                                <Button
                                    variant="light btn-sm"
                                    className="w-100"
                                    type="button"
                                    title="Logout"
                                    onClick={() => logout()}
                                ><FontAwesomeIcon icon={faRightFromBracket} className="text-dark" /></Button>
                            </Nav.Item>
                        </OverlayTrigger>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}
