import authHeader from "./auth.header";
import axios from "axios";
export default class DownloadService {
    constructor(baseUrl) {
        this.baseUrl = baseUrl;
    }

    getDetails(orderId) {
        return axios.get(`${this.baseUrl}/portal/api/v1/workobject/order/${orderId}/details`, authHeader());
    }

    async bulkDownload(imageTypes, targetWorkObjectIds) {
        let payload = {
            "imageTypes": imageTypes,
            "targetWorkObjectIds": targetWorkObjectIds
          }
        return axios.post(`${this.baseUrl}/portal/api/v1/bulkdownload`, payload, authHeader());
    }
}
