import SecureImageComponent from '../shared/secureimage.component';
import UploadComponent from '../shared/upload.component';

export default function OrderLineItemAttachments({ 
  workObjectId, 
  attachment, 
  fetchWorkObject, 
  lineItemId, 
  imageType, 
  fileTypes, 
  config, 
  cxPhotoIndex, 
  isAddonProduct, 
  workObject,
  lineItemIndex
}) {
  const isReplacing = attachment !== null && attachment !== undefined;
  const { lineItems } = workObject.workObject;

  function getPetName(lineItems, lineItemId, index) {
    if (index < 1 || index > 4) {
        // throw new Error("Index must be between 1 and 4.");
        return 'No name found';
    }

    for (const item of lineItems) {
        if (item.lineItem.id === lineItemId) {
            const petNameProperty = item.lineItem.properties.find(
                prop => prop.name === `Pet Name ${index}`
            );

            if (petNameProperty) {
                return petNameProperty.value;
            } else {
                return `No name found`;
            }
        }
    }

    return 'No name found';
}

  let petName = attachment?.metadata ? getPetName(lineItems, attachment.metadata.lineItemId, cxPhotoIndex + 1) : 'No name found';
  return (
    <div className="alert alert-light mb-4">
      <div className="d-flex align-items-center flex-column flex-lg-row flex-md-column">
        {attachment && (
          <>
          <SecureImageComponent
            workObjectId={workObjectId}
            attachmentId={attachment.id}
            classes="rounded rounded-bottom-0"
            fileName={attachment.fileName}
            uploadedOn={attachment.uploadedOn}
            petName={petName}
            width={130}
            by={attachment?.userId}
            fetchWorkObject={fetchWorkObject}
            allowDownload={true}
            isAddonProduct={isAddonProduct}
            workObject={workObject}
            imageType={imageType}
          />
          </>
        )}
      </div>
      {fileTypes.length > 0 && (
      <UploadComponent
        cxPhotoIndex={(imageType === 'cx' || imageType === 'artfile') ? cxPhotoIndex + 1 : null}
        isReplacing={isReplacing}
        lineItemId={lineItemId}
        workObjectId={workObjectId}
        imageType={imageType}
        fileTypes={fileTypes}
        fetchWorkObject={fetchWorkObject}
        config={config}
        isAddonProduct={isAddonProduct}
        workObject={workObject}
        lineItemIndex={lineItemIndex}
      />
      )}
    </div>
  );
}
