import moment from 'moment-timezone';

export function convertToEasternTime(isoDateString, split = false, useDateString = false) {
    const dateObject = moment(isoDateString);

    if (dateObject.isValid()) {
        // Convert to Eastern Time
        const easternTime = dateObject.tz('America/New_York');

        if (split) {
            // Format date and time for split display
            const date = easternTime.format('MM/DD/YYYY');
            const time = easternTime.format('hh:mm:ss A');
            return <span>{date}<br />{time}</span>;
        } else if(useDateString) {
              return moment(isoDateString).tz('America/New_York').format();
        } else {
            // Format date and time for same line display
            const formattedTime = easternTime.format('MM/DD/YYYY hh:mm:ss A');
            return <span>{formattedTime}</span>;
        }
    } else {
        console.log(`Invalid date: ${isoDateString}`);
        // Return a message for invalid date
        return null;
    }
}
