import React, { useState, useContext, useEffect, createRef, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faComment, faUser } from '@fortawesome/free-solid-svg-icons';
import { Button, Collapse, Form, InputGroup, Alert } from 'react-bootstrap';
import CacheContext from '../../services/cache.context';
import { timeSince } from '../../utils/timeSince';
import LoadingComponent from '../shared/loading.component';

export default function OrderCommentsComponent({ comments, orderId, fetchWorkObject }) {
    const [open, setOpen] = useState({});
    const [newComment, setNewComment] = useState('');
    const [error, setError] = useState('');
    const [loading, setLoading] = useState({});
    const { commentsService, getUserById } = useContext(CacheContext);
    const [userDisplayNames, setUserDisplayNames] = useState({});

    const textAreaRefs = useRef({});

    useEffect(() => {
        comments.forEach(comment => {
            textAreaRefs.current[comment.id] = createRef();
            getUserById(comment.userId)
                .then(displayName => {
                    setUserDisplayNames(prevState => ({
                        ...prevState,
                        [comment.userId]: displayName
                    }));
                });
        });
    }, [comments, getUserById]);

    const handleNewCommentChange = (event) => {
        setNewComment(event.target.value);
    };

    const handleNewCommentSubmit = () => {
        if (newComment.trim()) {
            setLoading({ newComment: true });
            handleNewComment(newComment);
            setNewComment('');
        }
    };

    const handleNewComment = (message) => {
        commentsService.newComment(orderId, message)
            .then(() => {
                fetchWorkObject();
                setError('');
            })
            .catch((error) => {
                setError('Failed to post a new comment. Please try again.');
            })
            .finally(() => setLoading(prev => ({ ...prev, newComment: false })));
    };

    const handleReply = (commentId) => {
        const message = textAreaRefs.current[commentId].current.value;
        if (message.trim()) {
            setLoading(prev => ({ ...prev, [commentId]: true }));
            commentsService.replyToComment(orderId, message, commentId)
                .then(() => {
                    fetchWorkObject();
                    setError('');
                    handleToggle(commentId);
                })
                .catch((error) => {
                    setError('Failed to post a reply. Please try again.');
                })
                .finally(() => setLoading(prev => ({ ...prev, [commentId]: false })));
        }
    };

    const handleDeleteComment = (commentId) => {
        commentsService.deleteComment(orderId, commentId)
            .then(() => {
                fetchWorkObject();
                setError('');
            })
            .catch((error) => {
                setError('Failed to delete the comment. Please try again.');
            });
    };

    const handleToggle = (id) => {
        setOpen(prevOpen => ({
            ...prevOpen,
            [id]: !prevOpen[id]
        }));
    };

    const renderComments = (comments, level = 0) => 
    comments.map(comment => {
        if (!textAreaRefs.current[comment.id]) textAreaRefs.current[comment.id] = createRef();

        return (
            <details key={`comment-${comment.id}`} open className="comment" id={`comment-${comment.id}`} style={{ marginLeft: level * 20 }}>
                <a href={`#comment-${comment.id}`} className="comment-border-link">
                    <span className="sr-only">Jump to comment-{comment.id}</span>
                </a>
                <summary>
                    <div className="comment-heading">
                        <div className="comment-info">
                            <div className="flex-shrink-0 me-2 position-relative comment-user-image">
                                <FontAwesomeIcon icon={faUser} /><span className="comment-author"> {userDisplayNames[comment.userId] || 'Loading...'}</span>
                                <span className="m-0"> {timeSince(comment.timestamp)}</span>
                            </div>
                        </div>
                    </div>
                </summary>
                <div className="comment-body">
                    <div className='border p-2 rounded shadow-sm border-light'>
                        <p className='rounded-4 bg-light p-2 ps-3'>{comment.message}</p>
                        <div className='text-end'>
                            <Button
                                variant=''
                                className={`${open[comment.id] ? 'fade hide btn-sm btn-brand' : 'fade show btn-sm btn-brand'}`}
                                onClick={() => handleToggle(comment.id)}
                            >
                                <FontAwesomeIcon icon={faComment} /> Reply
                            </Button>
                            <Button variant='danger' className={`${open[comment.id] ? 'fade hide btn-sm ms-2' : 'fade show btn-sm ms-2'}`} onClick={() => handleDeleteComment(comment.id)}>
                                <FontAwesomeIcon icon={faTrash} /> Delete
                            </Button>
                        </div>
                        {loading[comment.id] && <LoadingComponent message="Posting your reply..." variant='dark' size="sm"/>}
                        <Collapse in={open[comment.id]}>
                            <div className="reply-form">
                                <Form.Floating className="mb-2">
                                    <Form.Control
                                        as="textarea"
                                        ref={textAreaRefs.current[comment.id]}
                                        className="form-control h-auto"
                                        placeholder="Your reply..."
                                        rows="4"
                                    />
                                    <label htmlFor={`reply-comment-textarea-${comment.id}`}>Your reply...</label>
                                </Form.Floating>
                                <div className='d-flex justify-content-end align-items-center mt-2'>
                                    <Button
                                        variant=""
                                        className='btn-sm me-2 btn-brand'
                                        onClick={() => handleReply(comment.id)}
                                        disabled={loading[comment.id]}
                                    >
                                        <FontAwesomeIcon icon={faComment} /> Comment
                                    </Button>
                                    <Button variant='secondary' className='btn-sm' onClick={() => handleToggle(comment.id)}>
                                        Cancel
                                    </Button>
                                </div>
                            </div>
                        </Collapse>
                    </div>
                </div>
                {comment.childComments && <div className="replies">{renderComments(comment.childComments, level + 1)}</div>}
            </details>
        );
    });


    return (
        <>
            {error && <Alert variant="danger" onClose={() => setError('')} dismissible>{error}</Alert>}
            <div style={{ maxHeight: '600px', overflow: 'hidden', display: 'flex', flexDirection: 'column' }}>
                <div className="comment-thread" style={{ overflowY: 'auto', flexGrow: 1 }}>
                    {loading.newComment && <LoadingComponent message="Posting your comment..." variant='dark' size="sm" />}
                    {comments && comments.length > 0 && renderComments(comments)}
                </div>
                <InputGroup>
                    <Form.Control
                        as="textarea"
                        className='rounded-4 rounded-end-0'
                        placeholder="Add a new comment..."
                        value={newComment}
                        onChange={handleNewCommentChange}
                    />
                    <Button variant="" className="rounded-4 rounded-start-0 btn-brand" onClick={handleNewCommentSubmit} disabled={loading.newComment}>
                        Post
                    </Button>
                </InputGroup>
            </div>
        </>
    );
}
