import React, { useContext, useState, useEffect } from 'react';
import axios from 'axios';
import { Badge } from 'react-bootstrap';
import LoadingComponent from './loading.component';
import DownloadSecureImageButton from './download.secureimage.button.component';
import RemoveSecureImageButton from './remove.secureimage.button.component';
import { convertToEasternTime } from '../../utils/convertToEasternTime';
import { shouldDisplayElement } from '../../utils/shouldDisplayElement';
import heic2any from 'heic2any';
import Swal from 'sweetalert2';

// Import contexts
import AuthContext from '../../services/auth.context';
import ConfigContext from '../../services/config.context';
import CacheContext from '../../services/cache.context';
import authHeader from '../../services/auth.header';

const SecureImageComponent = ({
  workObjectId,
  workObject,
  imageType,
  attachmentId,
  classes,
  width,
  height,
  petName,
  fileName,
  by,
  uploadedOn,
  fetchWorkObject,
  allowDownload = false,
  isAddonProduct,
}) => {
  const [ image, setImage ] = useState(null);
  const [ loading, setLoading ] = useState(false);
  const [ isRemoving, setIsRemoving ] = useState(false);
  const [ errorMessage, setErrorMessage ] = useState(null);
  const [ uploadedBy, setUploadedBy ] = useState(null);
  // Destructure methods and variables from context
  const { hasPermission } = useContext(AuthContext);
  const { attachmentService, showToastNotification, getUserById } = useContext(CacheContext);
  const config = useContext(ConfigContext);

  const showRemoveButton = shouldDisplayElement(hasPermission, isAddonProduct, imageType, workObject);

  useEffect(() => {
    const fetchImage = async () => {
      setLoading(true);
      try {
        const endpoint = `${config.config.baseUrl}/portal/api/v1/attachment/download/${workObjectId}/${attachmentId}`;
        const response = await axios.get(endpoint, { ...authHeader(), responseType: 'arraybuffer' });
        const blob = new Blob([response.data], { type: 'image/jpeg' }); // Assume JPEG as a default
        let imageUrl;

        try {
          // Attempt to convert assuming the file might be HEIC
          const conversionResult = await heic2any({
            blob: blob,
            toType: 'image/jpeg',
          });
          imageUrl = URL.createObjectURL(conversionResult);
        } catch (error) {
          // Handle the specific error from heic2any indicating conversion is not necessary
          if (error.code && [1, 2, 3, 4, 5].includes(error.code)) {
            if (error.code === 1 || error.code === 2) {
              // Error code 1 means no conversion is needed
              //Error code 2 means the file is not HEIC
              imageUrl = URL.createObjectURL(blob);
            } else {
              // For other known error codes, save the error message
              console.log(error.message)
              setErrorMessage(error.message);
              throw error;
            }
          } else {
            // For an unknown error, set a generic message
            setErrorMessage('Failed to load or convert image');
            throw error;
          }
        }

        setImage(imageUrl);
      } catch (error) {
        console.error('Failed to load or convert image:', error);
        setImage(null);
      } finally {
        setLoading(false);
      }
    };

    fetchImage();
  }, [workObjectId, attachmentId, config.config.baseUrl]);

  useEffect(() => {
    if (by) {
      getUserById(by).then((user) => {
        setUploadedBy(user);
      });
    }
  }, [by, getUserById]);

  const handleRemoveImage = async () => {
    const result = await Swal.fire({
      title: 'Are you sure?',
      text: 'Do you want to remove this image?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, remove it!',
      cancelButtonText: 'No, keep it',
    });

    if (!result.isConfirmed) return;
    setIsRemoving(true);

    try {
      await attachmentService.deleteAttatchment(workObjectId, attachmentId);
      fetchWorkObject();
      showToastNotification("Successfully removed image", "success");
    } catch (error) {
      console.error('Failed to remove image:', error);
      showToastNotification("Failed to remove image", "danger");
    } finally {
      setIsRemoving(false);
    }
  };

  if (loading) return <LoadingComponent message="Loading image..." />;
  if (!loading && image === null) return <Badge variant="dark" bg="dark">${errorMessage || 'Failed to load image'}</Badge>;

  return (
    <>
      <div className={`border border-primary rounded shadow-sm mb-2 position-relative ${classes}`}>
        <img
          alt={attachmentId}
          width={width}
          height={height}
          src={image}
          loading="lazy"
          objectFit="contain" // Corrected "objectfit" to "objectFit"
        />
        {petName && (
          <p className="bg-secondary p-1 rounded text-white text-center mb-0">
            {petName}
          </p>
        )}
        {fileName && (
          <p className="bg-secondary p-1 rounded text-white text-center mb-0">
            {fileName}
          </p>
        )}
      </div>
      <div className="ms-3 w-100">
        {uploadedOn && (
          <p className="text-muted mb-0">
            Uploaded {convertToEasternTime(uploadedOn)}
          </p>
        )}
        {by && (
          <p className="text-muted mb-0">
           by: <span className='badge pill-badge bg-secondary text-white'>{uploadedBy}</span>
          </p>
        )}
        {allowDownload && <DownloadSecureImageButton image={image} fileName={fileName} />}
        {showRemoveButton && (
          <RemoveSecureImageButton onClick={handleRemoveImage} isRemoving={isRemoving} />
        )}
      </div>
    </>
  );
};

export default SecureImageComponent;
