import { useContext, useState, useEffect } from 'react';
import { ProgressBar, Badge, OverlayTrigger, Tooltip } from 'react-bootstrap';
import ConfigContext from '../../services/config.context';
import CacheContext from '../../services/cache.context';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle, faTimesCircle, faFlag } from "@fortawesome/free-solid-svg-icons";

export default function StatusProgressBarComponent({ workObject }) {
    const { config } = useContext(ConfigContext);
    const { getUserList } = useContext(CacheContext);
    const transitions = workObject.transitions || [];
    const currentStatus = workObject.status;
    const [ userList, setUserList ] = useState([]);
    let marginClass

    useEffect(() => {
        getUserList()
            .then(data => {
                setUserList(data);
            });
    }, [workObject, getUserList]);


    const normalSteps = Array.from({ length: 8 }, (_, i) => i);

    const isErrorStatus = currentStatus < 0 || currentStatus >= 8000;

    const lastSuccessfulStatus = transitions.length ? transitions[transitions.length - 1].sourceStatus : null;

    const stepsToShow = isErrorStatus ? normalSteps.slice(0, lastSuccessfulStatus + 1) : normalSteps;
    const stepClass = isErrorStatus ? 'subway-line-progress-bar-item-error' : 'subway-line-progress-bar-item';

    const getTooltipText = (status, timestamp, sourceUser, destinationUser) => {
        const actionText = config.statusMap[status.toString()]?.text || 'Unknown Status';
        const timeText = timestamp ? `on ${new Date(timestamp).toLocaleString()}` : '';
        const userText = destinationUser ? `Transitioned to ${destinationUser}` : '';
        const sourceUserText = sourceUser ? `by ${sourceUser}` : '';
        return `${actionText} ${userText} ${sourceUserText} ${timeText}`;
    };
    const getAssignedUser = (userId) => {
        const assignedUser = userList.find(user => user.id === userId);
        if (assignedUser !== undefined) {
            if (assignedUser.metadata.firstName === undefined || assignedUser.metadata.lastName === undefined) {
                return assignedUser.metadata.email;
            } else {
                return `${assignedUser.metadata.firstName} ${assignedUser.metadata.lastName}`;
            }
        } else {
            return userId;
        }
    }
    return (
        <div>
            <ProgressBar className="mb-3 subway-line-progress-bar">
            {stepsToShow.map((step, index) => {
                    let className;
                    let icon;

                    // Find transition data for tooltip
                    const transitionData = step === 0 ? transitions.find(t => t.sourceStatus === step) : transitions.find(t => t.destinationStatus === step);
                    const tooltipText = (transitionData !== null && transitionData !== undefined)
                        ? getTooltipText(step, transitionData.timestamp, getAssignedUser(transitionData.sourceUser), getAssignedUser(transitionData.destinationUser)) : 'Not yet reached';

                    // Check if the step was ever reached
                    const wasStepReached = transitions.some(t => t.destinationStatus === step);
                    // Step is current if it matches the current status and it was reached
                    const isCurrentStep = step === currentStatus && wasStepReached;
                    // Step is past if its status is less than the current status and it was reached
                    const isPastStep = step < currentStatus && wasStepReached;
                    // Step is future if its status is greater than the current status
                    const isFutureStep = step > currentStatus;

                    // Assign class and icon based on step status
                    if (isCurrentStep) {
                        className = isErrorStatus ? 'bg-warning' : 'bg-light-green';
                        icon = faFlag;
                        marginClass = 'ms-2';
                    } else if (isPastStep || step === 0) {
                        className = 'bg-current-step';
                        icon = faCheckCircle;
                        marginClass = 'ms-2';
                    } else if (isFutureStep) {
                        className = 'bg-secondary';
                        icon = null;
                        marginClass = null;
                    } else {
                        className = 'bg-secondary';
                        icon = faTimesCircle;
                        marginClass = 'ms-2';
                    }


                    return (
                        <ProgressBar
                            key={index}
                            now={(1 / stepsToShow.length) * 100}
                            className={`${className} border-end subway-line-progress-bar ${stepClass}`}
                            label={
                                <>
                                    <OverlayTrigger
                                        key={step}
                                        placement="top"
                                        overlay={
                                            <Tooltip id={`tooltip-${step}`}>
                                                {tooltipText}
                                            </Tooltip>
                                        }
                                    >
                                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} className={marginClass}>
                                            {icon && <FontAwesomeIcon icon={icon} className="subway-line-icon" />}
                                            <Badge className={`${className} subway-line-badge bg-transparent`}>{config.statusMap[step.toString()].text}</Badge>
                                        </div>
                                    </OverlayTrigger>
                                </>
                            }
                        />
                    );
                })}
                {isErrorStatus && (
                    <ProgressBar
                        now={(1 / stepsToShow.length) * 100}
                        className={`bg-danger subway-line-progress-bar ${stepClass}`}
                        label={
                            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} className={marginClass}>
                                <FontAwesomeIcon icon={faTimesCircle} className="subway-line-icon" />
                                <Badge className="bg-danger subway-line-badge bg-transparent">{config.statusMap[currentStatus.toString()].text}</Badge>
                            </div>
                        }
                    />
                )}
            </ProgressBar>
        </div>
    );
}
